// react
import React, { useRef, useState } from 'react';

// components
import {
    styled,
    Media,
    PT,
    Margin,
    Width,
    FontSize,
    Height,
    PL,
    MB,
    MR,
    Right,
    Top,
    Padding2,
    ML,
    PC,
    Mobile,
    MT,
    Bottom,
} from '../../components/common/mediaStlye';
import Header from '../../components/header/header';

// img
import DefaultImgImg from '../../img/myPage/defaultImg.png';
import EditIconImg from '../../img/myPage/editIon.png';
import EditImgIconImg from '../../img/myPage/editImgIcon.png';
import List from '../../components/myPage/list';
import MyActivity from '../../components/myPage/myActivity/myActivity';
import Shopping from '../../components/myPage/shopping/shopping';
import Information from '../../components/myPage/information/information';


const mediaPxMb = 1024;
const mbSize = 428;

const MyPage = (props) => {
    const fileRef = useRef();
    const [isEdit, setIsEdit] = useState(false);
    const [name, setName] = useState("Ahnnabanana");
    const [email, setEmail] = useState("ahnnabanana@prefinc.com");
    const [profileImg, setProfileImg] = useState(DefaultImgImg);
    const [contentStatus, setContentStatus] = useState(0);
    const [headerIcon, setHeaderIcon] = useState([true, false])


    // 저장버튼 클릭
    const onClickSave = () => {
        setIsEdit(false);
    }

    // 이미지 수정 버튼 클릭
    const onClickProfilImg = () => {
        fileRef.current.click()
    }

    // 이미지 수정
    const onChangeFile = (e) => {
        e.preventDefault(e);
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            setProfileImg(reader.result);
        };
        reader.readAsDataURL(file);
    }
 
    const contentReturn = () => {
        // 초기 리스트
        if(contentStatus == 0){
            return <List setContentStatus={setContentStatus}/>
        } // 나의활동
        else if(contentStatus == 1){
            return <MyActivity setContentStatus={setContentStatus} setHeaderIcon={setHeaderIcon} headerIcon={headerIcon}/>
        } // 쇼핑정보
        else if(contentStatus == 2){
            return <Shopping setContentStatus={setContentStatus}/>
        } // 계정정보
        else if(contentStatus == 5){
            return <Information setContentStatus={setContentStatus}/>
        }
    }

    return(
        <Container>
            <input onChange={(e) => onChangeFile(e)} type="file" style={{display:"none"}} ref={fileRef}/>
            <Header isSearch={headerIcon[0]} isMbBack={headerIcon[1]} onClickBack={() => setHeaderIcon([...[true, false]])}/>
        
            <GridWrap>
                {/* 프로필 */}
                <ProfileGrid>
                    <ProfileWrap>
                        <ImgTextSave>
                            <ImgText>
                                {/* 이미지 */}
                                <ProfileImgWrap>
                                    <ProfileImg isEdit={isEdit} onClick={() => {isEdit&&onClickProfilImg()}} src={profileImg} alt="프로필이미지"/>
                                    {
                                        isEdit&&<EditImgIcon src={EditImgIconImg} alt="수정버튼"/>
                                    }
                                </ProfileImgWrap>
                                {/* 텍스트 */}
                                <ProfileTextWrap>
                                    <ProfileNameWrap>
                                        {
                                            isEdit?
                                            <ProfileInput value={name} onChange={e => setName(e.target.value)}/>
                                            :
                                            <ProfileName>{name}</ProfileName>
                                        }
                                        {
                                            isEdit?
                                            <PC><SaveBtn onClick={() => onClickSave()}>Save</SaveBtn></PC>
                                            :
                                            <EditBtn onClick={() => setIsEdit(true)} src={EditIconImg} alt="수정버튼"/>
                                        }
                                    </ProfileNameWrap>

                                    {
                                        isEdit?
                                        <ProfileInput value={email} onChange={e => setEmail(e.target.value)}/>
                                        :    
                                        <ProfileEmail>{email}</ProfileEmail>
                                    }
                                </ProfileTextWrap>
                            </ImgText>
                            {
                                isEdit&&
                                <Mobile>
                                    <MobileSaveWrap>
                                        <SaveBtn onClick={() => onClickSave()}>Save</SaveBtn>
                                    </MobileSaveWrap>
                                </Mobile>
                            }
                        </ImgTextSave>
                        

                    </ProfileWrap>
                </ProfileGrid>

                <ContentWrap>
                    {contentReturn()}
                </ContentWrap>
            </GridWrap>
        </Container>
    )
};

export default MyPage;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    position: relative;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
const ProfileGrid = styled.div`
    padding-top: 162px;
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
    ])}
`
const ProfileWrap = styled.div`
    background-color: #F8F7F7;
    display: flex;
    align-items: center;
    justify-content: center;
`
const ImgTextSave = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 25px 0;
    ${Media(mediaPxMb,[
        Margin(17, 0, 17, 0, mbSize),
    ])}
`
const ImgText = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`
const ProfileImgWrap = styled.div`
    position: relative;
    margin: 0 66px 0 0;
    width: 130px;
    height: 130px;
    ${Media(mediaPxMb,[
        Margin(0, 27, 0, 0, mbSize),
        Width(69, mbSize),
        Height(69, mbSize),
    ])}
`
const EditImgIcon = styled.img`
    position: absolute;
    bottom: 20px;
    right: 0;
    width: 22px;
    ${Media(mediaPxMb,[
        Width(20, mbSize),
        Bottom(5, mbSize),
    ])}
`
const ProfileImg = styled.img`
    width: 100%;
    height: 100%;
    border-radius: 50%;
    ${({isEdit}) => {
        return isEdit&&`cursor: pointer;`
    }}
`
const ProfileTextWrap = styled.div`
    ${Media(mediaPxMb,[
        "display: flex",
        "flex-direction: column",
    ])}

`
const ProfileNameWrap = styled.div`
    display: flex;
`
const ProfileName = styled.div`
    margin-right: 119px;
    font-size: 20px;
    font-style:Oblique;
    font-weight: 1000; 
    margin-bottom: 21px;
    ${Media(mediaPxMb,[
        MR(0, mbSize),
        FontSize(20, mbSize),
        MB(6, mbSize),
    ])}
`
const ProfileInput = styled.input`
    width: 248px;
    height: 35px;
    border:1px solid #707070;
    padding-left: 16px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        Width(199, mbSize),
        Height(28, mbSize),
        PL(12, mbSize),
        FontSize(11, mbSize),
    ])}
    &:first-child{
        margin-bottom: 10px;
        ${Media(mediaPxMb,[
            MB(8, mbSize),
            // MT(21, mbSize),
        ])}
    }
`
const EditBtn = styled.img`
    width: 22px;
    height: 22px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        "position: absolute;",
        Width(22, mbSize),
        Height(22, mbSize),
        Right(67, mbSize),
    ])}
`
const MobileSaveWrap = styled.div`
    width: 100%;
    text-align: right;
`
const SaveBtn = styled.button`
    color:#fff;
    background: #000;
    outline: none;
    border: 0;
    font-size: 9px;
    margin-left: 22px;
    width: 47px;
    height: 24px;
    ${Media(mediaPxMb,[
        Width(51, mbSize),
        Height(26, mbSize),
        FontSize(12, mbSize),
        ML(0, mbSize),
    ])}
`
const ProfileEmail = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
`
const ContentWrap = styled.div`
    width: 569px;
    margin: 0 auto;
    ${Media(mediaPxMb,[
        "width:100%",
        "box-sizing: border-box",
        Padding2(0, 20, mbSize),
    ])}
`