
import star_1 from '../../img/common/reviewStar/star_1.png';
import star_2 from '../../img/common/reviewStar/star_2.png';
import star_3 from '../../img/common/reviewStar/star_3.png';
import star_4 from '../../img/common/reviewStar/star_4.png';
import star_5 from '../../img/common/reviewStar/star_5.png';

export default function ReviewStarStatus(status){
    switch (status){
        case 1:
            return star_1;
        case 2:
            return star_2;
        case 3:
            return star_3;
        case 4:
            return star_4;
        case 5:
            return star_5;
        default:
            return star_5;
    }
}