const Shopping = ({setContentStatus}) => {
    const [status, setStatus] = useState(0);
    const [prdData, setPrdData] = useState([]);
    const navData = [
        {title:"주문배송 조회", status:0},
        {title:"취소/반품/교환 조회", status:1},
        {title:"환불/입금 조회", status:2},
        {title:"찜리스트", status:3},
    ];


    useEffect(() => {
        // 주문배송 조회
        if(status == 0){
            setPrdData([
                {prd:testData, delStatus:0, completeDate:today, orderDate:today},
                // {prd:testData, delStatus:1,completeDate:today, orderDate:today},
                // {prd:testData, delStatus:2,completeDate:today, orderDate:today},
            ]);
        } // 취소/반품/교환 조회
        else if(status == 1){
            setPrdData([
                {prd:testData,
                delStatus:3,completeDate:today, orderDate:today},
                {prd:testData,
                delStatus:4,completeDate:today, orderDate:today},
                {prd:testData,
                delStatus:5,completeDate:today, orderDate:today},
                {prd:testData,
                delStatus:6,completeDate:today, orderDate:today},
            ]);
        } // 환불/입금 조회
        else if(status == 2){
            setPrdData([
                {prd:testData,
                delStatus:7,completeDate:today, orderDate:today},
                {prd:testData,
                delStatus:8,completeDate:today, orderDate:today},
            ]);
        } // 찜리스트
        else if(status == 3){
            // 서버통신하여 setPrdData를 업데이트 시켜준다.
            setPrdData([
                {prd:[{prd_img:prdImg, prd_title:"Soul", prd_price:158000, count:1, prdType:"Skincare"},],
                delStatus:9,completeDate:today, orderDate:today},
                {prd:[{prd_img:prdImg, prd_title:"Soul", prd_price:158000, count:1, prdType:"Skincare"},],
                delStatus:9,completeDate:today, orderDate:today},
            ]);
        }
    }, [status])

    return(
        <Wrap>
            <CommonTitle title="쇼핑정보" navData={navData} setStatus={setStatus} setContentStatus={setContentStatus}/>
            <PrdList prdData={prdData} status={status}/>
        </Wrap>
    )
};

export default Shopping;

