import React from 'react';


// style
import styled from "styled-components"

import BackIconWhiteImg from '../../img/common/backIconWhite.png';

const WhiteBackBtn = ({onClick}) => (
    <BackBtn onClick={() => onClick()}>
        <BackBtnImg src={BackIconWhiteImg} alt="뒤로가기 버튼" />
        Back
    </BackBtn>
    );

export default WhiteBackBtn;

const BackBtn = styled.div`
    position: absolute;
    font-size: 18px;
    top:28px;
    left: 121px;
    color:#fff;
    cursor: pointer;
    z-index:999;
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
`