//react
import React from 'react';
import { useHistory } from 'react-router';

// style
import styled, {keyframes}from "styled-components"


const NavDetailList = ({array}) => {
    const history = useHistory();
    return(
        <NavUl>
            {
                array.map((item, index) => {
                    return(
                        <NavLi onClick={() => history.push(`/${item.link}`)} style={{marginTop:index==0&&"54px"}} key={index}>{item.title}</NavLi>
                    )
                })
            }
        </NavUl>
    )
};

export default NavDetailList;

const fadeinText = keyframes`
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
`
const NavUl = styled.ul`
    animation: ${fadeinText} 800ms forwards;
`
const NavLi = styled.li`
    color:#fff;
    margin-bottom: 33px;
    cursor: pointer;
`