//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components"

import {
    Media,
    Px,
    Width,
    Height,
    Top,
    Bottom,
    Left,
    Right,
    FontSize,
    Margin,
    Padding,
    Padding2,
    MT,
    MB,
    ML,
    MR
} from '../../components/common/mediaStlye';

const mediaPx = 500;

const LoginInput = ({after, type, width, isError, errorText, isPadding, useRef}) => {

    return(
        <InputWrap width={width&&width} isPadding={isPadding?true:false}>
            <TitleSpan isPadding={isPadding?true:false}>
                {after}
                {isError?<ErrorText>{errorText}</ErrorText>:null}
            </TitleSpan>
            <InputId isError={isError} type={type} ref={useRef&&useRef}/>
        </InputWrap>
    )
};

export default LoginInput;

const InputWrap = styled.div`
    height:63px;
    width:calc(100%-32px);
    position: relative;
    margin-bottom:4px;



    ${Media(mediaPx, [
        `width:calc(100%-${Px(10, 428)})`,
        MB(5, 428),
        Height(79, 428),
    ])}

    @media (max-width: ${mediaPx}px) {
        ${({isPadding})=>{
        return isPadding?
        `${Padding2(0, 10, 428)}`
        :
        ``
    }}
    }

    ${({width})=>{
        return width&&`width:100%;`
    }}

    ${({isPadding})=>{
        return isPadding?
        `padding:0 32px;`
        :
        `0`
    }}


`
const TitleSpan = styled.span`
    position: absolute;
    top:7px;
    left:10px;
    font-size:12px;

    ${Media(mediaPx, [
        Top(7, 428),
        Left(10, 428),
        FontSize(12, 428),
    ])}

    ${({isPadding}) => {
        return isPadding&&`left:42px;`
    }}
    @media (max-width: ${mediaPx}px) {
        ${({isPadding})=>{
            return isPadding&&`${Left(42, 428)}`
        }}
    }

`
const ErrorText = styled.span`
    margin-left: 15px;
    font-size:12px;
    color:#FB3549;
    font-weight: 400;
    ${Media(mediaPx, [
        ML(15, 428),
        FontSize(12, 428),
    ])}
`
const InputId = styled.input`
    width:100%;
    height:100%;
    ${({isError})=>{
        return isError?
        ` border:1px solid #D7464E;`
        :
        ` border:1px solid #000;`
    }}
    background:#fff;
    text-align:center;
    font-size: 12px;
`