//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components";

// components
import DateText from '../../../components/common/dateText';
import ReviewStarStatus from '../../../components/common/reviewStarStatus';

import {
    Width,
    FontSize,
    ML,
    Height,
    MR,
    Padding,
    Right,
    Top,
    MB
} from '../../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const MbReviewItem = ({item, setIsImgModal, setSelectImgArr, setImageIndex}) => {

    const [sliceContent, setSliceContent] = useState("")
    const [isSlice, setIsSlice] = useState(true);
    const [isMore, setIsMore] = useState(false);

    // 펼쳐보기, 문자열자르기 --> 분기처리
    useEffect(() => {
        if(item.content.length > 70){
            setIsMore(true);
            let newText = item.content;
            newText=newText.substring(0, 70);
            setSliceContent(newText + "...")
        }else{
            setIsSlice(false)
        }
    }, [])

    const onClickImgEl = (index) => {
        setIsImgModal(true);
        setSelectImgArr(item.reviewImg);
        setImageIndex(index)
    }

    return(
        <ReviewItemWrap>
            <ImgTitle>
                <ProductImg src={item.prdImg} alt="상품 이미지"/>
                <ProductTitle>{item.title}</ProductTitle>
            </ImgTitle>

            <Date>{DateText(item.date, ".")}</Date>

            <TopImgWrap>
                {
                    item.reviewImg.map((el, index) => {
                        return(
                            <Img onClick={() => onClickImgEl(index)} src={el} key={index}/>
                        )
                    })
                }
            </TopImgWrap>

            <ContentWrap>
                <Star src={ReviewStarStatus(item.star)} />
                <NameText>
                    <Name>{item.name}</Name>
                    <Text>{isSlice?sliceContent:item.content}</Text>
                    <MoreBtnWrap>
                        {
                            isMore&&
                            <More onClick={() => setIsSlice(!isSlice)}>{isSlice?"+ 펼쳐보기":"- 접기"}</More>
                        }
                    </MoreBtnWrap>
                </NameText>
            </ContentWrap>
        </ReviewItemWrap>
    )
};

export default MbReviewItem;

const ReviewItemWrap = styled.div`
    position: relative;
    ${Padding(20, 21, 20, 21, mbSize)}
    border-bottom: 1px solid #CBCBCB;
`

const ContentWrap = styled.div`
    display: flex;
    position: relative;
`
const ImgTitle = styled.div`
    display: flex;
    align-items: center;
    ${MB(14, mbSize)}
`
const ProductImg = styled.img`
    ${Width(43, mbSize)}
    ${Height(54, mbSize)}
`
const ProductTitle = styled.h2`
    font-weight: 500;
    ${ML(14, mbSize)}
    ${FontSize(20, mbSize)}
`
const Date = styled.div`
    position: absolute;
    color: #8B8B8B;
    ${Right(21, mbSize)}
    ${Top(20, mbSize)}
    ${FontSize(14, mbSize)}
    z-index: -1;
`
const Name = styled.div`
    ${MB(4, mbSize)}
    ${FontSize(14, mbSize)}
`
const TopImgWrap = styled.div`
    ${MB(15, mbSize)}
`
const Img = styled.img`
    ${Width(58, mbSize)}
    ${Height(58, mbSize)}
    ${MR(6, mbSize)}
    cursor: pointer;
`
const NameText = styled.div`
`
const Star = styled.img`
    ${Width(40, mbSize)}
    ${Height(67, mbSize)}
    ${MR(15, mbSize)}
`
const Text = styled.div`
    font-weight: 300;
    ${FontSize(14, mbSize)}
`
const MoreBtnWrap = styled.div`
    text-align: right;
`
const More = styled.div`
    font-weight: 500;
    ${FontSize(13, mbSize)}
    cursor: pointer;
`