import React from 'react';

// css
import styles from './checkbox.module.css';


const CheckBox = ({id, red, onClick, checked, onChange}) => {
    
    return(
        <>
            <input className={styles.input} checked={checked&&checked} onChange={onChange&&onChange} id={id} type="checkbox"/>
            <label className={!red?styles.label:styles.red} onClick={onClick&&onClick} for={id}></label>
        </>
    )
};

export default CheckBox;