import { createAction, handleActions } from 'redux-actions';
import produce from 'immer';

// 액션 타입을 정의해줍니다.
const UPDATE_SELECTDATA = 'prd/selectPrd';

// 액션 생성 함수를 만듭니다.
export const updateSelectData = createAction(UPDATE_SELECTDATA);

// 모듈의 초기 상태를 정의합니다.
const initialState = {
  selectData:[],
}

// immer 를 사용하여 값을 수정하는 리듀서입니다.
export default handleActions({
  [UPDATE_SELECTDATA]: (state, action) => {
    return produce(state, draft => {
      draft.selectData = action.payload.selectData ? action.payload.selectData : draft.selectData;
    });
  },
}, initialState);
