//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link, useHistory} from "react-router-dom";

// style
import styled, {keyframes} from "styled-components";

// img
import LogoImg from '../../img/header/logo@3x.png';
import LockImg from '../../img/header/lock@3x.png';
import InstaImg from '../../img/header/insta@3x.png';
import SelectImg from '../../img/header/nav/selectImg.png';
import BackIconWhiteImg from '../../img/common/backIconWhite.png';
import BnbIconImg from '../../img/common/gnbIcon.png';
import LogoWhiteImg from '../../img/common/logoWhite.png';
import LockWhiteImg from '../../img/common/lockWhite.png';
import InstaWhiteImg from '../../img/common/instaWhite.png';
import MbBackIconImg from '../../img/common/mbBackIcon.png';
import OneColumnImg from '../../img/common/oneColumn.png';
import ThreeColumnImg from '../../img/common/threeColumn.png';
import SearchIconImg from '../../img/taste/mSeulmit/searchIcon.png';
import likeImg from '../../img/common/like.png';
import activeLikeImg from '../../img/common/activeLike.png';

import NavDetailList from './NavDetailList';

import {
    Mobile,PC,
    Media,
    Width,
    FontSize,
    MB,
    Height,
    PT,
    Right,
    MT,
    PR,
    Left,
    Top,
    Px,
    PL,
    Margin,
    Padding,
    Padding2
} from '../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const Header = ({isMbBack, isColumn, isThree, setIsThree, isSearch, onClickBack, isLikeIcon, isLike, setIsLike}) => {
    const history = useHistory();
    const [userText, setUserText] = useState("Login");
    const [searchValue, setSearchValue] = useState("");
    const [isSearchTab, setIsSearchTab] = useState(false);
    const navRef = useRef();
    const navDetailRef = useRef();
    const navData = ["Info", "Taste", "Collection", "Inside"];
    const collectEpi = [
        {title:"All", link:"Episode"},
        {title:"Episode_3", link:"Episode"},
        {title:"Episode_2", link:"Episode"},
        {title:"Episode_1", link:"Episode"},
    ]
    const collectPro = [
        {title:"All", link:"Product"},
        {title:"Skincare", link:"Product"},
        {title:"Body", link:"Product"},
        {title:"Fragrance", link:"Product"},
        {title:"Tools", link:"Product"},
        {title:"Accessories", link:"Product"},
    ]
    const [isCollect, setIsCollect] = useState([0, 0]);
    const [tabStatus, setTabStatus] = useState("");
    const [isMbNav, setIsMbNav] = useState(false);
    // const mbListData = ["Login", "Inside", "Collection", "Taste","Info"];
    const [mbActiveNav, setMbActiveNav] = useState(null);
    const mbListData = [
        {title:"Inside", detail:[{name:"Brand", link:""}, {name:"Slow Order", link:"SlowOrder"}]},
        {title:"Collection", detail:[{name:"Episodes", link:"Episode"}, {name:"Products", link:"Product"}]},
        {title:"Taste", detail:[{name:"Artist", link:"Taste"}, {name:"M.Seulmit", link:"MSeulmit"}]},
        {title:"Info", detail:[{name:"Notice", link:"Info"}, {name:"Review", link:"Review"}]},
    ];

    // 로그인버튼 클릭
    const onClickLogin = () => {
        // 로그인한상태면
        // history.push("/MyPage");
        // 로그인하지 않았으면
        history.push("/Login");
    }

    // 네비게이션 마우르 호버효과
    const hoverNavEl = () => {
        if(navRef.current){
            for(let i = 0 ; i < navRef.current.children.length ; i++){
                navRef.current.children[i].addEventListener("mouseover", () => {
                    setTabStatus(navRef.current.children[i].innerText);
                    navDetailRef.current.style.width = "294px";
                    navDetailRef.current.style.opacity = "1";
                })
            }
        }
    }
    useEffect(() => {
        hoverNavEl();
    }, [])
    window.addEventListener("resize", () => {
        if(window.innerWidth > 1024){
            hoverNavEl();
        }
    })

    // Collection 버튼 클릭
    const onClickCollec = (index) =>{
        let newArr=[0, 0];
        newArr[index] = 1;
        setIsCollect([...newArr]);
    }

    // 네비게이션 닫기 클릭
    const onClickCloseNav = () => {
        navDetailRef.current.style.width = "0";
        navDetailRef.current.style.opacity = "0";
    }

    // 네비게션 클릭
    const navDetailTap = () => {
        if(tabStatus == "Collection"){
            return(
                <>
                    <Span isAfter={isCollect[0]?true:false} onClick={() => {onClickCollec(0)}} style={{marginBottom:!isCollect[0]&&"46px"}}>
                        Episodes
                    </Span>
                    {isCollect[0]&&<NavDetailList bool={isCollect[0]} array={collectEpi}/>}

                    <Span isAfter={isCollect[1]?true:false} onClick={() => onClickCollec(1)}>
                        Products
                    </Span>
                    {isCollect[1]&&<NavDetailList bool={isCollect[1]} array={collectPro}/>}
                    
                </>
            )
        }else if(tabStatus == "Inside"){
            return(
                <>
                    <Span style={{marginBottom:"46px"}}>Brand</Span>
                    <Span onClick={() => history.push("/SlowOrder")}>Slow Order</Span>
                </>
            )
        }else if(tabStatus == "Taste"){
            return(
                <>
                    <Span style={{marginBottom:"46px"}} onClick={() => history.push("/Taste")}>Artist</Span>
                    <Span onClick={() => history.push("/MSeulmit")}>M.Seulmit</Span>
                </>
            )
        }else if(tabStatus == "Info"){
            return(
                <>
                    <Span style={{marginBottom:"46px"}} onClick={() => history.push("/Info")}>Notice</Span>
                    <Span onClick={() => history.push("/Review")}>Review</Span>
                </>
            )
        }
    }

    // 모바일 네비게이션 클릭
    const onClickMbNav = (index) => {
        if(index == mbActiveNav){
            setMbActiveNav(null);
            return;    
        }
        setMbActiveNav(index)
    }

    const onClickSearchIcon = () => {
        setIsSearchTab(!isSearchTab)
    }

    // 페이지 이동 시 스크롤 최상단으로 이동
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    return(
        <Container>
            <Logo onClick={() => {history.push("/")}} src={LogoImg} alt="로고 이미지"/>
            
            {/* Mobile --- */}
            <Mobile>
                {/* 뒤로가기 버튼 */}
                {isMbBack&&<BackIconImg onClick={() => {onClickBack?onClickBack():history.goBack()} } src={MbBackIconImg} alt="뒤로가기 아이콘"/>}
                {/* 3단, 1단 그리드 버튼 */}
                {
                    isColumn&&
                    <ColumnWap onClick={() => { setIsThree&&setIsThree(!isThree)}}>
                        <ColumnImg src={!isThree?ThreeColumnImg:OneColumnImg} alt="그리드 아이콘"/>
                        <ColumnText>{!isThree?"3":"1"}단보기</ColumnText>
                    </ColumnWap>
                }
                {/* 검색버튼 */}
                {isSearch&&<SearchIcon onClick={() => onClickSearchIcon()} src={SearchIconImg} alt="검색 아이콘"/>}
                {/* 햄버거바 */}
                {
                    isLikeIcon?
                    <LikeIcon onClick={() => setIsLike(!isLike)} src={isLike?likeImg:activeLikeImg} alt="관심버튼"/>
                    :
                    <GnbIcon onClick={() => setIsMbNav(true)} src={BnbIconImg} alt="햄버거바"/>
                }

                {
                    isMbNav&&
                    <MbNavWrap>
                        <NoNavSection onClick={() => setIsMbNav(false)}></NoNavSection>
                        <NavSection onClick={() => {setIsMbNav(true)}}>
                            <WhiteLogo onClick={() => history.push('/')} src={LogoWhiteImg} alt="로고"/>
                            <MbNavList>
                                <MbNavElWrap>
                                    <MbNavEl onClick={() => history.push('/Login')}>Login</MbNavEl>
                                </MbNavElWrap>
                                {
                                    mbListData.map((item, index) => {
                                        return(
                                            <MbNavElWrap key={index}>
                                                <MbNavEl onClick={() => onClickMbNav(index)}>{item.title}</MbNavEl>
                                                <MbNavDetailWrap active={mbActiveNav==index}>
                                                {
                                                    item.detail.map((item2, index2) => {
                                                        return(
                                                            <MbNavDetailEl
                                                            onClick={() => history.push(`/${item2.link}`)}
                                                            key={index2}>
                                                                {item2.name}
                                                            </MbNavDetailEl>
                                                        )
                                                    })
                                                }
                                                </MbNavDetailWrap>
                                            </MbNavElWrap>
                                        )
                                    })
                                }
                        
                            </MbNavList>
                            <LockWhiteIcon onClick={() => history.push('/Login')} src={LockWhiteImg} alt="자물쇠 아이콘"/>
                            <InstaWhiteIcon src={InstaWhiteImg} alt="인스타 아이콘"/>
                        </NavSection>
                    </MbNavWrap>
                }
                {
                    isSearchTab&&
                    <SearchInputWrap>
                        <SearchInput type="text" placeholder="Search" value={searchValue} onChange={(e) => setSearchValue(e)}/>
                        <SearchIconTab src={SearchIconImg} alt="검색 아이콘"/>
                    </SearchInputWrap>
                }
            </Mobile>
            {/* PC --- */}
            <PC>
                <NavWrap ref={navRef}>
                    {
                        navData.map((item, index) => {
                            return(
                                <NavEl key={index}>{item}</NavEl>
                            )
                        })
                    }
                </NavWrap>
                
                <UserWrap>
                    <UserText onClick={() => onClickLogin()}>{userText}</UserText>
                    <LockIcon onClick={() => onClickLogin()} src={LockImg} alt="자물쇠 아이콘"/>
                    <InstaIcon src={InstaImg} alt="인스타 아이콘"/>
                </UserWrap>

                <NavDetail ref={navDetailRef}>
                    <BackBtn onClick={() => onClickCloseNav()}>
                        <BackBtnImg src={BackIconWhiteImg} alt="뒤로가기 버튼" />
                        Back
                    </BackBtn>
                    
                    {navDetailTap()}
                </NavDetail>
            </PC>
            
            
        </Container>
    )
};

export default Header;

const Container = styled.div`
    position:fixed;
    width:76px;
    height:100vh;
    border-right:1px solid #1F1113;
    display:flex;
    flex-direction:column; 
    align-items:center;
    justify-content:space-between;
    background-color:#fff;
    z-index:99;
    ${Media(mediaPxMb,[
        Height(74, mbSize),
        "width:100%",
        "justify-content:center",
        "flex-direction:row",
    ])}
`

const Logo = styled.img`
    width:31px;
    height:131px;
    padding-top:57px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        "transform:rotate(90deg)",
        Height(105, mbSize),
        Width(25, mbSize),
        PT(0, mbSize),
    ])}
`
// PC
const NavWrap = styled.ul`
    height: 381px;
    width:100%;
    display:flex;
    flex-direction:column-reverse;
    align-items:center;
    justify-content:space-around;
`
const NavEl = styled.li`
    font-size:18px;
    color:#000000;
    transform:rotate(-90deg);
    font-weight: 700;
    cursor: pointer;
`

const UserWrap = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    padding-bottom:87px;
`
const UserText = styled.p`
    color:#000000;
    font-size:18px;
    transform:rotate(-90deg);
    cursor: pointer;
`
const LockIcon = styled.img`
    width:25px;
    height: 31px;
    margin:32px 0;
    cursor: pointer;
`
const InstaIcon = styled.img`
    width:25px;
    height: 25px;
    cursor: pointer;
`
// ----- 네비게이션 디테일
const NavDetail = styled.div`
    position:fixed;
    background-color: #000;
    height: 100%;
    transition: 600ms ease-out;
    width:0px;
    left: 76px;
    opacity: 0;
    display: flex;
    flex-direction: column;
    padding-left: 53px;
    justify-content: center;
    transition: 500ms;
`
const BackBtn = styled.div`
    position: absolute;
    font-size: 18px;
    top:28px;
    right: 37px;
    color:#fff;
    cursor: pointer;
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
`

const Span = styled.span`
    font-size: 25px;
    font-family: "Big Caslon";
    font-weight: 500;
    color:#fff;
    cursor: pointer;
    position: relative;
    ${ ({isAfter}) => {
       return isAfter?
        `
        &::after{
            position:absolute;
            content:'';
            background-image: url(${SelectImg});
            background-position: center;
            background-size: cover;
            width: 41px;
            height: 62px;
            top:-50%;
            left:10%;
            filter: blur(3px);
            opacity:0.5;
        }
        `
        :
        ``
    }}
    {

    }

`
// Mobile
const SearchInputWrap = styled.div`
    position: absolute;
    width: 100%;
    z-index: 0;
    ${Padding(10, 25, 10, 25, mbSize)};
    box-sizing: border-box;
    ${Height(75, mbSize)}
    ${Top(74, mbSize)}
    background-color: #fff;
`
const SearchInput = styled.input`
    width: 100%;
    height: 100%;
    border:1px solid #000;
    box-sizing: border-box;
    padding-left: 30px;
    ${PL(30, mbSize)}
`
const SearchIconTab = styled.img`
    width: 13px;
    height: 13px;
    position: absolute;
    top:50%;
    right:47px;
    ${Right(47, mbSize)}
    ${Width(13, mbSize)}
    ${Height(13, mbSize)}
    transform:translateY(-50%);
    cursor: pointer;
`

const BackIconImg = styled.img`
    position: absolute;
    ${Width(19, mbSize)}
    ${Height(11, mbSize)}
    ${Left(21, mbSize)}
    cursor: pointer;
`
const SearchIcon = styled.img`
    ${Width(15, mbSize)}
    position: absolute;
    ${Left(21, mbSize)}
    cursor: pointer;
`
const ColumnWap = styled.div`
    position: absolute;
    ${Left(19, mbSize)}
    ${Top(34, mbSize)}
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`
const ColumnImg = styled.img`
    width: 17px;
    height: 9px;
    margin-bottom: 1px;
`
const ColumnText = styled.div`
    ${FontSize(8, mbSize)}
`

const GnbIcon = styled.img`
    position: absolute;
    cursor: pointer;
    ${Width(15, mbSize)}
    ${Height(14, mbSize)}
    ${Right(25, mbSize)}
`
const LikeIcon = styled.img`
    position: absolute;
    cursor: pointer;
    ${Width(18, mbSize)}
    ${Height(18, mbSize)}
    ${Right(25, mbSize)}
`
const MbNavWrap = styled.div`
    height: 100vh;
    width: 100vw;
    text-align: right;
    z-index: 2;
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-direction: row;
`
const NoNavSection = styled.div`
    height: 100%;
    width: calc(100% - ${Px(346, mbSize)} );
`
const NavSection = styled.div`
    ${Width(346, mbSize)}
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background-color: #211013;
    padding-right: 38px;
    box-sizing: border-box;
`
const WhiteLogo = styled.img`
    width: 62px;
    height: 103px;
    margin: 17px 0 95px;
    cursor: pointer;
    /* ${Width(62, mbSize)}
    ${Height(103, mbSize)}
    ${MB(95, mbSize)}
    ${MT(17, mbSize)} */
`

const LockWhiteIcon = styled.img`
    /* ${Width(25, mbSize)}
    ${Height(31, mbSize)}
    ${MT(20, mbSize)}
    ${MB(30, mbSize)} */
    width: 25px;
    height: 31px;
    margin: 20px 0 30px;
    cursor: pointer;
`
const InstaWhiteIcon = styled.img`
    /* ${Width(25, mbSize)}
    ${Height(25, mbSize)} */
    width: 25px;
    height: 25px;
    cursor: pointer;
`

const MbNavList = styled.div`
`
const MbNavElWrap = styled.div`
    margin-bottom: 29px;
`
const MbNavEl = styled.div`
    color:#fff;
    font-size: 18px;
    /* ${FontSize(18, mbSize)}
    ${MB(29, mbSize)} */
    cursor: pointer;
`
const MbNavDetailWrap = styled.div`
    transition: 500ms;
    ${({active}) => {
        return active?
        `
        display:all;
        `
        :
        `
        display: none;
        `
    }}
`

const opacityEvent = keyframes`
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
`
const MbNavDetailEl = styled.div`
    color:#fff;
    font-size: 15px;
    margin: 10px 0;
    cursor: pointer;
    opacity: 0;
    animation: ${opacityEvent} 200ms linear forwards;
`