//react
import React from 'react';
import { useHistory } from 'react-router';

// style
import styled from "styled-components"

// components
import InsertComma from '../common/insertComma'

import {
    Media,
    Width,
    FontSize,
    Margin,
    PT,
    PL,
    Right,
    PC,
    MB,
    MR,
    Padding,
    MT,
    PB,
    Padding2,
    Margin2,
    Px,
    Mobile,
    Height,
    LineH,
    Top,
    Left
} from '../common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const GalleryItemProduct = ({item, slow, isThree, isMiddle, isSale}) => {
    const history = useHistory();

    const onClickPrd = () => {
        if(slow){
            history.push("/ProductDetail/slowOrder")
        }else{
            history.push("/ProductDetail/normal")
        }
    }



    return(
        <GalleryItem isMiddle={isMiddle?true:false} isThree={isThree}>
            {isSale&&<SaleTag isThree={isThree}>SALE</SaleTag>}
            <PrdImg isThree={isThree} onClick={() => onClickPrd()} src={item.img} alt="상품이미지"/>
            <PrdType isThree={isThree} >{item.type}</PrdType>
            <PrdDesc isThree={isThree} >
                <PrdTitle isThree={isThree} onClick={() => onClickPrd()}>{item.name}{item.enName&&` | ${item.enName}`}</PrdTitle>
                {
                    isSale?
                    <SaleWrap isThree={isThree}>
                        <SalePrice isThree={isThree}>{InsertComma(item.price)} won</SalePrice>
                        <PrdPrice isThree={isThree}>{InsertComma(item.salePrice)} won</PrdPrice>
                    </SaleWrap>
                    :
                    <PrdPrice isThree={isThree}>{InsertComma(item.price)} won</PrdPrice>
                }
            </PrdDesc>
            <PC>
                {item.desc&&<Desc>{item.desc}</Desc>}
            </PC>
            <Mobile>
                {
                    !isThree&&
                    <>
                        {
                            item.desc&&
                            <Desc isThree={isThree}>
                                {item.desc}
                            </Desc>  
                        }
                    </>
                }
            </Mobile>

        </GalleryItem>
    )
};

export default GalleryItemProduct;

const GalleryItem = styled.div`
    margin-bottom: 58px;
    position: relative;
    ${Media(mediaPxTablet,[
        MB(58, mediaPxTablet)
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree, isMiddle}) => {
            if(isThree){
                if(isMiddle){
                    return `
                    ${MB(21, mbSize)};
                    ${Padding2(0, 5.5, mbSize)};
                    box-sizing: border-box;
                    `
                }else{
                    return `${MB(21, mbSize)}`
                }
            }else{
                return `${MB(34, mbSize)}`
            }
        }}
    }
`
const SaleTag = styled.span`
    position: absolute;
    top:14px;
    left: 18px;
    width: 89px;
    height: 32px;
    font-family: "Big Caslon";
    font-size: 17px;
    font-weight: 500;
    display: inline-block;
    background-color: #fff;
    border:1px solid #000;
    line-height: 32px;
    text-align: center;

    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
                ${Width(46, mbSize)}
                ${Height(16, mbSize)}
                ${LineH(16, mbSize)}
                ${FontSize(8, mbSize)}
                ${Top(6, mbSize)}
                ${Left(5, mbSize)}
            `
            :
            `
                ${Width(89, mbSize)}
                ${Height(32, mbSize)}
                ${LineH(32, mbSize)}
                ${FontSize(17, mbSize)}
                ${Top(21, mbSize)}
                ${Left(19, mbSize)}
            `
        }}
    }
`
const PrdImg = styled.img`
    width: 491px;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Width(491, mediaPxTablet),
    ])}
    
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            ${Width(139, mbSize)}
            `
            :
            `
            width: 100vw;
            `
        }}
    }
`
const PrdType = styled.div`
    color:#8B8B8B;
    font-style: 15px;
    margin: 5px 0 4px;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        Margin(5, 0, 4, 0, mediaPxTablet),
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            box-sizing: border-box;
            ${PL(8, mbSize)}
            ${FontSize(10 , mbSize)}
            `
            :
            `
            box-sizing: border-box;
            ${PL(19, mbSize)}
            ${FontSize(12, mbSize)}
            `
        }}
    }
`
const PrdDesc = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            flex-direction: column;
            align-items: unset;
            box-sizing: border-box;
            ${Width(139, mbSize)}
            ${Padding2(0, 8, mbSize)}
            `
            :
            `
            box-sizing: border-box;
            ${Padding2(0, 19, mbSize)}
            `
        }}
    }
`
const PrdTitle = styled.span`
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        FontSize(22, mediaPxTablet)
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            ${FontSize(15, mbSize)}
            ${PB(3, mbSize)}
            ${MB(4, mbSize)}
            border-bottom:1px solid #E6E6E0;
            `
            :
            `
            ${FontSize(31, mbSize)}
            `
        }}
    }
`
const SaleWrap = styled.div`
    display: flex;
    flex-direction: column;
    ${({isThree}) => {
        return isThree?
        `text-align: left;`
        :
        `text-align: right;`
    }}
`
const SalePrice = styled.del`
    color: #CBCBCB;
    font-size: 14px;
`
const PrdPrice = styled.span`
    color:#8B8B8B;
    font-style: 20px;
    font-weight: 500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            ${FontSize(15, mbSize)}
            `
            :
            `
            ${FontSize(17, mbSize)}
            `
        }}
    }
`
const Desc = styled.div`
    width: 491px;
    margin-top: 5px;
    padding-top: 20px;
    border-top: 1px solid #E6E6E0;
    font-size: 13px;
    font-weight: 500;
    ${Media(mediaPxTablet,[
        Width(491, mediaPxTablet),
        MT(5, mediaPxTablet),
        PT(20, mediaPxTablet),
        FontSize(13, mediaPxTablet),
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isThree}) => {
            return isThree?
            `
            ${Width(139, mbSize)}
            `
            :
            `
            box-sizing: border-box;
            width:calc(100% - ${Px(38, mbSize)});
            ${Margin(5, 19, 0, 19, mbSize)};
            ${PT(17, mbSize)};
            ${FontSize(11, mbSize)};
            `
        }}
    }
`