//react
import React ,{useState, useEffect, useRef} from 'react';
import {useHistory} from "react-router-dom";


// img
import LogoImg from '../../img/login/logo.png';
import style_1 from '../../img/login/style_1.png';
import style_2 from '../../img/login/style_2.png';
import BackBtn from '../../img/login/backBtn.png';

// components
import LoginInput from '../../components/login/loginInput';
import CommonBtn from '../../components/login/commonBtn';
import ContentTitle from '../../components/login/contentTitle';

import {
    styled,
    Media,
    Width,
    Padding,
} from '../../components/common/mediaStlye';

const mediaPx = 500;
const ChangePw = (props) => {
    const history = useHistory();

    const [searchPwInput, setSearchPwInput] = useState([
        {after:"Password", type:"password", isError:false, errorText:""},
        {after:"Confirm Password", type:"password", isError:false, errorText:"비밀번호가 일치하지 않습니다."},
    ])

    const onClickchange = () => {
        // 틀리면
        // let newArr = searchPwInput;
        // newArr[1].isError = true;
        // setSearchPwInput([...newArr]);
        // 맞으면 
        history.push('/')
    }

    return(

        <Container>
            <LoginWrap>
                <ContentWrap>

                <SearchPwContent>
                    <ContentTitle text={"Change Password"} isSearch/>
                    {
                        searchPwInput.map((item, index) => {
                            return(
                                <LoginInput
                                key={index}
                                after={item.after}
                                type={item.type}
                                isError={item.isError}
                                errorText={item.errorText}
                            />
                            )   
                        })
                    }
                    <CommonBtn text={"비밀번호 변경"} onClick={() => {onClickchange();}} margin={"27px 0 24px"}/>
                </SearchPwContent>

                </ContentWrap>
            </LoginWrap>
        </Container>
        
        
    )
};

export default ChangePw;

const Container = styled.div`
    width:100vw;
    height:100vh;
    background-image: url(${style_1});
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    display:flex;
    align-items:center;
    justify-content:center;
`
const LoginWrap = styled.div`
    width:458px;
    
    ${Media(mediaPx, [
        Width(404, 428),
    ])}
`
const ContentWrap = styled.div`
    background-color: #fff;
    border-bottom: 5px solid #000;
    position: relative;
    &>div{
        display:flex;
        flex-direction:column;
    }
`
// ----- 비밀번호 찾기
const SearchPwContent = styled.div`
    padding:56px 32px 45px;
    ${Media(mediaPx,[
        Padding(37, 10, 49, 10, 428)
    ])}
`
