import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    Width,
    FontSize,
    MR,
    MB,
    ML,
} from '../../../common/mediaStlye';
import DateText from '../../../common/dateText';
import InsertComma from '../../../common/insertComma';
import FillHeart from '../../../../img/common/activeLike.png';

const mediaPxMb = 1024;
const mbSize = 428;

const ShoppingListEl = ({item, index, delData}) => {

    const [prdCount, setPrdCount] = useState(0);
    
    useEffect(() => {
        if(!item.cart_items){return;}
        let cnt = 0;
        const prdData = item.cart_items.data;
        for(let i = 0 ; i < prdData.length ; i++){
            cnt +=prdData[i].count;
        }
        setPrdCount(cnt);
    }, [])

    const convertDate = date => {
        var week = new Array('일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일');
        var today = date.getDay();
        var todayLabel = week[today];
        return (`${date.getMonth()+1}/${date.getDate()}(${todayLabel[0]})`)
    } 

    /*
        0 - 배달 완료
        1 - 배송 중
        2 - 상품 준비중
        3 - 교환 접수중
        4 - 교환 완료
        5 - 반품 완료
        6 - 주문 취소
        7 - 환불 처리중
        8 - 환불처리 완료
        9 - type
    */

    const returnDatus = (int) => {
        if(int == 0){
            return <><Red style={{color:"red"}}>배송완료</Red> {convertDate(delData.completeDate)} 도착</>
        }else if(int == 1){
            return `배송중 ${convertDate(delData.completeDate)} 도착 예정`
        }else if(int == 2){
            return `상품 준비중`
        }else if(int == 3){
           return `교환 접수중` 
        }else if(int == 4){
            return `교환 완료` 
        }else if(int == 5){
            return `반품 완료` 
        }else if(int == 6){
            return `주문 취소` 
        }else if(int == 7){
            return `환불 처리중` 
        }else if(int == 8){
            return `환불처리 완료` 
        }else{
            return ``
        }
    }
    
    // 서버통신
    const onClickLike = () => {
        console.log("onClickLike");
    }

    return(
        <PrdElWrap>
            <PrdImg src={item.prd_img}/>
            <PrdTextWrap>
                {
                    delData.delStatus==9?
                    <>
                        <Like onClick={() => onClickLike()} src={FillHeart}/>
                        <PrdType>{item.prdType}</PrdType>
                        <PrdNamePrice>
                            <PrdName>{item.prd_title}</PrdName>
                            <PrdPrice>{InsertComma(item.prd_price)} won</PrdPrice>
                        </PrdNamePrice>
                    </>
                    :
                    <>
                        {
                            index==0&&
                            <StatusDate>
                                <DelStatus>{returnDatus(delData.delStatus)}</DelStatus>
                                <OrderDate>{DateText(delData.orderDate, ".")}</OrderDate>
                            </StatusDate>
                        }
                        <PrdName>{item.prd_title}</PrdName>
                        <PrdPrice>{InsertComma(item.prd_price)} won<PrdCount>{prdCount}개</PrdCount></PrdPrice>
                    </>
                }
            </PrdTextWrap>
        </PrdElWrap>
    )
};

export default ShoppingListEl;

const PrdElWrap = styled.div`
    display: flex;
    margin-bottom: 10px;
    ${Media(mediaPxMb,[ 
    ])}
`
const PrdImg = styled.img`
    width: 77px;
    margin-right: 24px;
    ${Media(mediaPxMb,[ 
        Width(87, mbSize),
        MR(23, mbSize),
    ])}
`
const PrdTextWrap = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
`
const StatusDate = styled.div`
    display: flex;
    justify-content: space-between;
`
const DelStatus = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    font-weight: 300;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const OrderDate = styled.div`
    color:#8B8B8B;
    font-size: 15px;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
    ])}
`
const PrdName = styled.div`
    font-size: 15px;
    margin-bottom: 2px;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
        MB(2, mbSize),
    ])}
`
const PrdPrice = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize), 
    ])}
`
const PrdCount = styled.span`
    font-size: 15px;
    color:#8B8B8B;
    margin-left: 11px;
    font-weight: 300;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
        ML(11, mbSize),
    ])}
`
const Red = styled.span`
    color:#E83548;
    font-size: 15px;
    font-weight: 200;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
    ])}
`
const Like = styled.img`
    width: 15px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    ${Media(mediaPxMb,[ 
        Width(15, mbSize),
    ])}
`
const PrdType = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    margin-bottom: 5px;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
        MB(5, mbSize),
    ])}
`
const PrdNamePrice = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
`