//react
import React ,{useState, useEffect, useRef} from 'react';

// style
import styled from "styled-components"

import {
    Media,
    Height,
    FontSize,
    LineH,
} from '../../components/common/mediaStlye';

const mediaPx = 500;

const CommonBtn = ({text, onClick, margin, isWhite}) => {
    

    return(
        <Button onClick={onClick&&onClick} style={{margin:margin}} isWhite={isWhite?true:false}>
            {text}
        </Button>
    )
};

export default CommonBtn;

const Button = styled.div`
    height:56px;
    width:100%;
    font-size:14px;
    line-height:56px;
    text-align:center;
    cursor:pointer;

    ${Media(mediaPx,[
        Height(71, 428),
        FontSize(15, 418),
        LineH(71, 428),
    ])}

    ${({isWhite}) => {
        return isWhite?
        `
        background-color:#fff;
        color:#000;
        border:1px solid #000;
        `
        :
        `
        background-color:#000;
        color:#fff;
        `
    }}
`