import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"
import CheckBox from '../common/checkbox';

import {
    Media,
    FontSize,
    MB,
    MT,
    Width,
    Margin,
    MR,
    Mobile,
} from '../common/mediaStlye';

import DateText from '../common/dateText'

const mediaPxMb = 1024;
const mbSize = 428;


const PurchaseListItem = ({item, id, slowOrder}) => {

    let [date, setDate] = useState(new Date());
    let [optionOne, setOptionOne] = useState(1);
    
    // 옵션, 날짜 선택 세팅
    useEffect(() => {
        let optionData = item.options.optionList;
        let newArr = [];
        for(let key in optionData){
            newArr = optionData[key];
        }
        setOptionOne(newArr[0].opt_name);
        if(newArr[1]){
            setDate( new Date(newArr[1].opt_name));
        }

    }, [])

    return(
        <PrdListEl>
            <PrdLeft>
                <CheckBox id={id}/>
                <PrdImg src={item.prd_img} alt="상품이미지"/>
                <PrdTitle slowOrder={slowOrder}>{item.prd_title}</PrdTitle>
            </PrdLeft>

            <PrdRight slowOrder={slowOrder}>
                {
                    slowOrder&&
                    <PrdOptQua slowOrder={slowOrder}>{DateText(date, ".")} <Mobile><br /></Mobile> 배달예정</PrdOptQua>
                }
                <PrdOptQua slowOrder={slowOrder}>{optionOne}</PrdOptQua>
                <PrdOptQua slowOrder={slowOrder}>{item.count}</PrdOptQua>
            </PrdRight>
        </PrdListEl>
    )
};

export default PurchaseListItem;

const PrdListEl = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    ${Media(mediaPxMb,[
        MT(7, mbSize)
    ])}
    &>div{
        display: flex;
        align-items: center;
    }
`
const PrdLeft = styled.div`
    ${({slowOrder}) => {
        return slowOrder?
        `width:300px;`
        :
        ``
    }}
    @media (max-width: ${mediaPxMb}px) {
        ${({slowOrder}) => {
        return slowOrder?
        `
        ${Width(100, mbSize)}
        `
        :
        ``
        }}
    }
`
const PrdImg = styled.img`
    width: 75px;
    margin: 0 32px 0 35px;
    ${Media(mediaPxMb,[
        Width(45, mbSize),
        Margin(0, 10, 0, 12, mbSize),
    ])}
`
const PrdTitle = styled.div`
    font-size: 15px;
    font-weight: 500;

    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
    ;
    @media (max-width: ${mediaPxMb}px) {
        ${({slowOrder}) => {
        return slowOrder?
        `
        text-overflow: ellipsis;
        overflow:hidden;
        white-space:nowrap;
        ${Width(88, mbSize)}
        `
        :
        ``
        }}
    }

`
const PrdRight = styled.div`
    display: flex;
    justify-content: space-between;
    ${({slowOrder}) => {
        return slowOrder?
        `
        width:525px;
        `
        :
        `width: 200px;`
    }}
    @media (max-width: ${mediaPxMb}px) {
        ${({slowOrder}) => {
        return slowOrder?
        `
        ${Width(191, mbSize)}
        justify-content: space-around;
        `
        :
        `${Width(105, mbSize)}`
        }}
    }
`
const PrdOptQua = styled.div`
    font-size: 15px;
    text-align: center;
    font-weight: 300;
    &:last-child{
        font-weight: 500;
    }
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
    ${({slowOrder}) => {
        return slowOrder?
        `
        width: calc(100% / 3);
        `
        :
        `width: 50%;`
    }}
`