import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    FontSize,
    Width,
    Height,
    ML,
    PL,
} from '../../components/common/mediaStlye';
    
// img
import ArrowIconImg from '../../img/common/arrowIron.png';

const mediaPxMb = 1024;
const mbSize = 428;

const CommonTitle = ({title, navData, setStatus, setContentStatus, noTab}) => {
    const [currentNav, setCurrentNav] = useState([])


    useEffect(() => {
        if(noTab){return};
        let newArr = currentNav;
        for(let i = 0 ; i < navData.length ; i++){
            if(i==0){
                newArr.push(true)}
            else{
                newArr.push(false);
            };
        }
        setCurrentNav([...newArr])
    }, [])

    const onClickNavEl = (index) => {
        if(noTab){return};
        let newArr = [];
        for(let i = 0 ; i < navData.length ; i++){
            newArr.push(false);
        }
        newArr[index] = true;
        setStatus(index);
        setCurrentNav([...newArr])
    }

    return(
        <HeadWrap>
            <BtnWrap onClick={() => setContentStatus(0)}>
                <ArrowImg src={ArrowIconImg} alt="뒤로가기 아이콘"/>
                <Title>{title}</Title>
            </BtnWrap>
            {
                !noTab&&
                <NavWrap>
                    {
                        navData.map((item, index) => {
                            return(
                                <NavEl onClick={() => onClickNavEl(index)} active={currentNav[index]} key={index}>{item.title}</NavEl>
                            )
                        })
                    }
                </NavWrap>
            }
        </HeadWrap>
    )
}; 

export default CommonTitle;

const HeadWrap = styled.div`
    margin-top: 43px;
`
const BtnWrap = styled.div`
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 54px;
    ${Media(mediaPxMb,[
        PL(16, mbSize),
    ])}
`
const ArrowImg = styled.img`
    width: 9px;
    height: 13px;
    transform: rotate(180deg);
    ${Media(mediaPxMb,[
        Width(9, mbSize),
        Height(13, mbSize),
    ])}
`
const Title = styled.div`
    font-weight: 400;
    font-size: 14px;
    margin-left: 13px;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
        ML(8, mbSize),
    ])}
`
const NavWrap = styled.ul`
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #1F1113;
    padding: 31px 0 0;
    position: relative;
    &:last-child{
        margin-right: 0;
    }
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        "justify-content: space-around",
    ])}

`
const NavEl = styled.li`
    font-size: 12px;
    margin: 0 15px;
    text-align: center;
    color:#A0A0A0;
    cursor: pointer;
    padding-bottom: 16px;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        "margin:0"
    ])}
    &:last-child{
        margin-right: 0;
    }
    ${({active}) => {
        return active&&`border-bottom:3px solid #000; color:#000;`
    }}
`
