import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    Padding,
    Width,
    Height,
    FontSize,
} from '../../../common/mediaStlye';
import AlertModal from '../../../common/modal/alertModal';
import ExchangeModal from '../../../common/modal/exchangeModal';
import ReviewModal from '../../../common/modal/reviewModal';
import Shopping from '../shopping';
import ShoppingListEl from './shoppingListEl';

const mediaPxMb = 1024;
const mbSize = 428;

const PrdList = ({prdData, status}) => {

    const [isCancel, setIsCancel] = useState(false);
    const [isExchange, setIsExchange] = useState(false)
    const [clickData, setClickData] = useState([])
    const [isReviewModal, setIsReviewModal] = useState(false);

    const btnRetrn = (int, index) => {
        if(int == 0){
            return(
                <>
                    <Btn onClick={() => {setIsExchange(true); setClickData(prdData[index]);}}>교환, 반품 신청</Btn>
                    <Btn onClick={() => setIsReviewModal(true)}>리뷰 작성</Btn>
                </>
            )
        }else{
            return(
                <Btn onClick={() => {setIsCancel(true);}}>주문 취소</Btn>
            )
        }
    }

    return(
        <ListWrap>
            {isCancel&&<AlertModal title="주문이 취소되었습니다." onClickClose={() => setIsCancel(false)}/>}
            {
                isExchange&&
                <ExchangeModal
                clickData={clickData}
                title="Exchange &#38; Refund"
                subtitle="교환, 반품 신청"
                onClickBack={() => setIsExchange(false)}
                onClickSubmit={() => setIsExchange(false)}
                />
            }
            {
                isReviewModal&&
                <ReviewModal
                onClickBack={() => setIsReviewModal(false)}
                subtitle="나의 상품 사용기"
                title="Write Review"
                />
            }
            {
                prdData.map((item, index) => {
                    return(
                        <ListElWrap key={index}>
                            <PrdWrap>
                                {
                                    item.prd.map((item2, index2) => {
                                        return(
                                            <ShoppingListEl
                                            delData={item} item={item2} index={index2} key={index2}/>
                                        )
                                    })
                                }
                            </PrdWrap>
                            {
                                status==0&&
                                <BtnWrap style={{justifyContent:item.delStatus == 0?"space-between":"flex-end"}}>
                                    {btnRetrn(item.delStatus, index)}
                                </BtnWrap>
                            }
                        </ListElWrap>
                    )
                })
            }
        </ListWrap>
    )
};

export default PrdList;

const ListWrap = styled.div`
    margin-bottom: 178px;
`
const ListElWrap = styled.div`
    box-sizing: border-box;
    padding: 13px 100px 12px;
    border-bottom: 1px solid #E2E2E2;
    ${Media(mediaPxMb,[ 
        Padding(15, 8, 12, 8, mbSize)
    ])}
`
const PrdWrap = styled.div`
`

const BtnWrap = styled.div`
    display: flex;
    justify-content: flex-end;
`
const Btn = styled.button`
    font-size: 15px;
    width: 179px;
    height: 48px;
    border: 1px solid #000;
    background-color: #fff;
    ${Media(mediaPxMb,[ 
        Width(179, mbSize),
        Height(48, mbSize),
        FontSize(15, mbSize)
    ])}
`