//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components";

// components
import DateText from '../../../components/common/dateText';
import ReviewStarStatus from '../../../components/common/reviewStarStatus';

import {
    Media,
    Width,
    FontSize,
    PB,
    ML,
    PT,
    Margin,
    Height,
    PL,
    MR,
    PR,
    LineH,
    Padding,
    MT
} from '../../common/mediaStlye';
import ReviewImgModal from '../../common/modal/reviewImgModal';

const mediaPxTablet = 1300;

const ReviewItem = ({item, setIsImgModal, setSelectImgArr, setImageIndex}) => {

    const [sliceContent, setSliceContent] = useState("")
    const [isSlice, setIsSlice] = useState(true);
    const [isMore, setIsMore] = useState(false);

    // 펼쳐보기, 문자열자르기 --> 분기처리
    useEffect(() => {
        if(item.content.length > 200){
            setIsMore(true);
            let newText = item.content;
            newText=newText.substring(0, 200);
            setSliceContent(newText + "...")
        }else{
            setIsSlice(false)
        }
    }, [])

    const onClickImgEl = (index) => {
        setIsImgModal(true);
        setSelectImgArr(item.reviewImg);
        setImageIndex(index)
    }
    return(
        <ReviewItemWrap>
            <ProductImg src={item.prdImg} alt="상품 이미지"/>

            <ContentWrap>
                <ProductDate>
                    <ProductTitle>{item.title}</ProductTitle>
                    <Date>{DateText(item.date, ".")}</Date>
                </ProductDate>
                <NameImg>
                    <Name>{item.name}</Name>
                    <TopImgWrap>
                        {
                            item.reviewImg.map((el, index) => {
                                return(
                                    <Img onClick={() => onClickImgEl(index)} src={el} key={index}/>
                                )
                            })
                        }
                    </TopImgWrap>
                </NameImg>
                <StarText>
                    <Star src={ReviewStarStatus(item.star)} />
                    <Text>{isSlice?sliceContent:item.content}</Text>
                </StarText>
                {
                    isMore&&
                    <More onClick={() => setIsSlice(!isSlice)}>{isSlice?"+ 펼쳐보기":"- 접기"}</More>
                }
            </ContentWrap>

        </ReviewItemWrap>
    )
};

export default ReviewItem;

const ReviewItemWrap = styled.div`
    padding: 18px 60px 18px 30px;
    display: flex;
    border-bottom:1px solid #CBCBCB;
    ${Media(mediaPxTablet,[
        Padding(18, 60, 18, 20, mediaPxTablet),
    ])}
`
const ProductImg = styled.img`
    width: 143px;
    height: 176px;
    margin-right: 35px;
    ${Media(mediaPxTablet,[
        Width(143, mediaPxTablet),
        Height(176, mediaPxTablet),
        MR(35, mediaPxTablet),
    ])}
`
const ContentWrap = styled.div`
`
const ProductDate = styled.div`
    display:flex;
    justify-content: space-between;
    align-items: center;
`
const ProductTitle = styled.h2`
    font-size: 20px;
    font-weight: 500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
    ])}
`
const Date = styled.div`
    color: #8B8B8B;
    font-size:14px;
    ${Media(mediaPxTablet,[
        FontSize(14, mediaPxTablet),
    ])}
`
const NameImg = styled.div`
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin: 8px 0 4px;
    ${Media(mediaPxTablet,[
        Margin(8, 0, 4, 0, mediaPxTablet),
    ])}
`
const Name = styled.div`
   font-size: 14px;
    ${Media(mediaPxTablet,[
        FontSize(14, mediaPxTablet),
    ])}
`
const TopImgWrap = styled.div`
    margin-left: 15px;
    ${Media(mediaPxTablet,[
        ML(15, mediaPxTablet),
    ])}
`
const Img = styled.img`
    width: 45px;
    margin-right: 5px;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        MR(5, mediaPxTablet),
        Width(45, mediaPxTablet),
    ])}
    &:last-child{
        margin-right: 0px;
    }
`
const StarText = styled.div`
    display:flex;
    justify-content: space-between;
`
const Star = styled.img`
    width: 40px;
    height: 67px;
    margin-right: 35px;
    ${Media(mediaPxTablet,[
        Width(40, mediaPxTablet),
        Height(67, mediaPxTablet),
        MR(35, mediaPxTablet),
    ])}
`
const Text = styled.div`
    font-size: 14px;
    line-height: 20px;
    font-weight: 300;
    ${Media(mediaPxTablet,[
        FontSize(14, mediaPxTablet),
        LineH(20, mediaPxTablet),
    ])}
`
const More = styled.div`
    font-size: 13px;
    font-weight: 500;
    text-align: right;
    margin-top: 5px;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        FontSize(13, mediaPxTablet),
        MT(5, mediaPxTablet)
    ])}
`