//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components"
import GalleryItemProduct from '../../../components/collection/galleryItemProduct';
import Header from '../../../components/header/header';

import productImg_1 from '../../../img/collection/products/productImg_1.png';
import productImg_2 from '../../../img/collection/products/productImg_2.png';

import {
    Media,
    Width,
    FontSize,
    PT,
    PL,
    MB,
    MR,
    Padding,
    Top,
    PB,
    MT,
    Height
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const Product = (props) => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const navData = ["All", "Skincare", "Body", "Fragrance", "Tools", "Accessories"];
    const [isThree, setIsThree] = useState(false);
    const [listData, setListData] = useState([
        {img:productImg_2, type: "Skincare", name:"솔 세럼", enName:"Sol Serum", price:15800},
        {img:productImg_1, type: "Fragrance", name:"소울 인센스 홀더", enName:"Soul Incense Holder", price:15800},
        {img:productImg_1, type: "Fragrance", name:"소울 인센스 홀더", enName:"Soul Incense Holder", price:15800},
        {img:productImg_2, type: "Skincare", name:"솔 세럼", enName:"Sol Serum", price:15800},
        {img:productImg_1, type: "Fragrance", name:"소울 인센스 홀더", enName:"Soul Incense Holder", price:15800},
        {img:productImg_1, type: "Fragrance", name:"소울 인센스 홀더", enName:"Soul Incense Holder", price:15800},
        {img:productImg_2, type: "Skincare", name:"솔 세럼", enName:"Sol Serum", price:15800},
        {img:productImg_1, type: "Fragrance", name:"소울 인센스 홀더", enName:"Soul Incense Holder", price:15800},
    ])

    useEffect(() => {
        const newArr = listData;
        for(let i = 1 ; i < newArr.length ; i= i + 3){
            newArr[i].isMiddle = true;
        }
        setListData([...newArr]);
    }, [])

    return(
        <Container>
            <Header isColumn isThree={isThree} setIsThree={setIsThree}/>
            <GridWrap>
                <Grid>
                    <ProductNavWrap>
                        <Title>Products</Title>
                        <NavList>
                            {
                                navData.map((item, index) => {
                                    return(
                                        <NavEl
                                        onClick={() => setCurrentIndex(index)}
                                        active={currentIndex==index?true:false}
                                        key={index}>
                                            {item}
                                        </NavEl>
                                    )
                                })
                            }
                        </NavList>
                    </ProductNavWrap>

                    <GalleryWrap>
                        {
                            listData.map((item, index) =>{
                                return(
                                    <GalleryItemProduct isMiddle={item.isMiddle} isThree={isThree} key={index} item={item}/>
                                )
                            })
                        }
                    </GalleryWrap>

                </Grid>
            </GridWrap>
        </Container>
    )
};

export default Product;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
` 
const Grid = styled.div`
    width:1052px;
    margin:0 auto;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
    ])}
`
const ProductNavWrap = styled.div`
    display: flex;
    flex-direction: column;
    padding-bottom: 67px;
    ${Media(mediaPxTablet,[
        PB(67, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "box-sizing: border-box",
        Height(104, mbSize),
        "background-color: #fff",
        "width: 100%",
        "z-index: 1",
        "position: fixed",
        Top(74, mbSize),
        "left:0",
        PL(19, mbSize),
        PB(23, mbSize),
    ])}
`
const Title = styled.h1`
    font-family: "Big Caslon";
    font-weight: 500;
    font-size: 35px;
    padding: 76px 0 16px;
    ${Media(mediaPxTablet,[
        FontSize(35, mediaPxTablet),
        Padding(76, 0, 16, 0, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(22, mbSize),
        Padding(27, 0, 10, 0, mbSize),
    ])}
`
const NavList = styled.ul`
    display: flex;
`
const NavEl = styled.li`
    font-family: "Big Caslon";
    font-weight: 500;
    font-size: 25px;
    cursor: pointer;
    margin-right: 22px;
    transition: 300ms;
    ${Media(mediaPxTablet,[
        FontSize(25, mediaPxTablet),
        MR(22, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
    ${({active}) => {
        return active?
        `color:#E83548`
        :
        ``
    }}
`

const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    margin-bottom: 320px;
    justify-content: space-between;
    ${Media(mediaPxTablet,[
        MB(320, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
       "justify-content: unset;",
       MT(90, mbSize),
    ])}

`
    