//react
import React ,{useState} from 'react';

// style
import styled from "styled-components";
import BackIconWhiteImg from '../../img/common/backIconWhite.png';
import MbBackIconImg from '../../img/common/mbBackIcon.png';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/swiper.scss";

import {
    Media,
    PC,
    Mobile,
    MT,
    Px,
    Width,
    Height,
    Top,
    Left,
    ML
} from '../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const GalleryBig = ({setIsBig, galleryImgData}) => {
    

    return(
        <Container>
            <Mobile>
                <MbBackIcon src={MbBackIconImg} alt="뒤로가기 버튼" onClick={() => setIsBig(false)}/>
            </Mobile>

            {/* 이미지 스와이퍼 */}
            <SliderWrap>
                {/* 뒤로가기 */}
                <PC>
                    <BackBtn onClick={() => setIsBig(false)}>
                        <BackBtnImg src={BackIconWhiteImg} alt="뒤로가기 버튼" />
                        Back
                    </BackBtn>
                </PC>
                <PC>
                    <Swiper
                    spaceBetween={20}
                    slidesPerView={1.5}
                    className={"swiper"}
                    >
                        {
                            galleryImgData.map((item, index) => {
                                return(
                                    <SwiperSlide key={index}>
                                        <SlideImg src={item} alt="갤러리 이미지" />
                                    </SwiperSlide>
                                )
                            })
                        }
                    </Swiper>
                </PC>
                <Mobile>
                    {
                        galleryImgData.map((item, index) => {
                            return(
                                <SlideImg src={item} alt="갤러리 이미지" key={index}/>
                            )
                        })
                    }
                </Mobile>
            </SliderWrap>
        </Container>
    )
};

export default GalleryBig;

const Container = styled.div`
    position: fixed;
    background: rgba(0, 0, 0, 0.69);
    width: 100vw;
    height: 100%;
    z-index:999;
    display: flex;
    align-items: center;
    ${Media(mediaPxMb,[
        "align-items: unset",
    ])}
`
const BackBtn = styled.div`
    font-size: 18px;
    ${ML(121, 1920)}
    color:#fff;
    cursor: pointer;
    z-index:999;
    cursor: pointer;
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
`
const MbBackIcon = styled.img`
    ${Width(19, mbSize)}
    ${Height(11, mbSize)}
    filter: invert(1);
    position: absolute;
    ${Top(94, mbSize)}
    ${Left(21, mbSize)}
    cursor: pointer;
`

const SliderWrap = styled.div`
    width: 100%;
    margin: 66px 0 67px;
    ;
    ${Media(mediaPxMb,[
        MT(137, mbSize),
        "overflow: scroll",
        `height:calc(100% - ${Px(137, mbSize)})`
    ])}
`
const SlideImg = styled.img`
    width: 100%;
    object-fit: cover;
`