//react
import React ,{useState, useEffect} from 'react';
import {Link, useHistory} from "react-router-dom";

// style
import styled from "styled-components"
//img
import ListImg from '../../img/info/listImg@3x.png';

import {
    Media,
    Width,
    Height,
    Bottom,
    FontSize,
    Padding,
    MT,
    MB,
    ML,
    MR,
    LineH
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const InfoList = ({isEnd, link}) => {
    const history = useHistory()
    const [color, setColor] = useState("#000000");

    const onClickBtn =() => {
        if(!link){return}
        history.push(`/${link}`)
    }

    useEffect(() => {
        setColor(isEnd?"#A0A0A0":"#000000");
    }, [])

    return(
        <Container>
            <ImgWrap isEnd={isEnd?true:false}>
                <ImgDesc isEnd={isEnd?true:false} >{isEnd?"종료된 노티스 입니다.":"[EVENT] Minimal May"}</ImgDesc>
                <ContentImg className="contentImg" src={ListImg} alt="리스트 이미지"/>
            </ImgWrap>
            <TextWrap color={color}>
                <TextTitle>[EVENT] Minimal May</TextTitle>
                <Desc> 
                    5월을 맞이하여 ‘Minimal May’ 를 진행합니다.<br />
                    ALL PRODUCTS <br />
                    전 제품 최대 53% 할인<br />
                    *액세서리류 제외<br />
                    공식 홈페이지 단독 KIT 최대 53% 할인<br />
                    * KIT 무료배송<br />
                    <br />
                    PROMOTION GIFT<br />
                    프로모션 기간 동안 1만원 이상 구매하시는 모든 고객님들께<br />
                    ‘Abib Scented paper type G Fragrant tag’ 을 증정 드립니다.<br />
                    <br />
                    기간 : 2021.05.04(화) - 05.17(월) 23:55분 까지<br />
                </Desc>

                <MoreBtn color={color} onClick={() => onClickBtn()}>더보기</MoreBtn>
            </TextWrap>

        </Container>
    )
};

export default InfoList;

const Container = styled.div`
    margin-bottom:40px;
    display:flex;
    align-items:center;
    justify-content:center;
    ${Media(mediaPxTablet,[
        MB(40, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        "flex-direction: column",
        "align-items: unset",
        MB(29, mbSize),
    ])}
`
const ImgWrap = styled.div`
    width:705px;
    height:100%;
    margin-right:39px;
    position: relative;
    
    ${Media(mediaPxTablet,[
        Width(705, mediaPxTablet),
        MR(39, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        "width:100%",
        "margin-right:0",
    ])}

    ${({isEnd})=>{
    return isEnd?
    `
    &:after {
        position:absolute;
        top:0;
        left:0;
        content: "";
        width:100%;
        height:100%;
        background-color:rgba(20, 20, 20, 0.5);
        display:flex;
        align-items:center;
        justify-content:center;
    }
    `:``
}}
`

const ImgDesc = styled.span` 
    color:#fff;
    z-index: 1;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 500;
    @media (max-width: ${mediaPxTablet}px) {
        ${({isEnd}) => {
        return isEnd?
            `
                top: 50%;
                ${FontSize(20, mediaPxTablet)}
            `
            :
            `
                ${FontSize(25, mediaPxTablet)}
                ${Bottom(47, mediaPxTablet)}
                font-family: "Big Caslon"; 
                color:#625452;
            ` 
        }}
    }
    ${({isEnd}) => {
       return isEnd?
       `
        top: 50%;
        font-size:20px;
       `
       :
       `
        font-family: "Big Caslon";
        font-size:32px;
        bottom:47px;
        color:#625452;
       ` 
    }}
`

const ContentImg = styled.img`
    width:100%;
    height:100%;
`
const TextWrap = styled.div`
    padding:7px 0 9px;
    ${Media(mediaPxTablet,[
        Padding(7, 0, 9, 0, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        Padding(18, 0, 0, 0, mediaPxTablet),
        ML(18, mbSize)
    ])}

    & > * > *, & > *{
        ${({color})=>{
            return(`color:${color}`);
        }}
    }
`
const TextTitle = styled.p`
    font-size:20px;
    margin-bottom:11px;
    font-weight:500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
        MB(11, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
    ])}
`
const Desc = styled.p`
    font-size: 15px;
    font-weight: 500;
    line-height: 23px;
    font-weight: 400;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        LineH(23, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        LineH(23, mbSize),
    ])}
`
const MoreBtn = styled.button`
    cursor:pointer;
    width:385px;
    height: 66px;
    margin-top: 24px;
    background-color:#fff;
    font-size:15px;
    padding: 0;
    
    ${Media(mediaPxTablet,[
        Width(385, mediaPxTablet),
        Height(66, mediaPxTablet),
        FontSize(15, mediaPxTablet),
    ])}
    
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        MT(43, mbSize),
        Width(385, mbSize),
        Height(66, mbSize),
    ])}


    ${({color})=>{
        return(`border:1px solid ${color}`);
    }}
`