import React, {useEffect, useState} from 'react';

// style
import styled from "styled-components"
import GalleryItemProduct from '../../../components/collection/galleryItemProduct';
import Header from '../../../components/header/header';

// img
import headImg from '../../../img/collection/episode/headImg.png';
import productImg_1 from '../../../img/collection/products/productImg_1.png';
import productImg_2 from '../../../img/collection/products/productImg_2.png';

import {
    FontSize,
    Margin,
    MB,
    Media,
    MT,
    Padding,
    PT,
    Top,
    Width,
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const content = "소나무 향으로 공간을채우고 명상을 갖습니다. 눈을 떠 얼굴에 비춰진 빛과 그림자를 마주할때, 외면하지 않고 손을 뻗습니다. 제품설명칸입니다."

const SaleProduct = (props) => {
    const [isThree, setIsThree] = useState(false);
    const [listData, setListData] = useState([
        {img:productImg_2, type: "Skincare", name:"Sol", price:15800, desc:content, isSale:true, salePrice:14800},
        {img:productImg_1, type: "Fragrance", name:"Sol", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:"Sol", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"Sol", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:"Sol", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"Sol", price:15800, desc:content},
        {img:productImg_2, type: "Skincare", name:"Sol", price:15800, desc:content},
        {img:productImg_1, type: "Fragrance", name:"Sol", price:15800, desc:content},
    ])

    useEffect(() => {
        const newArr = listData;
        for(let i = 1 ; i < newArr.length ; i= i + 3){
            newArr[i].isMiddle = true;
        }
        setListData([...newArr]);
    }, [])


    return(
        <Container>         
            <Header isColumn isThree={isThree} setIsThree={setIsThree}/>

            <GridWrap>
                <ListWrap>
                    <TitleWrap>
                        <ListTitle>Slow Order</ListTitle>
                        <SubTitle>Body</SubTitle>
                    </TitleWrap>
                     <GalleryWrap>
                        {
                            listData.map((item, index) =>{
                                return(
                                    <GalleryItemProduct isMiddle={item.isMiddle} isThree={isThree} key={index} item={item} isSale={item.isSale?true:false}/>
                                )
                            })
                        }
                    </GalleryWrap>
                </ListWrap> 

            </GridWrap>
        </Container>
    )
};

export default SaleProduct;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 컨텐츠
const ListWrap = styled.div`
    width:1052px;
    margin:0 auto;
    margin-bottom: 219px;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
        MB(219, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100%",
        "margin:0",
        PT(74, mbSize),
        MB(273, mbSize)
    ])}
`
const TitleWrap = styled.div`
    padding:76px 0 17px;
    ${Media(mediaPxMb,[
        "background-color: #fff",
        "width: 100%",
        "position: fixed",
        Top(74, mbSize),
        "z-index: 1",
        Padding(26, 0, 10, 19, mbSize)
    ])}
`
const ListTitle = styled.h2`
    font-size: 35px;
    font-weight: 500;
    font-family: "Big Caslon";
    margin-bottom: 17px;
    ${Media(mediaPxMb,[
        FontSize(22, mbSize),
        MB(10, mbSize)
    ])}
`
const SubTitle = styled.span`
    font-size: 25px;
    font-weight: 500;
    font-family: "Big Caslon";
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
    margin-bottom: 320px;
    ${Media(mediaPxTablet,[
        MB(320, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        MB(273, mbSize),
        "justify-content: unset;",
        MT(90, mbSize),
    ])}
`