import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
} from '../../common/mediaStlye';
import CommonTitle from '../commonTitle';
import MyQnA from './myQnA';
import MyReview from './myReview';
    
//img
import prdImg from '../../../img/collection/products/productImg_1.png';
import reviewImg_1 from '../../../img/collection/products/productDetail/reviewImg_1.png';
import reviewImg_2 from '../../../img/collection/products/productDetail/reviewImg_2.png';


const today = new Date("2021.01.01");
const title = "안녕하세요. 스킨타입이랑 성분때문에 문의드립니다. "
const content = "제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들때문에 문의드립니다. 제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들 "


const MyActivity = ({setContentStatus, setHeaderIcon, headerIcon}) => {
    



    const [status, setStatus] = useState(0);
    const [qaData, setQaData] = useState([]);
    const [reviewData, setReviewData] = useState([]);
    const navData = [
        {title:"나의 Q&A", status:0},
        {title:"나의 리뷰", status:1},
    ];

    // status가 바뀔때마다 서버통신 
    useEffect(() => {
        if(status == 0){
            setQaData([
                {prdImg:prdImg, prdName:"Sol", date:today, title:title, content:content, type:0},
                {prdImg:prdImg, prdName:"Sol", date:today, title:title, content:content, type:1},
            ])
        }else{
            setReviewData([
                {id: 1, checked:false, prdImg:prdImg, prdNmae:"Soul", prdType:"Skincare", prdPrice:15800, userName:"Ahnnabanana", date:today, star:2, reviewImg:[], review:"1"+content},
                {id: 2, checked:false, prdImg:prdImg, prdNmae:"Soul1", prdType:"Skincare", prdPrice:15800, userName:"Ahnnabanana", date:today, star:5, reviewImg:[reviewImg_1, reviewImg_2], review:"2"+content},
                {id: 3, checked:false, prdImg:prdImg, prdNmae:"Soul2", prdType:"Skincare", prdPrice:15800, userName:"Ahnnabanana", date:today, star:5, reviewImg:[reviewImg_1, reviewImg_2, reviewImg_1, reviewImg_1, reviewImg_1, reviewImg_1], review:content},
            ])
        }
    }, [status]);

    // 나의 QnA, 나의 리뷰 분기 처리
    const activityReturn = () => {
        if(status == 0){
            return <MyQnA qaData={qaData} setHeaderIcon={setHeaderIcon} headerIcon={headerIcon}/>
        }else{
            return(
                <MyReview
                reviewData={reviewData}
                setReviewData={setReviewData}
                />
            )
        }
    };



    return(
        <Wrap>
            <CommonTitle title="나의활동" navData={navData} setStatus={setStatus} setContentStatus={setContentStatus}/>
            {activityReturn()}
        </Wrap>
    )
};

export default MyActivity;

const Wrap = styled.div`
`