//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components";
import BackBtn from '../../../components/common/backBtn';
import Header from '../../../components/header/header';

// img
import noticeDetailMain from '../../../img/info/notice/noticeDetailMain.png';

import {
    Mobile,PC,
    Media,
    PT,
    FontSize,
    Height,
    Padding,
    Margin,
    MT,
    MB,
    Width,
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;


const NoticeDetail = (props) => {

    return(
        <Container>
            <Header isMbBack/>
            <PC>
                <BackBtn />
            </PC>
            <GridWrap>
                <SmallTitleWrap>
                    <SmallTitle>[EVENT] Photo review event</SmallTitle>
                    <Date>2020.01.01</Date>
                </SmallTitleWrap>

                <MainImg>
                    <MainText>Photo review event</MainText>
                </MainImg>

                <ContentWrap>
                    Notice Text 
                </ContentWrap>

                <BtnWrap>
                    <ListBtn>목록보기</ListBtn>
                </BtnWrap>
            </GridWrap>
        </Container>
    )


};
 
export default NoticeDetail;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100vw - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100%",
        "margin:0",
    ])}
`
const SmallTitleWrap = styled.div`
    width:calc(100% - 84px);
    border-bottom:1px solid #1f1113;
    padding: 148px 17px 15px;
    margin: 0 auto 15px;
    display:flex;
    justify-content:space-between;
    ${Media(mediaPxMb,[
        Padding(87, 8, 19, 8, mbSize),
        "width:calc(100% - calc(100vw * 36/428))",
        "flex-direction: column-reverse",
        Margin(0, 10, 19, 10, mbSize)
    ])} 
`
const SmallTitle = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
    ])} 
`
const Date = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])} 
`
const MainImg = styled.div`
    width: 100%;
    height: 823px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(${noticeDetailMain});
    ${Media(mediaPxMb,[
        Height(452, mbSize),
    ])} 
`
const MainText = styled.h1`
    font-size: 67px;
    font-weight: 300;
    color:#E8E2DB;
    ${Media(mediaPxMb,[
        FontSize(30, mbSize),
    ])} 
`
const ContentWrap = styled.div`
    padding: 49px 0 0 60px;
`
const BtnWrap = styled.div`
    width: 100%;
    margin: 166px 0 250px;
    text-align: center;
    ${Media(mediaPxMb,[
        MT(59, mbSize),
        MB(131, mbSize),
    ])} 
`
const ListBtn = styled.button`
    width: 385px;
    height: 66px;
    background: #fff;
    border:1px solid #000;
    font-size: 15px;
    ${Media(mediaPxMb,[
        Width(385, mbSize),
        Height(66, mbSize),
        FontSize(15, mbSize),
    ])} 
`
