//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import plusIconImg from '../../../img/common/plusIcon.png';
import xIconImg from '../../../img/common/xIcon.png';
import selectImg from '../../../img/common/selectImg.png';
import ReviewStarStatus from '../reviewStarStatus';

import {
    Media,
    Width,
    FontSize,
    Margin,
    MB,
    Padding,
    MT,
    Height,
    Px,
    PL,
    ML
} from '../../common/mediaStlye.js';
import ModalHead from './modalHead';

const mediaPxMb = 1024;
const mbSize = 428;

const ReviewModal = ({subtitle, title, onClickBack, onClickSubmit, data}) => {
    const [starStatus, setStarStatus] = useState(1);
    const starListData = ["매우 불만족", "불만족", "보통", "만족", "매우 만족"]
    const [textValue, setTextValue] = useState("");
    const [uploadImgArr, setUploadImgArr] = useState([]);
    
    useEffect(() => {
        if(!data){return}
        setStarStatus(data.star);
        setTextValue(data.review);
        setUploadImgArr([...data.reviewImg]);
    }, [])

    // 이미지 추가
    const onChangeFile = (e) => {
        e.preventDefault(e);
        if(uploadImgArr.length == 6){alert("이미지 더 이상 추가하실 수 없습니다."); return}
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            let newArr = uploadImgArr;
            newArr.push(reader.result);
            setUploadImgArr([...newArr]);
        };
        reader.readAsDataURL(file);
    }

    // 이미지 삭제
    const onClickClose = (index) => {
        let newArr = uploadImgArr;
        newArr.splice(index, 1);
        setUploadImgArr([...newArr]);
    }

    return(
        <Container>
            <ReviewModalWrap>

                <ModalHead subtitle={subtitle} title={title} onClickBack={onClickBack}/>
                <StarWrap>
                    <StarVisual src={ReviewStarStatus(starStatus)}/>
                    <StarList>
                        {
                            starListData.map((item, index) => {
                                return(
                                    <StarListEl active={starStatus==index+1?true:false} onClick={() => setStarStatus(index+1)} key={index}>{item}</StarListEl>
                                )
                            })
                        }
                    </StarList>
                </StarWrap>
                
                <ReviewWrap>
                    <ReviewText value={textValue} onChange={e => setTextValue(e.target.value)} placeholder="슬밋제품의 사용기를 작성해주세요 :)"></ReviewText>
                </ReviewWrap>

                <ImgDesc>* 사진등록은 최대 6장까지 등록하실 수 있습니다.</ImgDesc>
                <ImgWrap>
                    <ImgBox>
                        <ImgBoxInput onChange={(e) => onChangeFile(e)} type="file"/>
                    </ImgBox>
                    {
                        [0, 1, 2, 3, 4, 5].map((item, index) => {
                            return(
                                <ImgBox bgImg={uploadImgArr[index]} key={index}>
                                    {
                                        uploadImgArr[index]&&
                                        <CloseBtn onClick={() => onClickClose(index)}><CloseIcon src={xIconImg} alt="닫기 아이콘"/></CloseBtn>
                                    }
                                </ImgBox>
                            )
                        })

                    }
                </ImgWrap>
                
                <BtnWrap>
                    <SubmitBtn onClick={() => onClickSubmit()}>등록하기</SubmitBtn>
                </BtnWrap>

            </ReviewModalWrap>
        </Container>
    )   
};

export default ReviewModal;

const Container = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.47);
    z-index: 99;
    top:0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
`
const ReviewModalWrap = styled.div`
    background: #fff;
    padding: 25px 35px 45px;
    width: 640px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[ 
        `max-height: calc(100vh - ${Px(100, mbSize)})`,
        "overflow: scroll",
        Width(406, mbSize),
        Padding(28, 8, 36, 8, mbSize)
    ])}
`
const BackBtn = styled.div`
    font-size: 18px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
    ${Media(mediaPxMb,[
        Width(14, mbSize),
        Height(8, mbSize),
    ])}
`
const SubTitleBack = styled.div`
    display: flex;
    justify-content: space-between;
`
const SubTitle = styled.div`
    font-size: 12px;
    font-weight: 500;
    padding-left: 23px;
    ${Media(mediaPxMb,[
        FontSize(10, mbSize),
        PL(12, mbSize),
    ])}
`
const Title = styled.h1`
    font-size: 32px;
    font-weight: 500;
    font-family: "Big Caslon";
    padding: 10px 0 12px 23px;
    border-bottom: 1px solid #000;
    ${Media(mediaPxMb,[
        FontSize(23, mbSize),
        Padding(12, 0, 11, 12, mbSize)
    ])}
`
const StarWrap = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`
const StarVisual = styled.img`
    width: 86px;
    margin: 41px 0 35px;
    ${Media(mediaPxMb,[
        Width(55, mbSize),
        Margin(30, 0, 27, 0, mbSize),
    ])}
`
const StarList = styled.ul`
    display: flex;
    width: 360px;
    justify-content: space-around;
    margin-bottom: 28px;
    ${Media(mediaPxMb,[
        // Width(360, mbSize),
        "width:100%",
        MB(28, mbSize),
    ])}
`
const StarListEl = styled.li`
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    position: relative;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
    ${({active}) => {
        return active?
        `
            &:after{
                position: absolute;
                content: "";
                width: 24px;
                height: 36px;
                opacity:0.9;
                filter:blur(1px);
                background-image: url(${selectImg});
                left: 50%;
                top:-50%;
                transform:translateX(-50%);
            }
        `
        :
        ``
    }}
`
const ReviewWrap = styled.div`
    padding: 0 27px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        "padding:0;"
    ])}
`
const ReviewText = styled.textarea`
    resize: none;
    width: 100%;
    height: 172px;
    padding: 21px 37px 21px 37px;
    font-weight: 300;
    box-sizing: border-box;
    font-size: 12px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        Padding(18, 28, 18, 28, mbSize),
        Height(172, mbSize),
    ])}
`
const ImgDesc = styled.div`
    margin-top: 17px;
    font-size: 12px;
    margin-bottom: 3px;
    font-weight: 300;
    padding-left: 27px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        MT(15, mbSize),
        MB(3, mbSize),
        "padding:0;",
    ])}
`
const ImgWrap = styled.div`
    display:flex;
    width: 100%;
    padding: 0 27px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        "padding:0;"
    ])}
`
const ImgBoxInput = styled.input`
    opacity: 0;
    width: 100%;
    height: 100%;
`
const ImgBox = styled.div`
    width: 75px;
    height: 71px;
    margin-left: 4px;
    border:1px solid #CBCBCB;
    position: relative;
    &:first-child{
        margin-left:0px;
        border:1px solid #000;
        display: flex;
        align-items: center;
        justify-content: center;
        background-image: url(${plusIconImg});
        background-size: 26px 26px;
    }

    ${Media(mediaPxMb,[
        Width(57, mbSize),
        Height(53, mbSize),
        ML(4, mbSize)
    ])}

    ${({bgImg}) => {
        return(`
            background-image: url(${bgImg});
        `)
    }}
`
const CloseBtn =styled.span`
    position: absolute;
    right: 0;
    top:0;
    width: 20px;
    height: 19px;
    background-color: rgba(0, 0, 0, 0.48);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(15, mbSize),
        Height(14, mbSize),
    ])}
`
const CloseIcon =styled.img`
    width: 9px;
    height: 9px;
    ${Media(mediaPxMb,[
        Width(7, mbSize),
        Height(7, mbSize),
    ])}
`
const BtnWrap =styled.div`
    width: 100%;
    text-align: center;
    margin: 27px 0 0;
    ${Media(mediaPxMb,[
        MT(18, mbSize),
    ])}
`
const SubmitBtn = styled.button`
    border: 1px solid #000;
    width: 385px;
    height: 66px;
    background-color: #fff;
    font-size: 15px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        Width(385, mbSize),
        Height(66, mbSize),
    ])}
`