import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    FontSize,
    MB,
    Margin,
    Width,
    Height,
    Padding,
    MR,
    Px,
} from '../../../components/common/mediaStlye';

import DateText from '../../common/dateText';
import MyQnADetail from './myQnADetail';

const content = "제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들어가거제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들어가거제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들어가거제가 민감성 스킨이라 향료와 peg는 무조건 배재하는중인데, 혹시 들어가거"
const title = "안녕하세요. 스킨타입이랑 성분때문에 문의드립니다.";

const mediaPxMb = 1024;
const mbSize = 428;

const MyQnAListEl = ({item, setHeaderIcon, headerIcon}) => {
    const [sliceContent, setSliceContent] = useState("");
    const [isDetail, setIsDetail] = useState(false);
    const [detailData, setDetailData] = useState({});

    // 서버에서 넘어온 타입 한글로 변환
    const returnType = (int) => {
        if(int == 0){
            return("답변 대기중")
        }else{
            return("답변완료")
        }
    }

    // 본문보기 클릭
    const onClickDetail = () => {
        setIsDetail(true);
        setDetailData({
            qustionData:new Date("2021-01-01"), // 질문 날짜
            title:title, // 질문 제목
            type:1, // 답변 여주
            prdImg:item.prdImg, // 상품 이미지
            prdType:"Skincare", // 상품 타입
            prdName:"Soul", // 상품 이름
            prdPrice:15800, // 상품 가격
            question:content, // 질문 내용
            answerDate:new Date("2021-01-01"), // 답변 시간
            answerContent:content, // 답변 내용
        })
    }


    // 펼쳐보기, 문자열자르기 --> 분기처리
    useEffect(() => {
        let newText = item.content;
        if(item.content.length > 37){
            newText=newText.substring(0, 37);
            setSliceContent(newText + "...")
            return;
        }
        setSliceContent(newText)
    }, [])

    useEffect(() => {
        if(isDetail){
            setHeaderIcon([...[false, true]])
        }else{
            setHeaderIcon([...[true, false]])
        }
    }, [isDetail])

    useEffect(() => {
        if(headerIcon[0]){
            setIsDetail(false);
        }
    }, [headerIcon])

    return(
        <ListWrap>
            {
                isDetail&&
                <MyQnADetail detailData={detailData} setIsDetail={setIsDetail}/>
            }
            <ContentWrap>
                <PrdImg src={item.prdImg}/>
                <TextWrap>
                    <DateType>
                        <QDate>{DateText(item.date, ".")}</QDate>
                        <Type active={item.type==1}>{returnType(item.type)}</Type>
                    </DateType>
                    <Name>{item.prdName}</Name>
                    <Title>{item.title}</Title>
                    <Content>{sliceContent}</Content>
                </TextWrap>
            </ContentWrap>
            <BtnWrap> 
                <ViewBtn onClick={() => onClickDetail()}>본문보기</ViewBtn>
            </BtnWrap>
        </ListWrap>
    )

};

export default MyQnAListEl;

const ListWrap = styled.div`
    border-bottom: 1px solid #E2E2E2;
    padding: 15px 19px 0 60px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        Padding(15, 16, 15, 16, mbSize), 
    ])}
`
const ContentWrap = styled.div` 
    display: flex;
`
const PrdImg = styled.img` 
    width: 87px;
    height: 107px;
    margin-right: 13px;
    ${Media(mediaPxMb,[
        Width(87, mbSize),
        Height(107, mbSize),
        MR(8, mbSize),
    ])}
`
const TextWrap = styled.div` 
    width: calc(100% - 100px);
    ${Media(mediaPxMb,[
        `width:calc(100% - ${Px(95, mbSize)})`
    ])}
`
const DateType = styled.div` 
    display: flex;
    justify-content: space-between;
`
const QDate = styled.div` 
    font-size: 12px;
    color:#A0A0A0;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
`
const Type = styled.div` 
    color:#A0A0A0;
    font-size: 12px;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}

    ${({active}) => {
        return active&&`color:#E83548;`
    }}
`
const Name = styled.div` 
    font-size: 20px;
    margin: 4px 0 5px ;
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
        Margin(4, 0, 5, 0, mbSize),
    ])}
`
const Title = styled.div` 
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 5px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    width: 100%;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        MB(6, mbSize)
    ])}
`
const Content = styled.div` 
    font-size: 12px;
    font-weight: 300;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
`
const BtnWrap = styled.div` 
    text-align: right;
    margin: 16px 0 15px;
    ${Media(mediaPxMb,[
    ])}
`
const ViewBtn = styled.button` 
    color:#fff;
    background-color: #211013;
    width: 98px;
    height: 33px;
    outline: none;
    border: 0;
    font-size: 12px;
    ${Media(mediaPxMb,[
        Width(98, mbSize),
        Height(33, mbSize),
        FontSize(12, mbSize)
    ])}
`
