//react
import React from 'react';

// style
import styled from "styled-components"

// components
import GalleryItemCollect from './galleryItemCollect';

import {
    Media,
    FontSize,
    MB,
    PC,
} from '../common/mediaStlye';

const mediaPxTablet = 1300;

const GallertEpEl = ({itemArr, title}) => {
    

    return(
        <ContentWrap>
            <PC><ContentTitle>{title}</ContentTitle></PC>
            <GalleryWrap>
                {
                    itemArr.map((item, index) => {
                        return(
                            <GalleryItemCollect
                            key={index}
                            item={item}
                            />
                        )
                    })
                }
            </GalleryWrap>
        </ContentWrap>
    )
};

export default GallertEpEl;

const ContentWrap = styled.div`
    width:100%;
    margin-bottom: 65px;
    ${Media(mediaPxTablet,[
        MB(65, mediaPxTablet),
    ])}
`
const ContentTitle = styled.div`
    margin-bottom: 66px;
    font-size: 25px;
    font-weight:500;
    font-family: "Big Caslon";
    ${Media(mediaPxTablet,[
        FontSize(25, mediaPxTablet),
        MB(66, mediaPxTablet)
    ])}
`
const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
`