//react
import React ,{useState} from 'react';

// style
import styled from "styled-components"
import Header from '../../../components/header/header';
import GalleryItemMSeul from '../../../components/taste/GalleryItemMSeul';

// img
import mS_1 from '../../../img/taste/mSeulmit/mS_1.png';
import mS_2 from '../../../img/taste/mSeulmit/mS_2.png';
import mS_3 from '../../../img/taste/mSeulmit/mS_3.png';
import mS_4 from '../../../img/taste/mSeulmit/mS_4.png';
import mS_5 from '../../../img/taste/mSeulmit/mS_5.png';
import mS_6 from '../../../img/taste/mSeulmit/mS_6.png';
import SearchIconImg from '../../../img/taste/mSeulmit/searchIcon.png';

import {
    Media,
    Width,
    FontSize,
    Height,
    Margin,
    PT,
    PL,
    Right,
    PC
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const MSeulmit = (props) => {
    const [searchValue, setSearchValue] = useState("");
    const galleryData = [
        {
            img:mS_1,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
        {
            img:mS_2,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
        {
            img:mS_3,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
        {
            img:mS_4,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
        {
            img:mS_5,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
        {
            img:mS_6,
            title:"2021.1",
            historyLink:"/MSeulmitDetailFirst"
        },
    ]

    const onChangeValue = (e) => {
        setSearchValue(e.target.value)
    }

    const onClickSearch = () => {
        console.log("onClickSearch");
    }

    const onClickMore = () => {
        console.log("onClickMore") 
    }

    return(
        <Container>
            <Header isSearch/>
            <GridWrap>
                <Grid>
                    <Title>M.Seulmit</Title>
                    <PC>
                        <SearchInputWrap>
                            <SearchInput type="text" placeholder="Search" value={searchValue} onChange={(e) => onChangeValue(e)}/>
                            <SearchIcon onClick={() => onClickSearch()} src={SearchIconImg} alt="검색 아이콘"/>
                        </SearchInputWrap>
                    </PC>
                    <GalleryWrap>
                        {
                            galleryData.map((item, index) => {
                                return(
                                    <GalleryItemMSeul
                                    key={index}
                                    img={item.img}
                                    title={item.title}
                                    desc={item.desc}
                                    isDouble={item.isDouble}
                                    historyLink={item.historyLink}
                                    />
                                )
                            })
                        }
                    </GalleryWrap>

                    <BtnWrap>
                        <MoreBtn onClick={() => onClickMore()}>더보기</MoreBtn>
                    </BtnWrap>
                </Grid>
            </GridWrap>
        </Container>
    )
};

export default MSeulmit;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
const Grid = styled.div`
    width:1052px;
    margin:0 auto;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
    ])}
`
const Title = styled.p`
    font-size:35px;
    padding-top: 71px;
    font-family: "Big Caslon";
    font-weight: 500;
    ${Media(mediaPxTablet,[
        FontSize(35, mediaPxTablet),
        PT(71, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(22, mbSize),
        Margin(0, 0, 19, 19, mbSize)
    ])} 
`
const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
`
const BtnWrap = styled.div`
    text-align: center;
`
const MoreBtn = styled.button`
    font-size:15px;
    border:1px solid #000;
    outline: none;
    width:385px;
    height: 66px;
    margin: 85px 0 261px;
    background-color: #fff;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Width(385, mediaPxTablet),
        Height(66, mediaPxTablet),
        Margin(85, 0, 261, 0, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Width(385, mbSize),
        Height(66, mbSize),
        Margin(50, 0, 171, 0, mbSize),
    ])}
`
const SearchInputWrap = styled.div`
    margin: 17px 0 109px;
    position: relative;
    width: 362px;
    height: 46px;
    ${Media(mediaPxTablet,[
        Width(362, mediaPxTablet),
        Height(46, mediaPxTablet),
        Margin(17, 0, 109, 0, mediaPxTablet),
    ])}
`
const SearchInput = styled.input`
    width: 100%;
    height: 100%;
    border:1px solid #000;
    padding-left: 30px;
    ${Media(mediaPxTablet,[
        PL(30, mediaPxTablet),
    ])}
`
const SearchIcon = styled.img`
    width: 13px;
    height: 13px;
    position: absolute;
    top:50%;
    right:22px;
    transform:translateY(-50%);
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Width(13, mediaPxTablet),
        Height(13, mediaPxTablet),
        Right(22, mediaPxTablet),
    ])}
`

