//react
import React ,{useState, useEffect} from 'react';
import {Link} from "react-router-dom";

// style
import styled from "styled-components"

// img
import DetailFirstImg_1 from '.././../img/taste/detailFirst/detailFirst_1.png'
import DetailFirstImg_2 from '.././../img/taste/detailFirst/detailFirst_2.png'

// components
import Header from '../../components/header/header';
import BackBtn from '../../components/common/backBtn';

import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    MB,
    LineH,
    PC,
    Mobile,
    PT,
    MT,
} from '../../components/common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const TasteDetailFirst = (props) => {


    return(
        <Container>
            <Header isMbBack/>
            <PC>
                <BackBtn />
            </PC>
            <GridWrap>
                <Mobile>
                    <SmallTitleWrap>
                        <SmallTitle>iconics</SmallTitle>
                        <Date>2020.01.01</Date>
                    </SmallTitleWrap>
                </Mobile>

                <MainSection>
                    <Title>IconIcs</Title> 
                </MainSection>
                {/* 중간 */}
                <MiddleSection>
                    <PC>
                        <SmallTitleWrap>
                            <SmallTitle>iconics</SmallTitle>
                            <Date>2020.01.01</Date>
                        </SmallTitleWrap>
                    </PC>

                    <MiddleVisual>
                        <MiddleImg src={DetailFirstImg_2} alt="이미지"/>
                        <MiddleText>
                            <SubTitle>Los Angeles</SubTitle>
                            <MiddlTitle>Pants</MiddlTitle>
                            <MiddleDesc>
                                Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website<br />
                                design, animation and video game production. … Artists can work as freelancers or for studios,<br />
                                advertising agencies, media design companies, marketing firms or in graphic arts departments.<br />
                                <br /><br />
                                Artist is a broad term that describes occupations in fine arts,<br />
                                crafts, desktop publishing, website design, animation and video game production. …<br />
                                Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                                firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                                crafts, desktop publishing, website design, animation and video game production. …<br />
                                Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                                firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                                crafts, desktop publishing, website design, animation and video game production. …<br />
                                Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                            </MiddleDesc>
                        </MiddleText>
                    </MiddleVisual>
                </MiddleSection>
 
                {/* 히단 */}
                <EndSection>
                    <EndImg bgImg={DetailFirstImg_1}></EndImg>
                    <EndTextWrap>
                        <EndText>
                            Artist is a broad term that describes occupations in fine arts, <br />
                            crafts, desktop publishing, website design, animation and video game production. …<br />
                            Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                            firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                            crafts, desktop publishing, website design, animation and video game production. …<br />
                            Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                            firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                            crafts, desktop publishing, website design, animation and video game production. …<br />
                            Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                        </EndText>
                    </EndTextWrap>
                </EndSection>
            </GridWrap>
        </Container>
    )
};

export default TasteDetailFirst;

const Container = styled.div`
`

const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 메인 ---
const MainSection = styled.div`
    width:100%;
    ${Height(893, 1920)}
    background-image:url(${DetailFirstImg_1});
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    display:flex;
    align-items:center;
    justify-content:center;
    position: relative;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
    ${Media(mediaPxMb,[
        Height(694, mbSize),
    ])}
`
const Title = styled.div`
    color:#e8e2db;
    ${FontSize(79, 1920)}
    font-weight:300;
    ${Media(mediaPxMb,[
        FontSize(47, mbSize),
    ])}
`
// 중간 ---
const MiddleSection = styled.div`
`
const SmallTitleWrap = styled.div`
    width:calc(100% - 84px);
    border-bottom:1px solid #1f1113;
    ${Padding(37, 17, 17, 17, 1920)}
    margin: 0 auto 15px;
    display:flex;
    justify-content:space-between;

    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "flex-direction: column-reverse",
    ])}
`
const SmallTitle = styled.span`
    /* font-size:20px; */
    font-weight:500;
    ${FontSize(20, 1920)}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
        MT(7, mbSize),
    ])}
`
const Date = styled.span`
    font-weight:500;
    ${FontSize(20, 1920)}
    ${Media(mediaPxMb,[
        "font-weight:300",
        FontSize(13, mbSize),
    ])}
`

const MiddleVisual = styled.div`
    display:flex;
    ${Media(mediaPxMb,[
        "flex-direction: column",
    ])}
`
const MiddleImg = styled.img`
    width:50%;
    ${Media(mediaPxMb,[
        "width:100%"
    ])}
`
const MiddleText = styled.div`
    width:50%;
    ${Padding(454, 0, 0, 82,1920)}
    ${Media(mediaPxMb,[
        "width:100%",
        Padding(42, 27, 0, 18,mbSize),
        "box-sizing: border-box",
    ])}
`
const SubTitle = styled.div`
    ${FontSize(18, 1920)}
    color:#707070;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
    ])}
`
const MiddlTitle = styled.div`
    /* font-size:110px; */
    ${FontSize(110, 1920)}
    color:#211013;
    margin:25px 0 81px;
    font-family: "Big Caslon";
    ${Margin(25, 0, 81, 0, 1920)};
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(87, mbSize),
    ])}
`
const MiddleDesc = styled.div`
    font-size:15px;
    color:#707070;
    font-family: "Big Caslon";
    font-weight: 500;
    /* line-height:30px; */
    /* margin-bottom:48px; */
    ${FontSize(15, 1920)}
    ${LineH(30, 1920)}
    ${MB(48, 1920)}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        LineH(30, mbSize),
    ])} 
`



// 하단 ---
const EndSection = styled.div`
    ${Media(mediaPxMb,[
        Margin(70, 0, 219, 0, mbSize),
    ])} 
`
const EndImg = styled.div`
    width:100%;
    height:823px;
    ${Height(823, 1920)}
    ${Media(mediaPxMb,[
        Height(823, mbSize)
    ])}
    ${({bgImg}) => {
        return`
            background-image:url(${bgImg});
        `
    }}
`

const EndTextWrap = styled.div`
    padding:68px 0 250px 59px;
    ${Padding(68, 0, 250, 59, 1920)}
`
const EndText = styled.div`
    color:#707070;
    font-size:15px;
    font-weight:500;
    line-height:30px;
    font-family: "Big Caslon";
    font-weight: 500;
    ${FontSize(15, 1920)}
    ${LineH(30, 1920)}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        LineH(30, mbSize),
    ])}
`

