import React, { useEffect, useRef, useState } from 'react';

// components
import {
    styled,
    Media,
    Margin,
    MB,
    FontSize,
    PL,
    Height,
} from '../../common/mediaStlye';
import CommonTitle from '../commonTitle';
import InfoInput from './infoInput';

const mediaPxMb = 1024;
const mbSize = 428;

const Information = ({setContentStatus}) => {
    const inputListRef = useRef();
    const [inputData, setInputData] = useState([
        {title:"이름", errText:"", isError:false,},
        {title:"우편번호", errText:"", isError:false,},
        {title:"배송지 주소", errText:"", isError:false, isSearch:true},
        {title:"추가 주소정보", errText:"", isError:false, isNotImp:true},
        {title:"휴대폰", errText:"", isError:false,},
        {title:"이메일", errText:"", isError:false,},
        {title:"생년월일", errText:"", isError:false,},
        {title:"비밀번호", errText:"", isError:false,},
        {title:"비밀번호 확인", errText:"", isError:false,},
    ]);

    // 에러 활성화
    const activeError = (index) => {
        let newArr = inputData;
        newArr[index].isError = true;
        setInputData([...newArr]);
    }

    // 에러 초기화
    const initError = () => {
        let newArr = inputData;
        for(let i = 0; i < newArr.length ; i++){
            newArr[i].isError = false;
        }
        setInputData([...newArr]);
    }
    // 저장버튼 클릭 
    const onClickSave = () => {
        const inputEl = inputListRef.current.children;
        initError();
        for(let i = 0; i < inputEl.length ; i++){
            if(!inputEl[i].children[0].value){activeError(i)}
        }
    }


    return(
        <Wrap>
            <CommonTitle title="멤버정보" setContentStatus={setContentStatus} noTab/>
            <Grid>
                <InputWrap ref={inputListRef}>
                    {
                        inputData.map((item, index) => {
                            return(
                                <InfoInput item={item} key={index} marginRight={index==2}/>
                            )
                        })
                    }
                </InputWrap>
                <Out>회원 탈퇴</Out>
                <SaveBtn onClick={() => onClickSave()}>저장</SaveBtn>
            </Grid>

        </Wrap>
    )

};

export default Information;

const Wrap = styled.div`
`
const Grid = styled.div`
    width: 569px;
    ${Media(mediaPxMb,[
        "width:100%"
    ])}
`
const InputWrap = styled.div`
    margin: 38px auto 24px;
    padding: 0 28px 24px;
    border-bottom: 1px solid #E2E2E2;
    ${Media(mediaPxMb,[ 
        Margin(43, 0, 22, 0, mbSize),
        "padding:0 0 30px;"
    ])}
`
const Out = styled.div`
    margin: 0px 0 57px;
    font-size: 14px;
    padding-left: 82px;
    font-weight: 400;
    ${Media(mediaPxMb,[ 
        MB(45, mbSize),
        FontSize(14, mbSize),
        PL(27, mbSize),
    ])}
`
const SaveBtn = styled.button`
    border:1px solid #000;
    font-size: 15px;
    width: 385px;
    height: 66px;
    background-color: #fff;
    margin-left: 82px;
    margin-bottom: 156px;
    ${Media(mediaPxMb,[
        "width:100%",
        "margin:0",
        Height(66, mbSize),
        FontSize(15, mbSize),
        MB(227, mbSize),
    ])}
`