//react
import React from 'react';

// style
import styled from "styled-components"

// img
import dropArrowImg from '../../../img/common/dropArrow.png';

import {
    Media,
    Width,
    FontSize,
    Height,
} from '../../../components/common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const DropTop = ({title, index, onClickDrop, currentDrop, onClick}) => {


    return(
        <DropTopWrap onClick={() => {onClickDrop(index); onClick&&onClick();}}>
            <DropTitle>{title}</DropTitle>
            <LineArrow>
                <Line></Line>
                <DropArrow active={currentDrop[index]?true:false} src={dropArrowImg} alt="화살표 아이콘"/>
            </LineArrow>
        </DropTopWrap>
    )
};

export default DropTop;

const DropTopWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    cursor: pointer;
`
const DropTitle = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const LineArrow = styled.div`
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const Line = styled.span`
    display: inline-block;
    width: 80%;
    height: 1px;
    border-bottom:1px solid #000;
`
const DropArrow = styled.img`
    width: 11px;
    height: 8px;
    transition: 300ms;
    ${Media(mediaPxMb,[
        Width(11, mbSize),
        Height(8, mbSize),
    ])}
    ${({active}) => {
        return active?
        `transform: rotate(180deg);`
        :
        ``
    }}
`