import React, { useEffect, useRef, useState } from 'react';

// style
import styled from "styled-components"
import "./purchaseInput.css";

import {
    Media,
    Width,
    FontSize,
    MB,
    Mobile,
    Height,
    Top,
    Left,
    MR
} from '../../components/common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const PurchaseInput = ({item, marginRight}) => {
    const subErrRef = useRef();
    const [inputValue, setInputValue] = useState("");

    const onChangeInput = (e) => {
        setInputValue(e.target.value);
    }

    const subErr = () => {
        if(item.title == "배송지 주소" && item.isError){
            return <SubErrText isError={item.isError} >유효하지 않은 주소 입니다.<Mobile><br /></Mobile> 정확하게 입력하여주세요.</SubErrText>
        }else if(item.title == "이메일" && !item.isError){
            return <SubErrText isError={item.isError}>(이메일을 통해 주문처리과정을 보내드립니다.<Mobile><br /></Mobile> 반드시 수신가능한 이메일주소를 입력해 주세요.)</SubErrText>
        }else{
            return <SubErrText ref={subErrRef} isError={item.isError} >{item.isError?item.errorText:item.subText}</SubErrText>
        }
    }
    return(
        <InputWrap marginRight={marginRight}>
            <Input ref={item.ref} value={inputValue} onChange={(e) => onChangeInput(e)} type="text" isError={item.isError}/>
             {  
                item.isSearch&&
                <AddressSearch>주소 <Mobile><br /></Mobile> 검색</AddressSearch>
             }
            <InputTitleWrap>
                <InputTitle>{item.title} {!item.isNotImp&&<ImportantDot>*</ImportantDot>}</InputTitle>
                {subErr()}
            </InputTitleWrap>
        </InputWrap>
    )
};

export default PurchaseInput;

const InputWrap = styled.div`
    width: 100%;
    height: 79px;
    position: relative;
    margin-bottom: 5px;
    display: flex;
    ${Media(mediaPxMb,[
        Height(79, mbSize),
        MB(5, mbSize)
    ])}
    ${({marginRight}) => {
      return marginRight&&`margin-right:20px;`
    }}
`
const Input = styled.input`
    width: 100%;
    height: 100%;
    outline: none;
    text-align: center;
    font-size: 15px;
    font-weight: 300;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
    ])}
    ${({isError}) => {
        return isError?
        `border:1px solid #D7464E;`
        :
        `border:1px solid #000;`
    }}
`
const AddressSearch = styled.button`
    background-color: #000;
    color:#fff;
    width: 131px;
    height: 100%;
    margin-left: 7px;
    border:1px solid #000;
    font-size: 15px;
    ${Media(mediaPxMb,[ 
        Width(90, mbSize),
        FontSize(13, mbSize),
    ])} 
`
const InputTitleWrap = styled.div`
    position: absolute;
    top:8px;
    left: 13px;
    display: flex;
    ${Media(mediaPxMb,[
        Top(11, mbSize),
        Left(13, mbSize),
    ])}
`
const InputTitle = styled.div`
    margin-right: 9px;
    font-size: 15px;
    font-weight: 300;
    ${Media(mediaPxMb,[
        MR(5, mbSize),
        FontSize(12, mbSize)
    ])}
`
const ImportantDot = styled.span`
    font-size: 15px;
    font-weight: 300;
    color:#E83548;
    ${Media(mediaPxMb,[
        FontSize(9, mbSize),
    ])}
`
const SubErrText = styled.div`
    font-size: 15px;
    font-weight: 300;
    ${Media(mediaPxMb,[
        FontSize(9, mbSize),
    ])}

    ${({isError}) => {
        return isError?
        `color:#D7464E;`
        :
        `color:#A0A0A0;`
    }}
`