import React ,{useState, useEffect} from 'react';


// img
import fillArrowImg from '../../img/common/fillArrow.png';
import greyArrowImg from '../../img/common/greyArrow.png';
import trashIconImg from '../../img/common/trashIcon.png';


//css
import {
    Media,
    Width,
    FontSize,
    Padding,
    Padding2,
    Height,
    Px,
    styled,
    ML,
    MR
} from '../common/mediaStlye';

import InsertComma from '../common/insertComma';

const mediaPxMb = 1024;
const mbSize = 428;

export default function SelectProduct({item,title,count, price,index,removeSelectItem,updateCount}){

  const [optionText, setOptionText] = useState("")
  var optionList = [];

  for(const key in item.optionList){
    optionList.push(item.optionList[key]);
  }
  
  const decreaseCount = () =>{
    if(count > 1){
      updateCount(index,count - 1);
    }
  }


  const increaseCount = () =>{
    updateCount(index,count + 1);
  }

  useEffect(() => {
    for(let key in item.optionList){
        let list = item.optionList[key];
        let optText = list[0].opt_name;
        for(let i = 1 ; i < list.length ; i++){
            optText = optText + " / " + list[i].opt_name;
        }
        setOptionText(optText)
    }
  }, [])


  return (
    <SelectWrap>
        <SelectList>

            {
                optionList.map((list, item)=>{
                    return(
                        <SelectItemCon key={item}>
                            <SelectTitleOption>
                                <SelectTitle>{title}</SelectTitle>
                                <TitleDelete>
                                    <Selectoption>{optionText}</Selectoption>
                                    <DeleteBtn onClick={() => {removeSelectItem(index)}} src={trashIconImg} alt="삭제 아이콘"/>
                                </TitleDelete>
                            </SelectTitleOption>
                            <CountWrap>
                                <CountItem onClick={() => {if(item.count==1){return}decreaseCount()}}>
                                    <CountArrow src={count==1?greyArrowImg:fillArrowImg} alt="화살표 아이콘"/>
                                </CountItem>
                                <CountItem>
                                    <CountText>{count}</CountText>
                                </CountItem>
                                <CountItem onClick={() => increaseCount()}>
                                    <CountArrow style={{transform: "rotate(180deg)"}} src={fillArrowImg} alt="화살표 아이콘"/>
                                </CountItem>
                            </CountWrap>
                        </SelectItemCon>
                    )
                })
            }

        </SelectList>
        
        {/* <SelectPriceWrap>
            <Total>Total</Total>
            <TotalPrice>
                {InsertComma(price)} won
            </TotalPrice>
        </SelectPriceWrap> */}
    </SelectWrap>
    

    // <View style={styles.container}>
    //   <View style={styles.titleContainer}>
    //     <Text style={styles.title}>{title}</Text>
    //     <TouchableOpacity onPress={()=>{removeSelectItem(index)}}>
    //       <Text style={styles.title}>X</Text>
    //     </TouchableOpacity>
    //   </View>
    //   {
    //     optionList.map((list)=>{

    //       let optionName = "";
    //       let price = 0;
          
    //       for(var i = 0;i<list.length;i++){
    //         price += list[i].opt_price;
    //         optionName += " " + list[i].opt_name + ( i + 1 == list.length ?  " " :  " /");
    //       }
    //       optionName += price > 0 ? " 옵션가 : " + numberWithCommas(price) +"원" : "";
    //       return <Text style={styles.title}>[ 옵션 : {optionName} ]</Text>;
    //     })
    //   }
    //   <View style={styles.resultContainer}>
    //     <View style={styles.countContainer}>
    //      <TouchableOpacity style={styles.touchableOpacity} onPress={decreaseCount}>
    //      <Image style={styles.minus} source={require('../../../assets/img/minus-plus/minus.png')} resizeMode={"center"}/>
    //       </TouchableOpacity>
    //         <Text style={styles.num}>{count}</Text>
    //       <TouchableOpacity style={styles.touchableOpacity} onPress={increaseCount}>
    //       <Image style={styles.plus} source={require('../../../assets/img/minus-plus/plus.png')} resizeMode={"center"}/>
    //       </TouchableOpacity>
    //     </View>
    //   <Text>{numberWithCommas(price)}원</Text>
    //   </View>
   
    // </View>

  )
};

// 선택 상품
const SelectWrap = styled.div`
    width: 100%;
    /*
    border:1px solid #000;
    margin-top: 21px;
    */
`
const SelectList = styled.div`
    background-color: #F8F7F7;
    
    padding: 0 17px;
    ${Media(mediaPxMb,[
        Padding2(0, 17, mbSize),
    ])}
`
const SelectPriceWrap = styled.div`
    width: calc(100% - 72px);
    height: 61px;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 42px;
    ${Media(mediaPxMb,[
        `width: calc(100% - ${Px(34, mbSize)})`,
        Padding(0, 17, 0, 17, mbSize),
        Height(61, mbSize),
    ])} 
`
const Total = styled.div`
    font-size: 15px;
    color:#fff;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize)
    ])} 
`
const TotalPrice = styled.div`
    font-size: 18px;
    font-weight: 500;
    color:#fff;
    ${Media(mediaPxMb,[
        FontSize(18, mbSize)
    ])} 
`

const SelectItemCon = styled.div`
    width: 100%;
    border-bottom:1px solid #CBCBCB;
    padding:14px 0 15px;
    ${Media(mediaPxMb,[
        Padding(14, 0, 15, 0, mbSize),
    ])}
    &:last-child{
        border-bottom: 0;
    }
`
const SelectTitleOption = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const TitleDelete = styled.div`
    display: flex;
    align-items: center;
`
const DeleteBtn = styled.img`
    width: 17px;
    height: 17px;
    margin-left:13px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(17, mbSize),
        Height(17, mbSize),
        ML(13, mbSize),
    ])}
`
const SelectTitle = styled.div`
    font-size: 15px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const Selectoption = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const CountWrap = styled.div`
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-top: 11px;
`
const CountItem = styled.div`
    border:1px solid #000;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:nth-child(2){
        border-left: 0;
        border-right: 0;
    }
    &:last-child{
        margin-right: 30px;
        ${Media(mediaPxMb,[
            MR(30, mbSize),
        ])}
    }
    ${Media(mediaPxMb,[
        Width(32, mbSize),
        Height(32, mbSize),
    ])}
`
const CountArrow = styled.img`
    width: 9px;
    height: 7px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(9, mbSize),
        Height(7, mbSize),
    ])}
`
const CountText = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
