//react
import React,{useState,useEffect}  from 'react';

//css
import styled from "styled-components"

//img
import fillArrowImg from '../../img/common/fillArrow.png';

export default function DetailSetOptionSelectBox({title,list,clickSelect}) {

    //const [title,setTitle ] = useState("");
    const [show,setShow ] = useState(false);
    const [selectTitle, setSelectTitle] = useState(title);
    const [listHistory, setListHistory] = useState(list);
    // if(list == undefined || list.length == 0 || reset == true){
    //   return <View></View>
    // }

    // title 설정
    // list와 왜 비교하는지??
    useEffect(() => {
      if(!list || list[0].opt_relate_id == 0){
        return;
      }
      
      if(listHistory != null){
        if(JSON.stringify(listHistory) != JSON.stringify(list)){
          setSelectTitle(title);
        }
      }
        
      setListHistory(list);   
    }, [list])

    // 옵션 리스트 토글
    const openDropDownModal = (e) =>{
      // 상위 옵션 선택하지 않으면 클릭이벤트 x
      if(list == null){
        alert("다른 옵션을 먼저 선택해주세요.")
        return;
      }
      setShow(e => !e);
    }

    // 옵션 선택
    const modalSelect = (value) =>{
      setShow(false);

      if(clickSelect(value) == false)
        return;

      setSelectTitle(value.label);
    }

    return (
      <OptionWrap>
        <Optionbox onClick={openDropDownModal} active={show}>
            <p>{selectTitle}</p>
            <ArrowImg src={fillArrowImg} alt="방향아이콘" active={show}/>
        </Optionbox>
        {
            show && list ?
            <SelectBoxWrap>
                {
                    list.map((value,index)=>{
                    return (
                        <SelectBox key={index} onClick={()=>{modalSelect(value)}}>
                            <p>{value.label}</p>
                        </SelectBox>
                    )
                    })
                }
            </SelectBoxWrap>
            :
            null
        }
    </OptionWrap>
    );
}
          

const OptionWrap = styled.div`
    width: 100%;
    position: relative;
    margin-top: 5px;
`
const Optionbox = styled.div`
    width: 100%;
    height: 44px;
    position: relative;
    font-size: 15px;
    line-height: 44px;
    text-align: center;
    font-weight: 300;
    border: 1px solid #000;
    cursor: pointer;
    ${({active}) => {
        return active?`border-bottom:0;`:``
    }}
`
const ArrowImg = styled.img`
    position: absolute;
    right:30px;
    top:50%;
    transform: translateY(-50%);
    width: 10px;
    height: 8px;
    transition: 300ms;
    ${({active}) => {
        return active?`transform:rotate(180deg)`:``
    }}
`
const SelectBoxWrap = styled.div`
    position: absolute;
    z-index:10;
    left: 0%;
    width: 100%;
    border: 1px solid #000;
    border-top: 0px;
    background: #fff;
`
const SelectBox = styled.div`
    background-color: #fff;
    text-align: center;
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 15px;
    font-weight: 300;
    cursor: pointer;
    &:hover{
        background-color: #F8F7F7;
    }
`