//react
import React from 'react';
import {useHistory} from "react-router-dom";

// style
import styled from "styled-components"

import {
    PC,
    Media,
    FontSize,
    MB,
    Width,
    Height,
    Bottom,
    Mobile,
    Margin,
} from '../common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const GalleryItemCollect = ({item}) => {
    const history = useHistory();

    return(
        <>
            <GalleryItem bgImg={item.img} onClick={() => history.push("/EpisodeDetail")}>
                <PC>
                    <GalleryHover>
                        <HoverText>
                            <HoverTitle>{item.title}</HoverTitle> 
                            {
                                item.subTitle&&
                                <HoverSubTitle>{item.subTitle}</HoverSubTitle>
                            }
                        </HoverText>
                    </GalleryHover>
                </PC>
            </GalleryItem>
            <Mobile>
                <MbTextWrap>
                    <NavTitle>{item.navTitle}</NavTitle>
                    <MbTitle>{item.title}</MbTitle>
                </MbTextWrap>
            </Mobile>
        </>
    )
};


export default GalleryItemCollect;

const MbTextWrap = styled.div`
    display: flex;
    flex-direction: column;
    ${Margin(18, 0, 47, 19, mbSize)}
`
const NavTitle = styled.div`
    ${FontSize(15, mbSize)}
`
const MbTitle = styled.div`
    ${FontSize(23, mbSize)}
`

const GalleryItem = styled.div`
    width: 490px;
    height: 604px;
    margin-bottom: 66px;
    position: relative;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Width(490, mediaPxTablet),
        Height(604, mediaPxTablet),
        MB(66, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100%",
        Height(527, mbSize),
    ])}

    ${({bgImg}) => {
        return(
            `
            background-image: url(${bgImg});
            `
        )
    }}
`

const GalleryHover = styled.div`
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transition: 500ms;
    &:hover{
        opacity: 1;       
    }
    &:hover > span{
        bottom: 38px;
    }
`
const HoverText = styled.span`
    position: absolute;
    bottom: 58px;
    left: 50%;
    transform: translateX(-50%);
    transition: 500ms;
    display: inline-block;
    text-align: center;
    ${Media(mediaPxTablet,[
        Bottom(58, mediaPxTablet),
    ])}
`
const HoverTitle  = styled.div`
    color:#fff;
    font-size: 23px;
    ${Media(mediaPxTablet,[
        FontSize(23, mediaPxTablet),
    ])}
`
const HoverSubTitle = styled.div`
    font-size: 15px;
    color:#fff;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
    ])}
`