import React, {useState} from 'react';

// style
import styled from "styled-components"
import WhiteBackBtn from '../whiteBackBtn';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/swiper.scss";

// img
import leftArrowImg from '../../../img/taste/mSeulmit/detail/leftArrow.png';
import rightArrowImg from '../../../img/taste/mSeulmit/detail/rightArrow.png';
import mbBackIconImg from '../../../img/common/mbBackIcon.png';
import whiteArroMbImg from '../../../img/common/whiteArroMb.png';

import {
    Media,
    Width,
    FontSize,
    Margin,
    PT,
    PC,
    MB,
    Padding,
    MT,
    PB,
    Padding2,
    LineH,
    Mobile,
    Height,
    Px,
    Bottom,
    Left,
    Top
} from '../../common/mediaStlye.js';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const ReviewImgModal = ({array, onClick, imageIndex, setImageIndex}) => {

    const onClickArrow = (type) => {
        let newIndex = imageIndex;
        if(type=="minus"){
            if(imageIndex==0){return}
            newIndex--;
        }else{
            if(imageIndex==array.length-1){return}
            newIndex++;
        }
        setImageIndex(newIndex);
    }

    return(
        <ImageModalWrap>
                <PC><WhiteBackBtn onClick={() => onClick()}/></PC>
                <MbImgBack>
                    <Mobile>
                        <MbBackBtn onClick={() => onClick()} src={mbBackIconImg}/>
                    </Mobile>
                    <ReviewImg src={array[imageIndex]}/>
                </MbImgBack>


            <ArrowWrap>
                <PC><LeftArrow src={leftArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("minus")}/></PC>
                <Mobile><LeftArrow style={{transform:`rotateY(180deg)`}} src={whiteArroMbImg} alt="방향 아이콘" onClick={() => onClickArrow("minus")}/></Mobile>
                <IndexWrap>
                    {imageIndex+1} ——— {array.length}
                </IndexWrap>
                <PC><RightArrow src={rightArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("plus")}/></PC>
                <Mobile><RightArrow src={whiteArroMbImg} alt="방향 아이콘" onClick={() => onClickArrow("plus")}/></Mobile>
                
            </ArrowWrap>
        </ImageModalWrap>
    )
};

export default ReviewImgModal;

const ImageModalWrap = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.47);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    ${Media(mediaPxMb,[
        `background: rgba(0, 0, 0, 0.8)`,
    ])}
`
const ReviewImg = styled.img`
    width: 640px;
    ${Media(mediaPxMb,[
        Width(338, mbSize), 
    ])}
`
const MbImgBack = styled.div`
    display: flex;
    flex-direction: column;
`
const MbBackBtn = styled.img`
    ${Width(19, mbSize)};
    filter: invert(100);
    ${MB(20, mbSize)};
    cursor: pointer;
`
const ArrowWrap = styled.div`
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 87px;
    display: flex;
    justify-content: center;
    align-items: center;
    &>img{
        cursor: pointer;
        filter: invert(100%);
        ${Media(mediaPxMb,[
            "filter:invert(0%)",
        ])}
    }
    ${Media(mediaPxTablet,[
        Height(87, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Bottom(10, mbSize),
    ])}
`
const LeftArrow = styled.img`
    width: 264px;
    ${Media(mediaPxTablet,[
        Width(264, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Width(88, mbSize),
    ])}
`
const IndexWrap = styled.div`
    margin: 0 100px;
    color:#fff;
    font-size: 20px;
    font-weight: 300;
    ${Media(mediaPxTablet,[
        Margin(0, 100, 0, 100, mediaPxTablet), 
    ])}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
    ])}
`
const RightArrow = styled.img`
    width: 264px;
    ${Media(mediaPxTablet,[
        Width(264, mediaPxTablet), 
    ])}
    ${Media(mediaPxMb,[
         Width(88, mbSize)
    ])}
`
