//react
import React from 'react';

// style
import styled from "styled-components"

import {
    Media,
    FontSize,
    MB,
    MR
} from '../../components/common/mediaStlye';

const mediaPx = 500;

const ContentTitle = ({text, isSearch, isPadding}) => {

    return(
        <SignupSTitle isSearch={isSearch?true:false} style={{padding:isPadding?"0 32px":"0px"}}>
            <SignupArrow>
                &#8227; 
            </SignupArrow>
            <SignupSTitleText>
                {text}
            </SignupSTitleText>
        </SignupSTitle>
    )
};

export default ContentTitle;

const SignupSTitle = styled.div`
    display: flex;
    align-items: center;
    @media (max-width: ${mediaPx}px) {
        ${({isSearch})=>{
        return isSearch?
        `
        ${MB(7, 428)}
        `
        :
        `
        ${MB(7, 428)}
        `
        }}
    }

    ${({isSearch})=>{
    return isSearch?
    `
    margin-bottom: 10px;
    `
    :
    `
    margin-bottom: 14px;
    `
}}
`
const SignupArrow = styled.span`
    font-size:20px;
    margin-right: 7px;
    ${Media(mediaPx,[
        FontSize(20, 428),
        MR(7, 428),
    ])}
`
const SignupSTitleText = styled.div`
    font-size: 15px;
    ${Media(mediaPx,[
        FontSize(15, 428),
    ])}
`