//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link, useHistory} from "react-router-dom";

// style
import styled from "styled-components"
import Header from '../../../components/header/header';

// components
import BackBtn from '../../../components/common/backBtn';

// img
import BigIcon from '../../../img/taste/mSeulmit/detail/bigIcon.png';
import SpreadIcon from '../../../img/taste/mSeulmit/detail/spreadIcon.png';
import spreadIconAct from '../../../img/taste/mSeulmit/detail/spreadIconAct.png';
import leftArrowImg from '../../../img/taste/mSeulmit/detail/leftArrow.png';
import rightArrowImg from '../../../img/taste/mSeulmit/detail/rightArrow.png';
import mbLeftArrowImg from '../../../img/taste/mSeulmit/detail/mbLeftArrow.png';
import mbRightArrowImg from '../../../img/taste/mSeulmit/detail/mbRightArrow.png';
import galleryImg_1 from '../../../img/taste/mSeulmit/detail/gallery_1.png';
import galleryImg_2 from '../../../img/taste/mSeulmit/detail/gallery_2.png';
import galleryImg_3 from '../../../img/taste/mSeulmit/detail/gallery_3.png';
import galleryImg_4 from '../../../img/taste/mSeulmit/detail/gallery_4.png';
import galleryImg_5 from '../../../img/taste/mSeulmit/detail/gallery_5.png';
import galleryImg_6 from '../../../img/taste/mSeulmit/detail/gallery_6.png';
import VidBackimg from '../../../img/taste/mSeulmit/detail/vidBack.png';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { 
    Navigation, 
    Pagination, 
  } from "swiper";
import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import GalleryBig from '../../../components/taste/GalleryBig';

import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    PL,
    PC,
    MR,
    MB,
    Left,
    Top,
    Mobile,
    Padding2,
    PR,
    LineH
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

SwiperCore.use([Navigation, Pagination]);
const MSeulmitDetailFirst = (props) => {
    const [isSpread, setIsSpread] = useState(false);
    const [imageIndex, setImageIndex] = useState(1);
    const [isBig, setIsBig] = useState(false);
    const iconData = [
        {text:"확대보기", img:BigIcon},
        {text:"펼쳐보기", img:isSpread?spreadIconAct:SpreadIcon},
    ]
    const galleryImgData = [
        galleryImg_1,
        galleryImg_2,
        galleryImg_3,
        galleryImg_4,
        galleryImg_5,
        galleryImg_6,
        galleryImg_1,
        galleryImg_2,
        galleryImg_3,
        galleryImg_4,
        galleryImg_5,
        galleryImg_6,
    ]

    const onClickSpread = () => {
        setIsSpread(!isSpread);
    }

    const onClickArrow = (clickEl) => {
        document.querySelector(clickEl).click();
    }

    const onClickSpreadEl = (index) => {
        const dotEls = document.querySelectorAll(".swiper-pagination-bullet");
        dotEls[index].click();
        setImageIndex(index + 1);
    }


    return(
        <Container>
            {
                isBig&&
                <GalleryBig setIsBig={setIsBig} galleryImgData={galleryImgData}/>
            }
            <Header isMbBack/>
            <PC>
                <BackBtn />
            </PC>
            <GridWrap>
                <SmallTitleWrap> 
                    <SmallTitle>Vogue x seulmit collection vol.1</SmallTitle>
                    <Date>2020.01.01</Date>
                </SmallTitleWrap>
                <Gallery>
                    {/* PC 이미지 갤러리 */}
                    <PC>
                        <ImgSection>
                            {
                                isSpread&&
                                <SpreadWrap>
                                    <SpreadList>
                                        {
                                            galleryImgData.map((item, index) => {
                                                return(
                                                    <SpreadEl key={index} onClick={() => onClickSpreadEl(index)}>
                                                        <SpreadIndex>{index + 1}</SpreadIndex>
                                                        <SpreadImg active={imageIndex==index+1?true:false} src={item}/>
                                                    </SpreadEl>
                                                )
                                            })
                                        }
                                    </SpreadList>
                                </SpreadWrap>
                            }
                            <SliderWrap>
                                <Swiper
                                spaceBetween={43}
                                slidesPerView={1.5}
                                navigation={{ clickable: true }}
                                pagination={{ clickable: true }}
                                onSlideChange={(e) => {setImageIndex(e.activeIndex + 1);}}
                                className={"swiper"}
                                >
                                    {
                                        galleryImgData.map((item, index) => {
                                            return(
                                                <SwiperSlide>
                                                    <SlideImg src={item} alt="갤러리 이미지" />
                                                </SwiperSlide>
                                            )
                                        })
                                    }
                                </Swiper>
                            </SliderWrap>
                        </ImgSection>

                        <ControlSection>
                            {
                                !isSpread&&
                                <ArrowWrap>
                                    <LeftArrow src={leftArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-prev")}/>
                                    <IndexWrap>
                                        {imageIndex} ——— 12
                                    </IndexWrap>
                                    <RightArrow src={rightArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-next")}/>
                                </ArrowWrap>

                            }
                            <ControlWrap>
                                {
                                    iconData.map((item, index) => {
                                        return(
                                            <IconWrap onClick={() => {index?onClickSpread():setIsBig(true)}} key={index}>
                                                <Icon src={item.img} alt="아이콘"/>
                                                <IconText>{item.text}</IconText>
                                            </IconWrap>
                                        )
                                    })
                                }
                            </ControlWrap>
                        </ControlSection>
                    </PC>
                    
                    {/* 모바일 이밎 갤러리 */}
                    <Mobile>
                        <ImgSection>
                            <SliderWrap>
                                <Swiper
                                spaceBetween={15}
                                slidesPerView={1.1}
                                navigation={{ clickable: true }}
                                pagination={{ clickable: true }}
                                onSlideChange={(e) => {setImageIndex(e.activeIndex + 1);}}
                                className={"swiper"}
                                >
                                    {
                                        galleryImgData.map((item, index) => {
                                            return(
                                                <SwiperSlide onClick={() => setIsBig(true)} key={index}>
                                                    <SlideImg src={item} alt="갤러리 이미지" />
                                                </SwiperSlide>
                                            )
                                        })
                                    }
                                </Swiper>
                            </SliderWrap>
                        </ImgSection>

                        <ControlSection>
                            {
                                !isSpread&&
                                <ArrowWrap>
                                    <PC><LeftArrow src={leftArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-prev")}/></PC>
                                    <Mobile><LeftArrow src={mbLeftArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-prev")}/></Mobile>
                                    <IndexWrap>
                                        {imageIndex} ——— 12
                                    </IndexWrap>
                                    <PC><RightArrow src={rightArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-next")}/></PC>
                                    <Mobile><RightArrow src={mbRightArrowImg} alt="방향 아이콘" onClick={() => onClickArrow("div.swiper-button-next")}/></Mobile>
                                </ArrowWrap>

                            }
                        </ControlSection>
                        
                        <SpreadWrap>
                            <SpreadList>
                                {
                                    galleryImgData.map((item, index) => {
                                        return(
                                            <SpreadEl onClick={() => onClickSpreadEl(index)}>
                                                <PC><SpreadIndex>{index + 1}</SpreadIndex></PC>
                                                <SpreadImg active={imageIndex==index+1?true:false} src={item}/>
                                            </SpreadEl>
                                        )
                                    })
                                }
                            </SpreadList>
                        </SpreadWrap>
                    
                    </Mobile>
                </Gallery>

                <TextSection>
                    <TextTitle>Vogue x seulmit collection vol.1</TextTitle>
                    Artist is a broad term that describes occupations in fine arts, <br />
                    crafts, desktop publishing, website design, animation and video game production. …<br />
                    Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                    firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                    crafts, desktop publishing, website design, animation and video game production. …<br />
                    Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                    firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts,<br />
                    crafts, desktop publishing, website design, animation and video game production. …<br />
                    Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing <br />
                </TextSection>

                {/* ** 어떤형식의 비디오인지? */}
                <VideoSection>
                    
                </VideoSection>
            </GridWrap>
        </Container>
    )
};

export default MSeulmitDetailFirst;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 상단 작은 제목
const SmallTitleWrap = styled.div`
    width:calc(100% - 84px);
    border-bottom:1px solid #1f1113;
    padding: 148px 17px 15px;
    margin: 0 auto 15px;
    display:flex;
    justify-content:space-between;

    ${Media(mediaPxTablet,[
        Padding(148, 17, 15, 17, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        Padding(87, 8, 13, 8, mbSize),
        "width:calc(100% - calc(100vw * 36/428))",
        "flex-direction: column-reverse",
        Margin(0, 10, 19, 10, mbSize)
    ])}
`
const SmallTitle = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
    ])} 
`
const Date = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])} 
`

// 갤러리 
const Gallery = styled.div`
    background-color: #F8F7F7;
`
// 펼쳐보기
const SpreadWrap = styled.div`
    padding:0 45px 0 25px;
    ${Media(mediaPxTablet,[
        Padding(0, 45, 0, 25, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Padding(0, 0, 18, 0, mbSize),
    ])}
`
const SpreadList = styled.ul`
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    display: flex;
    flex-wrap: wrap;
    ${Media(mediaPxMb,[
        Padding2(0, 42, mbSize),
        "box-sizing: border-box",
    ])}
`
const SpreadEl = styled.li`
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 14px;
    width: 250px;
    display: flex;
    justify-content: space-between;
    ${Media(mediaPxTablet,[
        MB(14, mediaPxTablet),
        Width(350, 1920),
    ])}
    ${Media(mediaPxMb,[
        "width:calc(100% / 3);",
        "box-sizing: border-box",
        MB(6, mbSize),
        PR(9, mbSize),
    ])}
`
const SpreadIndex = styled.span`
    font-size: 20px;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
    ])}
`
const SpreadImg = styled.img`
    width: 199px;
    height: 134px;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Width(199, mediaPxTablet),
        Height(134, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Width(109, mbSize),
        Height(74, mbSize),
        "box-sizing: border-box",
    ])}
    ${({active}) => {
        return active?
        `border:2px solid #D7464E`
        :
        ``
    }}
`
// 갤러리 - 이미지
const ImgSection = styled.div`
    height: 750px;
    padding: 36px 0 0 43px;
    display: flex;
    ${Padding(36, 0, 0, 43, 1920)}
    ${Media(mediaPxMb,[
        "width:100%",
        Height(256, mbSize),
    ])}
`
const SliderWrap = styled.div`
    width: 100%;
    & > .swiper{
        height: 100%;
    }
`
const SlideImg = styled.img`
    cursor: pointer;
    width:100%;
    height: 100%;
    object-fit: cover;
`

// 갤러리 - 컨트롤
const ControlSection = styled.div`
    height: 87px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    ${Media(mediaPxTablet,[
        Height(87, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Padding(27, 0, 14, 0, mbSize),
    ])}
`
const ArrowWrap = styled.div`
    display: flex;
    align-items: center;
    &>img{
        cursor: pointer;
    }
`
const LeftArrow = styled.img`
    ${Width(264, 1920)}
    ${Media(mediaPxMb,[
        Width(46, mbSize),
    ])}
`
const IndexWrap = styled.div`
    margin: 0 100px;
    font-size: 20px;
    font-weight: 300;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
        Margin(0, 100, 0, 100, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const RightArrow = styled.img`
    ${Width(264, 1920)}
    ${Media(mediaPxMb,[
        Width(46, mbSize),
    ])}
`

const ControlWrap = styled.div`
    position: absolute;
    left: 74px;
    top: 20px;
    display: flex;
    ${Media(mediaPxTablet,[
        Left(74, mediaPxTablet),
        Top(20, mediaPxTablet)
    ])}
`
const IconWrap = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 45px;
    cursor: pointer;
    ${MR(45, 1920)}
`
const Icon = styled.img`
    width: 28px;
    height: 29px;
    margin-bottom: 3px;
    ${Media(mediaPxTablet,[
        Width(28, mediaPxTablet),
        Height(29, mediaPxTablet),
        MB(3, mediaPxTablet),
    ])}
`
const IconText = styled.span`
    font-size: 12px;
    font-weight: 400;
    ${Media(mediaPxTablet,[
        FontSize(12, mediaPxTablet),
    ])}
`
// 텍스트
const TextSection = styled.div`
    margin-bottom: 104px;
    font-size: 15px;
    font-weight: 500;
    padding-left: 60px;
    font-weight: 300;
    line-height: 30px;
    ${Media(mediaPxTablet,[
        FontSize(14, mediaPxTablet),
        MB(104, mediaPxTablet),
        PL(60, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "font-weight: 300",
        FontSize(15, mbSize),
        LineH(30, mbSize),
        MB(66, mbSize),
    ])}
`
const TextTitle = styled.p`
    margin: 82px 0 52px;
    font-size:20px;
    ${Media(mediaPxTablet,[
        Margin(82, 0, 52, 0, mediaPxTablet),
        FontSize(20, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
        Margin(40, 0, 37, 0, mbSize),
    ])}
`
// 비디오
const VideoSection = styled.div`
    margin-bottom: 262px;
    height: 823px;
    background-image:url(${VidBackimg});
    ${Media(mediaPxTablet,[
        MB(262, mediaPxTablet),
        Height(823, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        MB(224, mbSize),
    ])}
`
