import React, { useEffect, useState } from 'react';

import styles from'./myReviewListEl.module.css';

// components
import reviewStarImg from '../../common/reviewStarStatus';
import DateText from '../../common/dateText';
import InsertComma from '../../common/insertComma';
import {
    styled,
    Media,
    Padding,
    Width,
    Height,
    MR,
    Px,
    FontSize,
    MB,
    LineH,
    Top,
    Right,
    MT,
} from '../../common/mediaStlye';
import ReviewModal from '../../common/modal/reviewModal';
import ReviewImgModal from '../../common/modal/reviewImgModal';

// img
import editBtnImg from '../../../img/myPage/editIon.png';

const mediaPxMb = 1024;
const mbSize = 428;

const MyReviewListEl = ({item, setReviewData, index, reviewData}) => {
    const [isReviewModal, setIsReviewModal] = useState(false);
    const [isBigImg, setIsBigImg] = useState(false);
    const [imgIndex, setImgIndex] = useState(0);

    // 모달 수정버튼 클릭
    const onClickSubmit = () => {
        setIsReviewModal(false);
    }

    // 체크 토글
    const onChangeCheck = () => {
        let newArr = reviewData;
        newArr[index].checked = !newArr[index].checked;
        setReviewData([...newArr]);
    }

    // 수정 버튼 클릭
    const onClickEdit = () => {
        setIsReviewModal(true);
    }

    //  리뷰 이미지 클릭 
    const onClickImgEl = (index) => {
        setIsBigImg(true);
        setImgIndex(index);
    }

    return(
        <ListWrap>
            {
                isReviewModal&&
                <ReviewModal data={item} subtitle="나의 상품 사용기 수정" title="Write Review Edit" onClickBack={() => setIsReviewModal(false)} onClickSubmit={onClickSubmit}/>
            }
            {
                isBigImg&&
                <ReviewImgModal
                array={item.reviewImg}
                imageIndex={imgIndex}
                setImageIndex={setImgIndex}
                onClick={() => setIsBigImg(false)}
                />
            }

            <PrdWrap>
                <PrdImg src={item.prdImg}/>
                <PrdText>
                    <PrdType>{item.prdType}</PrdType>
                    <PrdNamePrice>
                        <PrdName>{item.prdNmae}</PrdName>
                        <PrdPrice>{InsertComma(item.prdPrice)} won</PrdPrice>
                    </PrdNamePrice>
                </PrdText> 
            </PrdWrap>
            
            <ReviewWrap>
                <>
                    <input className={styles.inputReview} checked={item.checked} onChange={() => onChangeCheck()} id={item.id} type="checkbox"/>
                    <label className={styles.labelReview} for={item.id}></label>
                </>
                {
                    item.reviewImg.length==0?
                    <NameDate>
                        <Name>{item.userName}</Name>
                        <Date>{DateText(item.date, ".")}</Date>
                        <EditBtn src={editBtnImg} onClick={() => onClickEdit()}/>
                    </NameDate>
                    :
                    <NameDateImg>
                        <NameWrap>
                            <Date style={{margin:0}}>{DateText(item.date, ".")}</Date>
                            <Name>{item.userName}</Name>
                        </NameWrap>
                        <ImgWrap>
                            {
                                item.reviewImg.map((value, index) => {
                                    return(
                                        <ImgEl onClick={() => onClickImgEl(index)} src={value} key={index}/>
                                    )
                                })
                            }
                        </ImgWrap>
                        <EditBtn active={item.reviewImg.length!==0?true:false} src={editBtnImg} onClick={() => onClickEdit()}/>
                    </NameDateImg>
                }

                <StarContent>
                    <Star src={reviewStarImg(item.star)}/>
                    <Content>{item.review}</Content>
                </StarContent>



            </ReviewWrap>
        </ListWrap>
    )
};

export default MyReviewListEl;

const ListWrap = styled.div`
    border-bottom: 1px solid #E2E2E2;
`
const PrdWrap = styled.div`
    background-color: #F8F7F7;
    display: flex;
    box-sizing: border-box;
    padding: 12px 109px 14px 111px;
    ${Media(mediaPxMb,[
        Padding(12, 37, 14, 37, mbSize),
    ])}
`
const PrdImg = styled.img`
    width: 77px;
    height: 95px;
    margin-right: 24px;
    ${Media(mediaPxMb,[
        Width(77, mbSize),
        Height(95, mbSize),
        MR(27, mbSize),
    ])}
`
const PrdText = styled.div`
    width: calc(100% - 77px);
    ${Media(mediaPxMb,[
        `width:calc(100%-${Px(77, mbSize)})`
    ])}
`
const PrdType = styled.div`
    color:#8B8B8B;
    font-size: 15px;
    margin-top: 8px;
    ${Media(mediaPxMb,[
        MT(8, mbSize),
        FontSize(15, mbSize),
    ])}
`
const PrdNamePrice = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
`
const PrdName = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const PrdPrice = styled.div`
    color:#8B8B8B;
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const ReviewWrap = styled.div`
    box-sizing: border-box;
    padding: 20px 60px 32px 60px;
    position: relative;
    ${Media(mediaPxMb,[
        Padding(39, 0, 27, 32, mbSize),
    ])}
`
const NameDate = styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    position: relative;
    ${Media(mediaPxMb,[
        MB(13, mbSize)
    ])}
`
const Name = styled.div`
    font-size: 14px;
    font-weight: 300;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
    ])}
`
const Date = styled.div`
    font-size: 14px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
        MR(50, mbSize),
    ])}
`
const NameDateImg = styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    position: relative;
    ${Media(mediaPxMb,[
        MB(16, mbSize),
    ])}
`
const NameWrap = styled.div`
`
const ImgWrap = styled.div`
    display: flex;
`
const ImgEl = styled.img`
    width: 49px;
    height: 49px;
    margin-right: 5px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(40, mbSize),
        Height(40, mbSize),
        MR(4, mbSize), 
    ])}
`
const EditBtn = styled.img`
    width: 22px;
    position: absolute;
    right: -30px;
    cursor: pointer;

    
    ${Media(mediaPxMb,[
        Width(23, mbSize),
        Right(0, mbSize)
    ])}

    @media (max-width: ${mediaPxMb}px) {
        ${({active}) => {
        return active?
            `
            ${Top(-30, mbSize)}
            `
            :
            ``
        }}
    }
`
const StarContent = styled.div`
    display: flex;
    align-items: center;
`
const Star = styled.img`
    width: 40px;
    margin-right: 14px;
    ${Media(mediaPxMb,[
        Width(40, mbSize),
        MR(14, mbSize),
    ])}
`
const Content = styled.div`
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
        LineH(20, mbSize),
    ])}
`