//react
import React ,{useState, useEffect} from 'react';
import {useHistory} from "react-router-dom";

// style
import styled from "styled-components"


import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    LineH,
    MT,
    Padding2,
    Px
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const GalleryItem = ({img, title, desc, isDouble, historyLink}) => {
    
    const history = useHistory();

    return(
        <GalleryItemWrap isDouble={isDouble} >
            <GImg src={img} alt="아티스트 이미지" onClick={() => history.push(historyLink)}/>
            <GTitle isDouble={isDouble}>{title}</GTitle>
            <GDesc isDouble={isDouble}>
                {/* {desc} */}
                고요함과 활기참, <br />
                휴식과 성장, <br />
                에너지의 응축과 발산을 metaphor 합니다. <br />

            </GDesc>
        </GalleryItemWrap>
    )
};

export default GalleryItem;

const GalleryItemWrap = styled.div`
    margin-bottom:41px;
    @media (max-width: ${mediaPxTablet}px) {
        ${({isDouble})=>{
        return isDouble?
            Width(698, mediaPxTablet)
        :
            Width(344, mediaPxTablet)
        }}
    }
    ${({isDouble})=>{
        return isDouble?
        `width:698px;`
        :
        `width:344px;`
    }}

    @media (max-width: ${mediaPxMb}px) {
        ${({isDouble})=>{
        return isDouble?
        `
            width:100%;
            ${MT(56, mbSize)}
        `
        :
        `
            width:100%;
            ${MT(56, mbSize)};
            ${Padding2(0, 42, mbSize)};
            box-sizing: border-box;
        `
        }}
    }

`
const GImg = styled.img`
    width:100%;
    cursor: pointer;
`
const GTitle = styled.div`
    margin:19px 0 14px;
    font-weight:500;
    font-size:20px;
    cursor: default;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet),
        Margin(19, 0, 14, 0, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isDouble})=>{
        return isDouble?
        `
            ${Padding2(0, 42, mbSize)};
            box-sizing: border-box;
        `:``
        }}
    }
`
const GDesc = styled.p`
    font-size:15px;
    line-height:30px;
    cursor: default;
    font-weight: 300;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        LineH(30, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "font-weight: 500",
        FontSize(15, mbSize),
        LineH(30, mbSize),
    ])}
    @media (max-width: ${mediaPxMb}px) {
        ${({isDouble})=>{
        return isDouble?
        `
            ${Padding2(0, 42, mbSize)};
            box-sizing: border-box;
        `:``
        }}
    }
`