//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link} from "react-router-dom";

// style
import styled from "styled-components";
import styles from "./main.module.css";

// components
import Header from '../../components/header/header';

// img
import IntroBackImg from '../../img/main/00_intro/introBack.png';
import LogoImg from '../../img/common/logo.png';
import MbSolImg_ from '../../img/main/mb/sol.png';
import MbVisualImg_ from '../../img/main/mb/visual.png';
import ProductImg from '../../img/main/mb/productImg.png';
import galleryImg_1 from '../../img/main/mb/gallery/gallery_1.png';
import galleryImg_2 from '../../img/main/mb/gallery/gallery_2.png';
import galleryImg_3 from '../../img/main/mb/gallery/gallery_3.png';
import galleryImg_4 from '../../img/main/mb/gallery/gallery_4.png';
import galleryImg_5 from '../../img/main/mb/gallery/gallery_5.png';
import footerImg from '../../img/main/mb/footer.png';
import fbIconImg from '../../img/main/05_footer/fbIcon.png';
import instaIconImg from '../../img/main/05_footer/instaIcon.png';
import twIconImg from '../../img/main/05_footer/twIcon.png';
import youIconImg from '../../img/main/05_footer/youIcon.png';


import ServiceModal from '../../components/common/modal/serviceModal';
import { FontSize, Height, LineH, Margin, MB, Mobile, MT, Padding, Padding2, PC, PL, PT, Width, Margin2, ML} from '../../components/common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const Main = (props) => {
    const gridRef = useRef();
    const [scrollHeight, setScrollHeight] = useState(2000);
    const [isCondition, setIsCondition] = useState(false);
    const [isPrivacy, setIsPrivacy] = useState(false);
    const [isMap, setIsMap] = useState(false);
    const shortGalleryData = [galleryImg_1, galleryImg_2, galleryImg_3];
    const footerListData = ["Skincare", "Body", "Makeup", "Accessories", "Tools", "-", "Pre- reservation", "Slow order"]
    const footerSocialData = [fbIconImg, instaIconImg, twIconImg, youIconImg]
    const setHeight = () => {
        setScrollHeight((gridRef.current.offsetWidth - window.innerWidth) + (1015 - (937 - window.innerHeight)) );
    }

    // 초기 height 값 조정
    useEffect(() => {
        if(!gridRef.current){return}
        setHeight();
    }, [])

    // resize 시 height 값 조정
    window.addEventListener("resize", () => {
        if(!gridRef.current){return}
        setHeight();
    }, [])

    // 스크롤 이벤트
    window.addEventListener("scroll", (e) => {
        if(!gridRef.current){return}
        let currentScroll = window.scrollY;
        gridRef.current.style.left=`${-1 * currentScroll}px`;
    })


    return(
        <Container>
            <Header/>
            {/* PC */}
            <PC>
                {/* 이용약관 */}
                {isCondition&&<ServiceModal stasus={0} onClickBack={() => setIsCondition(false)}/>}
                {/* 개인정보호법 */}
                {isPrivacy&&<ServiceModal stasus={1} onClickBack={() => setIsPrivacy(false)}/>}
                {/* 사이트맵 */}
                {isMap&&<ServiceModal stasus={2} onClickBack={() => setIsMap(false)}/>}
                


                <GridWrap ref={gridRef}>
                    <Contents>
                        {/* 인트로 */}
                        <Start>
                            <StartText>
                                As creators of design spaces, we <br />
                                want to inspire our customers and <br />
                                assist them in finding their comfort <br />
                                and personal style. We believe that <br />
                                furniture is made for self-expression. <br />
                                <StartDate>
                                    Seulmit. 2021 -
                                </StartDate>
                            </StartText>
                            <StartBack src={IntroBackImg} alt="배경 이미지"/>
                        </Start>

                        {/* About */}
                        <About>
                        </About>

                        {/* Episode */}
                        <Episode>
                            
                        </Episode> 

                        {/* Product */}
                        <Product>

                        </Product>
                        
                        {/* Gallery */}
                        <Gallery>

                        </Gallery>
                        
                        {/* Footer */}
                        <Footer>

                        </Footer>
                    </Contents>
                </GridWrap>

                {/* 스크롤을 위한 컨텐츠입니다. */}
                <ForScroll scrollHeight={scrollHeight}></ForScroll>    
            </PC>

            <Mobile>
                <MobileGrid>
                    <MbStart>
                        <MbStartText>
                            As creators of design spaces, we want to inspire our <br />
                            customers and assist them in finding their comfort and <br />
                            personal style.We believe that furniture is made for self- <br />
                            expression. <br />
                            <MbStartTextSub>Seulmit. 2021 -</MbStartTextSub>
                        </MbStartText>
                        <MbStartImg src={IntroBackImg} alt="배경 이미지"/>
                    </MbStart>

                    <MbAbout>
                        <MbSolWrap>
                            <MbSol>Sol</MbSol>
                            <MbSoul>Soul</MbSoul>
                            <MbSeoul>Seoul</MbSeoul>
                            <MbSolImg src={MbSolImg_} alt="솔나무 이미지"/>
                        </MbSolWrap>

                        <MbGirlText>
                            한국 여성을 표현할 수 있는 단어는 많습니다.  <br />
                            맑고, 순수한, 꾸밈없는, 섬세한, 지혜로운, 우아한 …. <br />
                            <br />
                            그리고 이런 단어들로도 그들을 표현 할 수 있습니다.  <br />
                            강인한, 단단한, 멋진, 독립적인, 책임감 있는, 지적인 …. <br />
                            <br />
                            우리는 여성의 겉으로 드러나는 모습보다 <br />
                            내면으로부터 배어 나오는 아우라가 진정한 아름다움이라고 생각합니다.  <br />
                        </MbGirlText>
                    </MbAbout>

                    <MbVisualWrap>
                        <MbLogoWrap>
                            <MbLogo src={LogoImg} alt="로고"/>
                        </MbLogoWrap>
                        <MbVisualImg src={MbVisualImg_} alt="궁 이미지"/>

                        <MbVisualTitleWrap>
                            <MbVisualTitle>Sol, Soul, Seoul</MbVisualTitle>
                            <MbVisualContent>
                                소나무 향으로 공간을채우고 명상을 갖습니다.  <br />
                                눈을 떠 얼굴에 비춰진 빛과 그림자를 마주할때,  <br />
                                외면하지 않고 손을 뻗습니다.  <br />
                                우리를 어지럽히는 것들의 껍질을 벗겨내고 오롯이  <br />
                                나를 감각합니다.  <br />
                                <br />
                                태양을 향에 뻗어가는 소나무는 우리를 닮았습니다.  <br />
                                강인하게 나아가고 때론 섬세하게 몸을 틀어 나를  <br />
                                성장시킵니다.   <br />
                                ‘슬밋’ 의 과거로부터 계승된 리추얼은 일상에 스며들어  <br />
                                내면의 힘을 만들어냅니다.  <br />
                            </MbVisualContent>
                        </MbVisualTitleWrap>
                    </MbVisualWrap>

                    <MbProductWrap>
                        <MbProductImg src={ProductImg} alt="상품이미지"/>
                        <MbProductTextWrap>
                            <MbNamePrice>
                                <MbName>Sol</MbName>
                                <MbPrice>158,000 won</MbPrice>
                            </MbNamePrice>
                            <MbDesc>
                                고요함과 활기참,  <br />
                                휴식과 성장,  <br />
                                에너지의 응축과 발산을 metaphor 합니다.  <br />
                            </MbDesc>
                            <MbLineWrap><MbCrtLine></MbCrtLine></MbLineWrap>
                        </MbProductTextWrap>
                    </MbProductWrap>

                    <MbGalleryWrap>
                        {
                            shortGalleryData.map((item, index) => {
                                return(
                                    <MbGalleryItem bgImg={item} key={index}>
                                        {
                                            index !== 2&&
                                            <MbGalleryText>
                                            고요함과 활기참,  <br />
                                            휴식과 성장,  <br />
                                            에너지의 응축과 발산을 metaphor 합니다.  <br />
                                            </MbGalleryText>
                                        }
                                    </MbGalleryItem>
                                )
                            })
                        }
                        <MbGalleryItem></MbGalleryItem>
                        
                        <MbGalleryShortWrap>
                            <MbGalleryItemShort></MbGalleryItemShort>
                            <MbGalleryItemShort bgImg={galleryImg_3}></MbGalleryItemShort>
                            <MbGalleryItemShort></MbGalleryItemShort>
                            <MbGalleryItemShort bgImg={galleryImg_4}></MbGalleryItemShort>
                        </MbGalleryShortWrap>

                        <MbGalleryItemLong bgImg={galleryImg_5}></MbGalleryItemLong>
                    </MbGalleryWrap>

                    <MbFooter>
                        <MbFooterList>
                            {
                                footerListData.map((item, index) => {
                                    return(
                                        <MbFooterlistEl style={{opacity:index==5&&0}} key={index}>{item}</MbFooterlistEl>
                                    )
                                })
                            }
                        </MbFooterList>

                        <MbFooterAboutWrap>
                            <MbSiteListWrap>
                                <MbSiteListEl>이용약관</MbSiteListEl>
                                <MbSiteListEl>개인정보처리방침</MbSiteListEl>
                                <MbSiteListEl>고객센터</MbSiteListEl>
                                <MbSiteListEl>사이트맵</MbSiteListEl>
                            </MbSiteListWrap>
                            <MbAboutFirstWrap>
                                <MbAboutText>상호 : (주)슬밋</MbAboutText>                               
                                <MbAboutText>대표 : 홍길동</MbAboutText>                               
                                <MbAboutText>사업자등록번호 : 123-45-6789</MbAboutText>                               
                            </MbAboutFirstWrap>
                            <MbAboutSecondWrap>
                                <MbAboutText>주소 : 서울특별시 서초구 서초대로 441,5층 (서초동 GT타워) (우:06615)</MbAboutText>                               
                            </MbAboutSecondWrap>
                            <MbAboutText>팩스 : 02-123-4567</MbAboutText>                               
                        </MbFooterAboutWrap>

                        <MbSocialWrap>
                            {
                                footerSocialData.map((item, index) => {
                                    return(
                                        <MbSocialEl src={item} key={index}/>
                                    )
                                })
                            }
                        </MbSocialWrap>

                        <MbFooterReserved>ⓒ.Seulmit All Rights Reserved</MbFooterReserved>
                    </MbFooter>

                </MobileGrid>

            </Mobile>
        </Container>
    )
};

export default Main;

const Container = styled.div`
    &::-webkit-scrollbar {
        display: none;
    }
`

// ---------------------- PC

const ForScroll = styled.div`
    ${({scrollHeight})=>{
    return (`height:${scrollHeight}px`)
    }}

`
const GridWrap = styled.div`
    z-index:1;
    position: fixed;
    width:8772px;
    margin: 0 0 0 76px;
    height: 100vh;
`
const Contents = styled.div`
    position: relative;
    height: 100%;
`
//  ------  Start
const Start = styled.div`
    width: 992px;
    height: 100%;
    position: relative;
`
const StartText = styled.div`
    font-size: 18px;
    color:#707070;
    line-height: 30px;
    padding: 57px 0 0 78px;
`
const StartDate = styled.div`
    margin-top: 14px;
    font-size: 18px;
    color:#bfbfbf;
    font-weight: 500;
`
const StartBack = styled.img`
    position: absolute;
    width:650px;
    height: 100%;
    right: 0;
    top: 0;
`
//  ------  About
const About = styled.div`
`
//  ------  Episode
const Episode = styled.div`
`
//  ------  Product
const Product = styled.div`
`
//  ------  Gallery
const Gallery = styled.div`
`
//  ------  Footer
const Footer = styled.div`
`

// ---------------------- Mobile
const MobileGrid = styled.div`
    ${PT(74, mbSize)}
`
const MbStart = styled.div`
`
const MbStartText = styled.p`
    ${FontSize(14, mbSize)}
    color:#707070;
    box-sizing: border-box;
    ${Padding(28, 34, 43, 34, mbSize)}
`
const MbStartTextSub = styled.span`
    ${FontSize(14, mbSize)}
    color:#BFBFBF;
`
const MbStartImg = styled.img`
    ${Width(274, mbSize)}
    ${Height(456, mbSize)}
    ${Padding2(0, 76, mbSize)}
`
const MbAbout = styled.div`
    ${MT(152, mbSize)}
`
const MbSolWrap = styled.div`
    position: relative;
    box-sizing: border-box;
    ${PL(29, mbSize)};
    ${Height(723, mbSize)}
    display: flex;
    flex-direction: column;
    justify-content: center ;
`
const MbSol = styled.div`
    ${FontSize(138, mbSize)}
    opacity:0.5;
    font-weight:500;
`
const MbSoul = styled.div`
    ${FontSize(138, mbSize)}
    filter: blur(18px);
    font-weight:500;
    box-sizing: border-box;
    ${PL(103, mbSize)}
`
const MbSeoul = styled.div`
    ${FontSize(138, mbSize)}
    opacity:0.5;
    font-weight:500;
`
const MbSolImg = styled.img`
    position: absolute;
    top: 0;
    right: 0;
    ${Width(244, mbSize)}
    ${Height(723, mbSize)}
    z-index: -1;
`
const MbGirlText = styled.p`
    ${PT(119, mbSize)}
    ${MB(84, mbSize)}
    ${FontSize(12, mbSize)}
    font-weight: 500;
    color:#707070;
    text-align: center;
`
const MbVisualWrap = styled.div`
`
const MbLogoWrap = styled.div`
    text-align: center;
`
const MbLogo = styled.img`
    ${Width(280, mbSize)}
    ${MB(16, mbSize)}
`
const MbVisualImg = styled.img`
    width: 100%;
`
const MbVisualTitleWrap = styled.div`
    box-sizing: border-box;
    ${PL(17, mbSize)}
`
const MbVisualTitle = styled.div`
    ${FontSize(27, mbSize)}
    ${Margin(51, 0, 17, 0, mbSize)}
    font-weight: 500;
`
const MbVisualContent = styled.div`
    ${FontSize(14, mbSize)}
    font-weight: 300;
`
const MbProductWrap = styled.div`
    ${Padding2(0, 21, mbSize)};
    ${MT(68, mbSize)};
`
const MbProductImg = styled.img`
    ${Width(385, mbSize)}
`
const MbProductTextWrap = styled.div`
`
const MbNamePrice = styled.div`
    display: flex;
    justify-content: space-between;
    ${Padding(27, 0, 19, 0, mbSize)}
    border-bottom:1px solid #000;
    ${MB(37, mbSize)}
`
const MbName = styled.div`
    ${FontSize(20, mbSize)}
    font-weight: 500; 
`
const MbPrice = styled.div`
    ${FontSize(20, mbSize)}
    color: #8B8B8B;
    font-weight: 500;
`
const MbDesc = styled.div`
    ${FontSize(15, mbSize)}
    ${MB(51, mbSize)}
    font-weight: 300;
`
const MbLineWrap = styled.div`
    ${Width(263, mbSize)}
    height: 3px;
    background-color: #E8E2DB;
    position: relative;
    margin: 0 auto;
    ${MB(54, mbSize)}
`
const MbCrtLine = styled.div`
    ${Width(87, mbSize)}
    position: absolute;
    height: 3px;
    background-color: #000;
    display: inline-block;
`
const MbGalleryWrap = styled.ul`
`
const MbGalleryItem = styled.li`
    width: 100%;
    ${Height(463, mbSize)}
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    ${({bgImg}) => {
        return bgImg?`background-image:url(${bgImg})`:`background-color:#000; `
    }}
`
const MbGalleryText = styled.div`
    ${FontSize(14, mbSize)}
    text-align: center;
    ${LineH(28, mbSize)}
    color:#fff;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.6);
    ${Padding(26, 0, 22, 0, mbSize)}
    font-weight: 300;
`
const MbGalleryShortWrap = styled.ul`
    display: flex;
    flex-wrap: wrap;
`
const MbGalleryItemShort = styled.li`
    object-fit: contain;
    width: 50%;
    ${Height(311, mbSize)}
    ${({bgImg}) => {
        return bgImg?`background-image:url(${bgImg})`:`background-color:#000; `
    }}
`
const MbGalleryItemLong = styled.li`
    width: 100%;
    ${Height(621, mbSize)}
    ${({bgImg}) => {
        return bgImg?`background-image:url(${bgImg})`:`background-color:#000; `
    }}
`
const MbFooter = styled.div`
    width: 100%;
    ${Height(926, mbSize)}
    background-image: url(${footerImg});
    & *{
        color:#fff;
    }
`
const MbFooterList = styled.ul`
    box-sizing: border-box;
    ${Padding(85, 0, 54, 68, mbSize)}
`
const MbFooterlistEl = styled.li`
    font-family: "Big Caslon";
    ${FontSize(21, mbSize)}
    font-weight: 500;
    ${MB(43, mbSize)}
`
const MbFooterAboutWrap = styled.div`
    box-sizing: border-box;
    ${Padding(0, 20, 0, 30, mbSize)}
`
const MbSiteListWrap = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    ${MB(34, mbSize)}
`
const MbSiteListEl = styled.div`
    ${FontSize(13, mbSize)}
    font-weight: 400;
`
const MbAboutFirstWrap = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
`
const MbAboutSecondWrap = styled.div`
    ${Margin2(8, 0, mbSize)}
`
const MbAboutText = styled.div`
    ${FontSize(12, mbSize)}
    font-weight: 300;
`
const MbSocialWrap=styled.div`
     text-align: center;
     ${Margin(32, 0, 21, 0, mbSize)}
`
const MbSocialEl=styled.img`
    ${Width(28, mbSize)}
    ${ML(19, mbSize)}
    &:first-child{
        margin: 0;
    }
`
const MbFooterReserved=styled.div`
    text-align: center;
    ${FontSize(10, mbSize)}
`