//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link, useHistory} from "react-router-dom";

// style
import styled from "styled-components"
import Header from '../../../components/header/header';


// img
import headImg from '../../../img/collection/episode/headImg.png';
import episodeImg_1 from '../../../img/collection/episode/episodeImg_1.png';
import episodeImg_2 from '../../../img/collection/episode/episodeImg_2.png';
import episodeImg_3 from '../../../img/collection/episode/episodeImg_3.png';
import episodeImg_4 from '../../../img/collection/episode/episodeImg_4.png';
import episodeImg_5 from '../../../img/collection/episode/episodeImg_5.png';
import episodeImg_6 from '../../../img/collection/episode/episodeImg_6.png';
import GallertEpEl from '../../../components/collection/gallertEpEl';

import {
    Media,
    Width,
    FontSize,
    MB,
    Padding,
    Height,
    Margin2,
    PC,
    Mobile,
    PT,
    MR,
    PL,
    PB,
    LineH,
    MT
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const Episode = (props) => {
    
    const [currentNav, setCurrentNav] = useState(0);
    const navData = ["All", "Episode1", "Episode2", "Episode3"]
    const galleryDataEp3 = [
        {img: episodeImg_1, title:"Sol, Soul, Seoul", navTitle:"Episode_3"},
        {img: episodeImg_2, title:"Sol, Soul, Seoul", navTitle:"Episode_3"},
    ]
    const galleryDataEp2 = [
        {img: episodeImg_3, title:"Cotton collection", subTitle:"2020 S/S", navTitle:"Episode_2"},
        {img: episodeImg_4, title:"Cotton collection", subTitle:"2020 S/S", navTitle:"Episode_2"},
        {img: episodeImg_5, title:"Cotton collection", subTitle:"2020 S/S", navTitle:"Episode_2"},
        {img: episodeImg_6, title:"Cotton collection", subTitle:"2020 S/S", navTitle:"Episode_2"},
    ]

    const onClickNav = (index) => {
        setCurrentNav(index)
    }

    return(
        <Container>
            <Header />
            <GridWrap>
                <PC>
                    <TopSection>
                        <TextWrap>
                            <HeadTitle>Main copy text place here</HeadTitle>
                            <Line></Line>
                            <HeadText>
                            [슬밋 SEULMIT]은 이미 알고 있지만 표현하기 힘든 자신들의 아름다움을 어떻게 고객들에게 알릴 수 있을<br />
                            까 고민했습니다. 그 결과 효능뿐 아니라 문화적 상징으로서 의미와 이야기를 품고 있는 한국의 헤리티지 원<br />
                            료에 주목했습니다. 슬밋의 에피소드는 이와같은 한국의 원료를 이용하여 제품을 개발하고 문화컨텐츠를 소<br />
                            개하는 슬밋만의 컨텐츠입니다.<br />
                            </HeadText>
                        </TextWrap>
                    </TopSection>
                </PC>

                <ListWrap>
                    <MbPosFixed>
                        <ListTitle>Episodes</ListTitle>
                        <Mobile>
                            <EpisodeNavList>
                                {
                                    navData.map((item, index) => {
                                        return(
                                            <EpisodeNavEl
                                            active={currentNav == index}
                                            onClick={() => onClickNav(index)}
                                            key={index}
                                            >{item}</EpisodeNavEl>
                                        )
                                    })
                                }
                            </EpisodeNavList>
                        </Mobile>
                    </MbPosFixed>
                    <GalleryWrap>
                        <GallertEpEl title={"Episode_3"} itemArr={galleryDataEp3}/>
                        <GallertEpEl title={"Episode_2"} itemArr={galleryDataEp2}/>
                    </GalleryWrap>
                </ListWrap>
            </GridWrap>
        </Container>
    )
};

export default Episode;


const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 상단
const TopSection = styled.div`
    width: 100%;
    height: 565px;
    background-image: url(${headImg});
    display: flex;
    align-items: center;
    justify-content: flex-end;
    ${Media(mediaPxTablet,[
        Height(565, mediaPxTablet),
    ])}
`
const TextWrap = styled.div`
    width: 1110px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 60px;
    backdrop-filter: invert(0.9);
` 
const HeadTitle = styled.h1`
    font-family: "Big Caslon";
    font-size: 25px;
    font-weight: 500;
    color:#fff;
    ${Media(mediaPxTablet,[
        FontSize(25, mediaPxTablet)
    ])}
` 
const Line = styled.span`
    margin: 24px 0;
    display: inline-block;
    width: 1px;
    height: 68px;
    border-left: 1px solid #fff;
    ${Media(mediaPxTablet,[
        Height(68, mediaPxTablet),
        Margin2(24, 0, mediaPxTablet),
        Height(68, mediaPxTablet),
    ])}
`
const HeadText = styled.p`
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    color:#fff;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        LineH(30, mediaPxTablet),
    ])}
`

// 컨텐츠
const ListWrap = styled.div`
    width:1052px;
    margin:0 auto;
    margin-bottom: 219px;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
        MB(219, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
        PT(74, mbSize),
    ])}
`
const MbPosFixed = styled.div`
    ${Media(mediaPxMb,[
        "width: 100%",
        "position: fixed",
        "z-index: 1",
        "background-color: #fff",
    ])}
`
const GalleryWrap = styled.div`
    ${Media(mediaPxMb,[
        MT(105, mbSize),
    ])}
`
const ListTitle = styled.h2`
    padding:127px 0 13px;
    font-size: 35px;
    font-weight: 500;
    font-family: "Big Caslon";
    ${Media(mediaPxTablet,[
        FontSize(35, mediaPxTablet),
        Padding(127, 0, 13, 0, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(22, mbSize),
        Padding(26, 0, 10, 19, mbSize),       
    ])}
`
const EpisodeNavList = styled.ul`
    display: flex;
    ${PL(19, mbSize)}
    ${PB(23, mbSize)}
`
const EpisodeNavEl = styled.li`
    font-weight: 500;
    font-family: "Big Caslon";
    cursor: pointer;
    transition: 300ms;
    ${FontSize(15, mbSize)}
    ${MR(18, mbSize)}
    ${({active}) => {
        return active&&`color:#E83548;`
    }}
`
 