//react
import React from 'react';

// style
import styled from "styled-components"


// img
import TasteMainImg_1 from '../../img/taste/tasteMain_1.png';
import TasteMainImg_2 from '../../img/taste/tasteMain_2.png';
import TasteMainImg_3 from '../../img/taste/tasteMain_3.png';
import TasteMainImg_4 from '../../img/taste/tasteMain_4.png';
import TasteMainImg_5 from '../../img/taste/tasteMain_5.png';

// components
import Header from '../../components/header/header';
import GalleryItem from '../../components/taste/GalleryItem';

import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    PT
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const Taste = (props) => {

    const galleryData = [
        {
            img:TasteMainImg_1,
            title:"Vogue x seulmit collection vol.1",
            desc:"고요함과 활기참, 휴식과 성장, 에너지의 응축과 발산을 metaphor 합니다.",
            isDouble:false,
            historyLink:"/TasteDetailSecond"
        },
        {
            img:TasteMainImg_2,
            title:"iconics",
            desc:"고요함과 활기참, 휴식과 성장, 에너지의 응축과 발산을 metaphor 합니다.",
            isDouble:false,
            historyLink:"/TasteDetailFirst"
        },
        {
            img:TasteMainImg_3,
            title:"All about calligraphy",
            desc:"고요함과 활기참, 휴식과 성장, 에너지의 응축과 발산을 metaphor 합니다.",
            isDouble:false,
            historyLink:"/TasteDetailFirst"
        },
        {
            img:TasteMainImg_4,
            title:"Vogue x seulmit collection vol.1",
            desc:"고요함과 활기참, 휴식과 성장, 에너지의 응축과 발산을 metaphor 합니다.",
            isDouble:true,
            historyLink:"/TasteDetailSecond"
        },
        {
            img:TasteMainImg_5,
            title:"",
            desc:"",
            isDouble:false,
            historyLink:"/TasteDetailFirst"
        },
    ]

    return(
        <Container>
            <Header />
            <GridWrap>
                <Grid>
                    <Title>Artists</Title>
                    <GalleryWrap>
                        {
                            galleryData.map((item, index) => {
                                return(
                                    <GalleryItem
                                    img={item.img}
                                    title={item.title}
                                    desc={item.desc}
                                    isDouble={item.isDouble}
                                    historyLink={item.historyLink}
                                    />
                                )
                            })
                        }
                    </GalleryWrap>
                    
                    <BtnWrap>
                        <MoreBtn>더보기</MoreBtn>
                    </BtnWrap> 
                </Grid>
            </GridWrap>
        </Container>
    )
};

export default Taste; 

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`

const Grid = styled.div`
    width:1052px;
    margin:0 auto;
    ${Media(mediaPxTablet,[
        Width(1052, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
    ])}
`

const Title = styled.p`
    font-size:35px;
    padding:71px 0 112px;
    font-family: "Big Caslon";
    font-weight: 500;
    ${Media(mediaPxTablet,[
        Padding(71, 0, 112, 0, mediaPxTablet),
        FontSize(35, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(22, mbSize),
        Padding(26, 0, 0, 18, mbSize),
    ])}
`

const GalleryWrap = styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
    ${Media(mediaPxMb,[
        "flex-direction: column",
    ])}
`
const BtnWrap = styled.div`
    text-align: center;
`
const MoreBtn = styled.button`
    font-size:15px;
    border:1px solid #000;
    outline: none;
    width:385px;
    height: 66px;
    margin: 147px 0 301px;
    background-color: #fff;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        Width(385, mediaPxTablet),
        Height(66, mediaPxTablet),
        Margin(147, 0, 301, 0, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        Width(385, mbSize),
        Height(66, mbSize),
        Margin(132, 0, 115, 0, mbSize),
    ])}

`
