//react
import React ,{useState, useEffect, useRef} from 'react';
import { useHistory} from "react-router-dom";

// style
import styled from "styled-components"

// img
import LogoImg from '../../img/login/logo.png';
import style_1 from '../../img/login/style_1.png';
import style_2 from '../../img/login/style_2.png';
import GoogleIconImg from '../../img/login/googleIcon.png';
import NaverIconImg from '../../img/login/naverIcon.png';
import KakaoIconImg from '../../img/login/kakaoIcon.png';
import SeulmitIconImg from '../../img/login/seulmitIcon.png';
import BackBtn from '../../img/login/backBtn.png';

// components
import LoginInput from '../../components/login/loginInput';
import CommonSocialBtn from '../../components/login/commonSocialBtn';
import Checkbox from '../../components/login/checkbox';
import CommonBtn from '../../components/login/commonBtn';
import ContentTitle from '../../components/login/contentTitle';
import CheckBox from '../../components/common/checkbox';
import {
    Media,
    Width,
    Height,
    FontSize,
    Margin,
    Padding,
    Padding2,
    MB,
    ML,
} from '../../components/common/mediaStlye';
import ServiceModal from '../../components/common/modal/serviceModal';

const mediaPx = 500;

const Login = (props) => {
    const history = useHistory();
    const barRef = useRef();
    const [isLogin, setIsLogin] = useState(true);
    const [status, setStatus] = useState("login");
    const [socialData, setSocialData] = useState([
        {title:"Google", img:GoogleIconImg},
        {title:"Naver", img:NaverIconImg},
        {title:"Kakaotalk", img:KakaoIconImg},
    ]);
    const [ckBoxData, setCkBoxData] = useState([
        {text:"개인정보 취급 방침에 동의합니다.", margin:"0 0 15px", isContent:true},
        {text:"이용약관에 동의합니다.", margin:"0 0 15px", isContent:true},
        {text:"마케팅 및 프로모션 소식을 수신하고 싶습니다. (선택)", margin:"0 0 29px"},
    ]);
    const [loginInput, setLoginInput] = useState([
        {after:"ID", type:"text", isError:false, errorText:""},
        {after:"Password", type:"password", isError:false, errorText:"ID 와 비밀번호가 일치하지 않습니다."},
    ])
    const [signupInput, setSignupInput] = useState([
        {after:"ID", type:"text", isError:false, width:290, errorText:"이미등록된 아이디가 있습니다."},
        {after:"Password", type:"password", isError:false, errorText:"비밀번호가 일치하지 않습니다."},
        {after:"Password confirmation", type:"password", isError:false, errorText:"비밀번호가 일치하지 않습니다."},
        {after:"Username", type:"text", isError:false},
    ])
    const [searchIdInput, setSearchIdInput] = useState({
        after:"Phone number",
        type:"number",
        isError:false,
        errorText:"입력하신 정보로 가입하신 회원정보가 없습니다.",
    })
    const [searchPwInput, setSearchPwInput] = useState([
        {after:"ID", type:"text", isError:false, errorText:"입력하신 정보로 가입하신 회원정보가 없습니다."},
        {after:"Phone number", type:"text", isError:false, errorText:"입력하신 정보로 가입하신 회원정보가 없습니다."},
    ])
    const [ckBoxStatus, setCkBoxStatus] = useState([0, 0, 0, 0]);
    const [answerId, setAnswerId] = useState("");
    const [isAnswer, setIsAnswer] = useState(false);
    const [isSignupBtn, setIsSignupBtn] = useState(true);
    const [isPwText, setIsPwText] = useState(false);
    const [isCondition, setIsCondition] = useState(false);
    const [isPrivacy, setIsPrivacy] = useState(false);
    const [isIdConfirm, setIsIdConfirm] = useState(false);
    const loginRef = useRef();
    const signupRef = useRef();
    const signupIdRef = useRef();
    const searchIdRef = useRef();
    const searchPwRef = useRef();

    // 회원가입, 로그인 탭 클릭
    const onClickType = (bool) => {
        if(bool){
            setStatus("login")
            setIsLogin(true);
            barRef.current.style.left="50%";
        }else{
            setStatus("signup");
            setIsLogin(false);
            barRef.current.style.left=0;
        }
    }

    // 에러 초기화
    const initError = (state, setState) => {
        let newArr = state;
        for(let i = 0; i < newArr.length ; i++){
            newArr[i].isError = false;
        }
        setState([...newArr]);
    }
    
    // 로그인 클릭
    const onClickLogin = () => {
        // 틀리면 실행
        commonActiveErr(loginRef, loginInput, setLoginInput);
    }

    // 회원가입 체크박스 클릭
    const onClickCkBox = (index) => {
        let newArr = ckBoxStatus;
        // 전체동의
        if(index == 0){
            if(newArr[0]){
                newArr = [0, 0, 0, 0];
            }else{
                newArr = [1, 1, 1, 1];
            }
        }
        // 개별버튼 
        if(ckBoxStatus[index]){
            newArr[index] = 0;
            newArr[0] = 0;
        }else{
            newArr[index] = 1;
        }
        // 개별버튼 모두 체크
        if(newArr[1]&&newArr[2]&&newArr[3]){
            newArr[0] = 1;
        }
        setCkBoxStatus([...newArr]);
    }

    // 아이디 찾기
    const  onClickSearchId = () => {
        if(!searchIdRef.current.value){
            alert("아이디를 입력해주세요.");
            return;
        }

        setAnswerId("ahnnabanana");
        setIsAnswer(true);
        // 틀리면 실행
        let newObj = searchIdInput;
        newObj.isError = true;
        setSearchIdInput(newObj);
    }

    // 로그인, 비밀번호찾기 공통 에러
    const commonActiveErr = (ref, state, setState) => {
        initError(state, setState);
        const inputEls = ref.current.children;
        let newArr = state;
        for(let i = 0 ; i < newArr.length ; i++){
            if(!inputEls[i].children[1].value){newArr[i].isError=true;}
        }
        setState([...newArr]);
    }

    // 비밀번호 찾기 클릭
    const onClickSearchPw = () => {
        commonActiveErr(searchPwRef, searchPwInput, setSearchPwInput);
        // 맞으면 실행
        // setIsPwText(true);
    }

    // 회원가입 버튼 클릭
    const onClickSignup = () => {
        initError(signupInput, setSignupInput);
        // 중복확인 여부 체크
        if(!isIdConfirm){alert("아이디를 중복확인해주세요")}
        // 빈칸 여부 체크
        const inputEls = signupRef.current.children;
        let newArr = signupInput;
        for(let i = 1 ; i < newArr.length ; i++){
            if(!inputEls[i-1]){continue};
            if(!inputEls[i-1].children[1].value){newArr[i].isError=true;}
        }
        // 비밀번호, 비밀번호확인 체크
        if(inputEls[0].children[1].value !== inputEls[1].children[1].value){
            newArr[2].isError=true;
        }
        setSignupInput([...newArr]);

        // 필수 체크항목 체크 여부 확인 
        if(!ckBoxStatus[1] || !ckBoxStatus[2]){
            alert("이용약관 및 개인정보 취급방침에 동의해주세요")
        }
    }

    // 회원가입 중복확인 버튼 클릭
    const onClickIdConFirm = () => {
        // 빈칸 여부 체크
        let newArr = signupInput;
        newArr[0].isError = false;
        if(!signupIdRef.current.value){
            alert("아이디를 입력해주세요.");
            return;
        }

        // 중복된 아이디 일때
        if(false){
            newArr[0].isError = true;
        }
        setIsIdConfirm(true);
        setSignupInput([...newArr]);
    }

    // UI 초기화
    useEffect(() => {
        // 아이디
        let newObj = searchIdInput;
        newObj.isError = false;
        setSearchIdInput(newObj);
        setIsAnswer(false);
        // 회원가입
        let newArr = signupInput;
        newArr[0].isError = false;
        newArr[1].isError = false;
        newArr[2].isError = false;
        setSignupInput([...newArr])
        // 비밀번호
        let newArrPw = searchPwInput;
        searchPwInput[0].isError = false;
        searchPwInput[1].isError = false;
        setSearchPwInput([...newArrPw]);
        // 로그인
        let newArrLogin = loginInput;
        newArrLogin[0].isError=false;
        newArrLogin[1].isError=false;
        setLoginInput([...newArrLogin]);
    }, [status])
    

    // 컨텐츠 내용
    const currentContent = () => {
        // 로그인
        if(status == "login"){
            return(
                <LoginContent>
                    <Logo src={LogoImg} alt="로고"/>
                    <div ref={loginRef}>
                        {
                            loginInput.map((item, index) => {
                                return(
                                        <LoginInput
                                        key={index}
                                        after={item.after}
                                        type={item.type}
                                        isError={item.isError}
                                        errorText={item.errorText}
                                        />
                                )
                            })
                        }
                    </div>
                    <CommonBtn text={"LOGIN"} onClick={() => onClickLogin()} margin={"28px 0 0"}/>
                    <SearchIdPw><Searchspan onClick={() => setStatus("searchId")}> 아이디 </Searchspan>/ <Searchspan onClick={() => setStatus("searchPw")}>비밀번호</Searchspan> 찾기</SearchIdPw>
                    <Or>or</Or>
                    {
                        socialData.map((item, index) => {
                            return(
                                <CommonSocialBtn key={index} item={item} backText={"Login"}/>
                            )
                        })
                    }
                </LoginContent>
            )
        } // 회원가입
        else if(status == "signup"){
            return(
                <SignupContent>
                    {
                        socialData.map((item, index) => {
                            return(
                                <CommonSocialBtn key={index} item={item} backText={"Sign up"}/>
                            )
                        })
                    }
                    <SignupBar />
                    <CommonSocialBtn
                        item={{title:"Seulmit", img:SeulmitIconImg}}
                        backText={"Sign up"}
                        onClick={ () => setStatus("signupSeulmit")}
                    />
                </SignupContent>
            )
        } // 슬밋 회원가입
        else if(status == "signupSeulmit"){
            return(
                <SignupSeulmitContent>
                    <ContentTitle text={"Seulmit Sign up"} isPadding/>
                    <SignupIdWrap>
                        <LoginInput useRef={signupIdRef} after={signupInput[0].after} width={signupInput[0].width} isError={signupInput[0].isError} errorText={signupInput[0].errorText}/>
                        <CheckId onClick={() => onClickIdConFirm()}>
                            중복확인
                        </CheckId>
                    </SignupIdWrap>
                    <div ref={signupRef}>
                        {
                            signupInput.map((item, index) => {
                                if(index == 0){return;}
                                return(
                                    <LoginInput
                                    isPadding
                                    key={index}
                                    after={item.after}
                                    type={item.type}
                                    isError={item.isError}
                                    errorText={item.errorText}
                                    />
                                )
                            })
                        }
                    </div>
                    
                    <CheckBoxEl isFirst={true}>
                        <CheckBox onChange={() => onClickCkBox(0)} checked={ckBoxStatus[0]} id={"allCheck"} red/>
                        <CheckText>전체동의</CheckText>
                    </CheckBoxEl>
                    
                    <SignupCkWrap>
                        {
                            ckBoxData.map((item, index) => {
                                return(
                                    <CheckBoxEl key={index}>
                                        <CheckBox onChange={() => onClickCkBox(index+1)} checked={ckBoxStatus[index+1]} id={index}/>
                                        <CheckText>{item.text} {item.isContent&&<Imp onClick={() => {index==0?setIsPrivacy(true):setIsCondition(true)}}>전체내용 보기 (필수)</Imp>} </CheckText>
                                    </CheckBoxEl>
                                )
                            })
                        }
                        <SeulSignupBtn>
                            <CommonBtn text={"SIGN UP"} isWhite={isSignupBtn} onClick={() => onClickSignup()} />
                        </SeulSignupBtn>
                    </SignupCkWrap>
                </SignupSeulmitContent>
            )
        } // 아이디 찾기
        else if(status == "searchId"){
            return(
                <SearchIdContent isAnswer={isAnswer}>
                    <ContentTitle text={"Find ID / Passwords"} isSearch/>
                    <LoginInput
                        useRef={searchIdRef}
                        after={searchIdInput.after}
                        type={searchIdInput.type}
                        isError={searchIdInput.isError}
                        errorText={searchIdInput.errorText}
                    />
                    {
                        isAnswer?
                        <AnswerText>
                            입력하신정보로 가입하신 아이디는 <br />
                            {answerId} 입니다.
                        </AnswerText>
                        :null
                    }
                    <CommonBtn text={"아이디 찾기"} margin={"21px 0 24px"} onClick={() => onClickSearchId()}/>
                    <SearchIdBottom>
                        <span onClick={() => setStatus("searchPw")}>비밀번호 찾기</span> / <span onClick={() => onClickType(false)}>회원가입</span>
                    </SearchIdBottom>
                </SearchIdContent>
            )
        } // 비밀번호 찾기
        else if(status == "searchPw"){
            return(
                <SearchPwContent>
                    <ContentTitle text={"Find ID / Passwords"} isSearch/>
                    <div ref={searchPwRef}>
                        {
                            searchPwInput.map((item, index) => {
                                return(
                                    <LoginInput
                                    key={index}
                                    after={item.after}
                                    type={item.type}
                                    isError={item.isError}
                                    errorText={item.errorText}
                                />
                                )   
                            })
                        }
                    </div>
                    {
                        isPwText&&
                        <PwText>입력하신 이메일로 비밀번호가 전송되었습니다.</PwText>
                    }
                    <CommonBtn text={"비밀번호 찾기"} onClick={() => onClickSearchPw()} margin={"27px 0 24px"}/>
                    <SearchIdBottom>
                        <span onClick={() => setStatus("searchId")}>아이디 찾기</span> / <span onClick={() => onClickType(false)}>회원가입</span>
                    </SearchIdBottom>
                </SearchPwContent>
            )
        }
    }
    

    return(
        <Container>
            {/* 이용약관 */}
            {isCondition&&<ServiceModal stasus={0} onClickBack={() => setIsCondition(false)}/>}
            {/* 개인정보호법 */}
            {isPrivacy&&<ServiceModal stasus={1} onClickBack={() => setIsPrivacy(false)}/>}

            <Backbtn onClick={() => history.goBack()} src={BackBtn} alt="뒤로가기"/>
            <LoginWrap>
                {/* 회원가입, 로그인 타입 */}
                <TypeBtnWrap>
                    <SignupBtn onClick={() => onClickType(false)} style={{color:!isLogin?"#000":"#8B8B8B"}}>
                        Sign up
                        </SignupBtn>
                    <LoginBtn onClick={() => onClickType(true)} style={{color:isLogin?"#000":"#8B8B8B"}}>
                        Login
                    </LoginBtn>
                    <TypeBar ref={barRef}></TypeBar>
                </TypeBtnWrap>

                <ContentWrap>
                    {currentContent()}
                </ContentWrap>
            </LoginWrap>
        </Container>
    )
};

export default Login;

const Container = styled.div`
    width:100vw;
    height:100vh;
    background-image: url(${style_1});
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    display:flex;
    align-items:center;
    justify-content:center;
`

const Backbtn = styled.img`
    width: 84px;
    height:44px;
    top:20px;
    right:19px;
    position:absolute;
    cursor: pointer;
`
const LoginWrap = styled.div`
    width:458px;
    
    ${Media(mediaPx, [
        Width(404, 428),
    ])}
`
const TypeBtnWrap = styled.div`
    display:flex;
    align-items:center;
    justify-content: space-around;
    padding-bottom:10px;
    position: relative;
    & > button{
        font-size:17px;
        background:transparent;
        border:0;
        outline:none;
        transition:400ms;
        ${Media(mediaPx, [
            FontSize(19, 428),
        ])}
    }
`
const SignupBtn = styled.button`
`
const LoginBtn = styled.button`
`

// 컨텐트
const ContentWrap = styled.div`
    background-color: #fff;
    border-bottom: 5px solid #000;
    position: relative;
    &>div{
        display:flex;
        flex-direction:column;
    }
`

// -----  로그인 컨텐츠
const LoginContent = styled.div`
    padding:18px 32px 22px;
    ${Media(mediaPx, [
        Padding(18, 10, 22, 10, 428)
    ])}
`
const TypeBar = styled.span`
    width:50%;
    height:5px;
    background-color:#000;
    position:absolute;
    bottom:0;
    left:50%;
    transition:400ms;
`
const Logo = styled.img`
    width:91px;
    height:22px;
    margin-bottom:7px;
    ${Media(mediaPx, [
        Width(90, 428),
        Height(21, 428),
        Margin(0, 0, 7, 0, 428)
    ])}
`

const SearchIdPw = styled.span`
    font-size:11px;
    text-align:right;
    margin:6px 0 13px;
    ${Media(mediaPx,[
        FontSize(11, 428),
        Margin(6, 0, 13, 0, 428),
    ])}
`
const Searchspan = styled.span`
    cursor: pointer;
    font-size: 11px;
    &:hover{
        border-bottom:1px solid #000;
    }
    ${Media(mediaPx,[
        FontSize(11, 428),
    ])}
`
const Or = styled.div`
    text-align:center;
    font-size:14px;
    margin-bottom:9px;
    ${Media(mediaPx,[
        FontSize(14, 428),
        MB(9, 428)
    ])}
`

// -----  회원가입 컨텐츠
const SignupContent = styled.div`
    padding:35px 32px 32px;
    ${Media(mediaPx,[
        Padding(35, 10, 32, 10, 428),
    ])}
`
const SignupBar = styled.span`
    width: 100%;
    border-bottom: 1px solid #000;
    margin: 33px 0 36px;
    ${Media(mediaPx,[
        Margin(33, 0, 36, 0, 428),
    ])}
`

// -----  슬밋 회원가입
const SignupSeulmitContent = styled.div`
    /* padding:22px 32px 52px; */
    padding:22px 0 52px;
    ${Media(mediaPx,[
        Padding(22, 0, 39, 0, 428),
    ])}
`
const SignupIdWrap = styled.div`
    padding: 0 32px;
    display:flex;
    justify-content: space-between;
    
    ${Media(mediaPx,[
        Padding2(0, 10, 428),
    ])}

`
const CheckId = styled.button`
    font-size: 11px;
    color:#fff;
    width: 100px;
    background-color: #000;
    border:none;
    outline: none;
    height: 65px;
    padding: 0;
    margin-left: 5px;
    ${Media(mediaPx,[
        FontSize(12, 428),
        Width(100, 428),
        Height(79, 428),
        ML(5, 428),
    ])}
`
const SignupCkWrap = styled.div`
    display: flex;
    flex-direction: column;
    padding: 0 32px;
    ${Media(mediaPx,[
        Padding2(0, 0, 428),
    ])}
`
const CheckBoxEl = styled.div`
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    ${Media(mediaPx,[
        MB(9, 428),
    ])}
    @media (max-width: ${mediaPx}px) {
        ${({isFirst})=>{
        return isFirst?
        `
            ${Margin(9, 0, 8, 0, 428)}
            ${Height(44, 428)}
            ${Padding(0, 0, 0, 32, 428)}
            background-color: #FBFBFB;
        `
        :
        `
            ${Padding(0, 0, 0, 32, 428)}
        `
        }}
    }
    ${({isFirst}) => {
        return isFirst?
        `
        margin:12px 0;
        height: 44px;
        background-color: #FBFBFB;
        padding: 0 32px;
        `
        :
        ``
    }}
`
const CheckText = styled.span`
    font-size: 15px;
    font-family: "HelveticaNeue";
    font-weight: 400;
    margin-left: 13px;
    ${Media(mediaPx,[
        FontSize(13, 428),
        ML(13, 428),
    ])}
`
const Imp = styled.span`
    font-size: 15px;
    font-family: "HelveticaNeue";
    font-weight: 400;
    cursor: pointer;
    ${Media(mediaPx,[
        FontSize(13, 428),
    ])}
`
const SeulSignupBtn = styled.div`
    ${Media(mediaPx,[
        Padding(15, 10, 0, 10, 428),
    ])}
`

// ----- 아이디 찾기
const SearchIdContent = styled.div`
    ${({isAnswer})=>{
        return isAnswer?
        `
        padding:56px 32px 45px;
        `
        :
        `
        padding:56px 32px 117px;
        `
    }}
    @media (max-width: ${mediaPx}px) {
        ${({isAnswer})=>{
            return isAnswer?
            `
            ${Padding(81, 10, 75, 10, 428)}
            `
            :
            `
            ${Padding(81, 10, 41, 10, 428)}
            `
        }}
    }
`
const SearchIdBottom = styled.div`
    color:#211013;
    font-size: 14px;
    text-align: center;
    &>span{
        cursor: pointer;
        font-weight: 400;
    }
    ${Media(mediaPx,[
        FontSize(14, 428),
    ])}
`
const AnswerText = styled.p`
    text-align: center;
    margin: 20px 0 0;
    font-size: 14px;
    line-height: 30px;
    font-weight: 400;
`


// ----- 비밀번호 찾기
const SearchPwContent = styled.div`
    padding:56px 32px 45px;
    ${Media(mediaPx,[
        Padding(37, 10, 49, 10, 428)
    ])}
`
const PwText = styled.div`
    color:#E83548;
    font-size: 13px;
    font-weight: 400;
    text-align: center;
    margin-top: 20px;
`