//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import closeIconImg from '../../../img/common/closeIcon.png';

import {
    Media,
    Width,
    FontSize,
    Padding,
    MT,
    Height,
    PL,
    Top,
    Right,
    LineH,
    MB,
    Px,
} from '../../common/mediaStlye.js';

const mediaPxMb = 1024;
const mbSize = 428;

const PolicyAlert = ({onClickClose}) => {

    return(
        <Container>
            <ReviewModalWrap>
                <CloseBtn onClick={() => onClickClose()} src={closeIconImg} alt="닫기 아이콘"/>
                <Title>교환 / 환불 규정</Title>
                <Content>
                • 단순변심 or 고객님의 착오로 인한 경우, <br />
                왕복 배송비는 고객님께서 부담하시게 됩니다.<br />
                ( 단, 자사측의 착오임이 확인 될 경우 <br />
                자사가 부담합니다. )<br />

                ​<br /><br />
                • 화면 해상도에 따라 실제 제품 색상과 <br />
                약간의 차이가 있을 수 있으며,<br />
                이는 교환/환불 사유에 해당되지 않습니다.<br />

                ​
                <br /><br />
                • 고객님의 부주의로 인한 상품 훼손 시, <br />
                교환/환불이 불가합니다.<br />
                </Content>
            </ReviewModalWrap>
        </Container>
    )   
};

export default PolicyAlert;

const Container = styled.div`
    position: absolute;
    z-index: 99;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
`
const ReviewModalWrap = styled.div`
    background: #fff;
    width: 519px;
    height: 349px;
    padding: 35px 40px 45px 35px;
    box-sizing: border-box;
    border:1px solid #000;
    position: relative;
    ${Media(mediaPxMb,[
        `max-height: calc(100vh - ${Px(200, mbSize)})`,
        "overflow: scroll",
        Width(390, mbSize),
        Height(349, mbSize),
        Padding(35, 40, 45, 35, mbSize),
    ])}
`
const CloseBtn = styled.img`
    width: 22px;
    position: absolute;
    top:27px;
    right: 25px;
    cursor: pointer;
    ${Media(mediaPxMb,[ 
        Width(21, mbSize),
        Top(24, mbSize),
        Right(24, mbSize),
    ])}
`
const Title = styled.div`
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 11px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        MB(11, mbSize)
    ])}
`
const Content = styled.div`
    font-size: 15px;
    font-weight: 500;
    line-height: 30px;
    height: 234px;
    overflow: scroll;
    ${Media(mediaPxMb,[ 
        FontSize(15, mbSize),
        LineH(30, mbSize),
        Height(234, mbSize),
    ])}
`