//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import closeIconImg from '../../../img/common/closeIcon.png';

import {
    Media,
    Width,
    FontSize,
    Padding,
    MT,
    Height,
    PL,
    Top,
    Right,
} from '../../common/mediaStlye.js';

const mediaPxMb = 1024;
const mbSize = 428;

const AlertModal = ({title, onClickClose}) => {

    return(
        <Container>
            <ReviewModalWrap>
                <CloseBtn onClick={() => onClickClose()} src={closeIconImg} alt="닫기 아이콘"/>
                {title}
            </ReviewModalWrap>
        </Container>
    )   
};

export default AlertModal;

const Container = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.47);
    z-index: 99;
    top:0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
`
const ReviewModalWrap = styled.div`
    background: #fff;
    width: 453px;
    height: 285px;
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid #000;
    position: relative;
    font-size: 15px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        Width(390, mbSize),
        Height(349, mbSize),
        FontSize(15, mbSize)
    ])}
`
const CloseBtn = styled.img`
    width: 22px;
    position: absolute;
    top:27px;
    right: 25px;
    cursor: pointer;
    ${Media(mediaPxMb,[ 
        Width(21, mbSize),
        Top(24, mbSize),
        Right(24, mbSize),
    ])}
`
