import React from 'react';


// style
import styled from "styled-components"

// img
import FillArrow from '../../img/common/fillArrow.png';
import EmptyArrow from '../../img/common/dropArrow.png';

// component
import InsertComma from '../common/insertComma';

import {
    Media,
    FontSize,
    MB,
    MT,
} from '../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const BillElTitle = ({title, noArrow, price, onClick, isDetail}) => {
    

    return(
        <BillTitleWrap>
            <BillTitle>{title}</BillTitle>
            <BillPriceWrap onClick={onClick&&onClick}>
                <BillPrice noArrow={noArrow?true:false}>{InsertComma(price)} won</BillPrice>
                {
                    !noArrow&&
                    <BillArrow isDetail={isDetail} src={isDetail?FillArrow:EmptyArrow} alt="방향 아이콘"/>
                }
            </BillPriceWrap>
        </BillTitleWrap>
    )
};

export default BillElTitle;

const BillTitleWrap = styled.div`
    margin-bottom: 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    ${Media(mediaPxMb,[
        MB(7, mbSize),
        MT(12, mbSize),
    ])}
` 
const BillTitle = styled.div`
    font-size: 15px;
    font-weight: 300;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
`
const BillPriceWrap = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
`
const BillPrice = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
    ${({noArrow}) => {
        return noArrow?
        `margin-right: 36px;`
        :
        `margin-right: 25px;`
    }}
`
const BillArrow = styled.img`
    width: 11px;
    height: 8px;
    ${Media(mediaPxMb,[

    ])}
    ${({isDetail}) => {
        return isDetail?
        `transform: rotate(180deg);`
        :
        ``
    }}
`