//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components";
import Header from '../../../components/header/header';
import ReviewItem from '../../../components/info/review/reviewItem';

// img 
import SearchIconImg from '../../../img/taste/mSeulmit/searchIcon.png';
import selectImg from '../../../img/common/selectImg.png';
import prdImg from '../../../img/collection/products/productImg_1.png';
import reviewImg_1 from '../../../img/collection/products/productDetail/reviewImg_1.png';
import reviewImg_2 from '../../../img/collection/products/productDetail/reviewImg_2.png';
import arrowImg from '../../../img/common/fillArrow.png';

import {
    Mobile,PC,
    Media,
    Width,
    FontSize,
    PB,
    ML,
    PT,
    Margin,
    Height,
    PL,
    MR,
    PR,
    LineH,
    Right,
    Px,
    MT,
    MB
} from '../../../components/common/mediaStlye';
import MbReviewItem from '../../../components/info/review/MbReviewItem';
import ReviewImgModal from '../../../components/common/modal/reviewImgModal';

const shortText = "제품력이 일단 너무 맘에들고, 제품을 산다기보다는 브렌드의아이덴티티와 스토리를 같이 구매하여 제 삶또한 슬밋스러워짐을 느껴 너무 만족합니다. 건성라인 또 만들어주세요!!ㅎㅎ";
const longText = "제품력이 일단 너무 맘에들고, 제품을 산다기보다는 브렌드의아이덴티티와 스토리를 같이 구매하여 제 삶또한 슬밋스러워짐을 느껴 너무 만족합니다. 건성라인 또 만들어주세요!!ㅎㅎ제품력이 일단 너무 맘에들고, 제품을 산다기보다는 브렌드의아이덴티티와 스토리를 같이 구매하여 제 삶또한 슬밋스러워짐을 느껴 너무 만족합니다. 건성라인 또 만들어주세요!!ㅎㅎ제품력이 일단 너무 맘에들고, 제품을 산다기보다는 브렌드의아이덴티티와 스토리를 같이 구매하여 제 삶또한 슬밋스러워짐을 느껴 너무 만족합니다. 건성라인 또 만들어주세요!!ㅎㅎ";

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const Review = (props) => {
    const [searchValue, setSearchValue] = useState("");
    const [currentIndex, setCurrentIndex] = useState(0);
    const navData = ["All", "Skincare", "Body", "Fragrance", "Tools", "Accessories"];
    const [listData, setListData] = useState([{prdImg:"", title:"", name:"", star:3, date:new Date(), reviewImg:[], content:""}])
    const [isFilter, setIsFilter] = useState(false);
    const [currentFilter, setCurrentFilter] = useState(0);
    const filterData = ["최신순", "인기순", "신상품순"];
    const [selectImgArr, setSelectImgArr] = useState([]);
    const [imageIndex, setImageIndex] = useState(0);
    const [isImgModal, setIsImgModal] = useState(false);
    useEffect(() => {
        setListData([
            {prdImg:prdImg, title:"Sol", name:"Nickname", star:3, date:new Date(), reviewImg:[reviewImg_1, reviewImg_2, reviewImg_2, reviewImg_2, reviewImg_2, reviewImg_2], content:shortText},
            {prdImg:prdImg, title:"Sol", name:"Nickname", star:2, date:new Date(), reviewImg:[], content:longText},
            {prdImg:prdImg, title:"Sol", name:"Nickname", star:2, date:new Date(), reviewImg:[], content:longText},
            {prdImg:prdImg, title:"Sol", name:"Nickname", star:2, date:new Date(), reviewImg:[], content:longText},
        ])
    }, [])
    
    const onClickSearch = () => {
        console.log("onClickSearch");
    }

    const onChangeValue = (e) => {
        setSearchValue(e.target.value)
    }

    const onClickFilter = (index) => {
        setCurrentFilter(index);
        setIsFilter(false);
    }

    return(
        <Container>
            <Header isSearch/>
            {isImgModal&&<ReviewImgModal onClick={() => setIsImgModal(false)} array={selectImgArr} imageIndex={imageIndex} setImageIndex={setImageIndex}/>}

            <GridWrap>
                <Grid>
                    <PC>
                        <Title>Review</Title>
                        <SearchInputWrap>
                            <SearchInput type="text" placeholder="Search" value={searchValue} onChange={(e) => onChangeValue(e)}/>
                            <SearchIcon onClick={() => onClickSearch()} src={SearchIconImg} alt="검색 아이콘"/>
                        </SearchInputWrap>
                    </PC>
                    
                    <MbPosFixed>

                        <NavFilter>
                            <NavList>
                                {
                                    navData.map((item, index) => {
                                        return(
                                            <NavEl
                                            active={currentIndex==index?true:false}
                                            onClick={() => setCurrentIndex(index)}
                                            key={index}>
                                                {item}
                                            </NavEl>
                                        )
                                    })
                                }
                            </NavList>

                            <FilterWrap>
                                <FilterHead onClick={() => setIsFilter(!isFilter)}>{filterData[currentFilter]}<ArrowImg active={isFilter?true:false} src={arrowImg}/></FilterHead>
                                {
                                    isFilter&&
                                    <FilterList>
                                        {
                                            filterData.map((item, index) => {
                                            return(
                                                <FilterItem onClick={() => onClickFilter(index)} key={index}>{item}</FilterItem>
                                            )   
                                            })
                                        }
                                    </FilterList>
                                }
                            </FilterWrap>
                        </NavFilter>
                    </MbPosFixed>
                    <PC>
                        <ReviewList>
                            {
                                listData.map((item, index) => {
                                    return(
                                        <ReviewItem setSelectImgArr={setSelectImgArr} setImageIndex={setImageIndex} setIsImgModal={setIsImgModal} item={item} key={index}/>
                                    )
                                })
                            }
                        </ReviewList>
                    </PC>
                    <Mobile>
                        <ReviewList>
                            {
                                listData.map((item, index) => {
                                    return(
                                        <MbReviewItem setSelectImgArr={setSelectImgArr} setImageIndex={setImageIndex} setIsImgModal={setIsImgModal} item={item} key={index}/>
                                    )
                                })
                            }
                        </ReviewList>
                    </Mobile>
                </Grid>
            </GridWrap>
        </Container> 
    )
};

export default Review;

const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100vw - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
const Grid = styled.div`
    width:1149px;
    margin:0 auto 200px;
    ${Media(mediaPxTablet,[
        Width(1149, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
        MB(125, mbSize),
    ])}
`
const Title = styled.p`
    font-size:35px;
    padding-top: 71px;
    font-family: "Big Caslon";
    font-weight: 500;
    margin-left: 60px;
    ${Media(mediaPxTablet,[
        FontSize(35, mediaPxTablet),
        PT(71, mediaPxTablet),
        ML(60, mediaPxTablet),
    ])}
`
const MbPosFixed = styled.div`
    ${Media(mediaPxMb,[
        "width:100%",
        "position: fixed",
        "background-color: #fff",
        "z-index:2"
    ])}
`
const SearchInputWrap = styled.div`
    margin: 17px 0 33px 60px;
    position: relative;
    width: 362px;
    height: 46px;
    ${Media(mediaPxTablet,[
        Margin(17, 0, 33, 60, mediaPxTablet),
        Width(362, mediaPxTablet),
        Height(46, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        Margin(1, 25, 21, 25, mbSize),
        Width(378, mbSize),
        Height(46, mbSize),
    ])}
`
const SearchInput = styled.input`
    width: 100%;
    height: 100%;
    border:1px solid #000;
    padding-left: 30px;
    ${Media(mediaPxTablet,[
        PL(30, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        `width: calc(100% - ${Px(30, mbSize)});`,
        PL(30, mbSize),
    ])}
`
const SearchIcon = styled.img`
    width: 13px;
    height: 13px;
    position: absolute;
    top:50%;
    right:22px;
    transform:translateY(-50%);
    cursor: pointer;
    ${Media(mediaPxMb,[
        Right(22, mbSize),
    ])}
`

const NavFilter = styled.div`
    width: 100%;
    display: flex;
    padding-bottom: 24px;
    border-bottom: 1px solid #000;
    justify-content: space-between;
    
    ${Media(mediaPxTablet,[
        PB(24, mediaPxTablet)
    ])}

    ${Media(mediaPxMb,[
        "align-items: flex-end",
        "flex-direction: column",
        `width:calc(100% - ${Px(46, mbSize)})`,
        PB(23, mbSize),
        Margin(21, 23, 23, 23, mbSize),
    ])}
`
const NavList = styled.ul`
    display: flex;
    padding: 0 0 0 60px;
    
    ${Media(mediaPxTablet,[
        PL(60, mediaPxTablet)
    ])}

    ${Media(mediaPxMb,[
        "padding:0;",
        `width:100%`,
        "justify-content: space-around"
    ])}
`
const NavEl = styled.li`
    font-size: 18px;
    cursor: pointer;
    margin-right: 40px;
    position: relative;
    ${Media(mediaPxTablet,[
        MR(40, mediaPxTablet),
        FontSize(18, mediaPxTablet),
    ])}
    
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
        "margin:0"
    ])}

    ${({active}) => {
        return active?
        `
            &:after{
                position: absolute;
                content: "";
                width: 25px;
                height: 31px;
                opacity:0.9;
                background-image:url(${selectImg});
                filter:blur(1px);
                left: 50%; 
                transform:translateX(-50%);
            }
        `
        :
        ``
    }}
    @media (max-width: ${mediaPxMb}px) {
        ${({active}) => {
        return active?
        `
            &:after{
                position: absolute;
                content: "";
                width: ${Px(14, mbSize)};
                height: ${Px(21, mbSize)};
                opacity:0.9;
                background-image:url(${selectImg});
                filter:blur(1px);
                left: 50%; 
                transform:translateX(-50%);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
        `
        :
        ``
        }}
    }
`
const FilterWrap = styled.div`
    padding-right: 60px;
    position: relative;
    ${Media(mediaPxTablet,[
        PR(60, mediaPxTablet),
    ])}
`
const FilterHead = styled.div`
    text-align: center;
    width: 125px;
    height: 39px;
    line-height: 39px;
    font-size: 15px;
    position: relative;
    cursor: pointer;
    font-weight: 400;
    ${Media(mediaPxTablet,[
        Width(125, mediaPxTablet),
        Height(39, mediaPxTablet),
        LineH(39, mediaPxTablet),
        FontSize(15, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
        Width(60, mbSize),
        Height(20, mbSize),
        MT(36, mbSize),
        LineH(20, mbSize),
        "font-weight: 500"
    ])}
`
const ArrowImg = styled.img`
    right:10px;
    width: 10px;
    height: 8px;
    top:50%;
    position: absolute;
    transition: 300ms;
    ${Media(mediaPxTablet,[
        Width(10, mediaPxTablet),
        Height(9, mediaPxTablet),
    ])}
    
    ${Media(mediaPxMb,[
        Right(-10, mbSize),
        Width(10, mbSize),
        Height(8, mbSize),
    ])}

    ${({active}) => {
        return active?
        `transform:translateY(-50%) rotate(180deg);`
        :
        `transform:translateY(-50%);`
    }}
`
const FilterList = styled.ul`
    position: absolute;
    border:1px solid #000;
    background-color: #fff;
    cursor: pointer;
`
const FilterItem = styled.li`
    text-align: center;
    width: 125px;
    height: 39px;
    line-height: 39px;
    font-size: 15px;
    background-color: white;
    font-weight: 400;
    ${Media(mediaPxTablet,[
        Width(125, mediaPxTablet),
        Height(39, mediaPxTablet),
        LineH(39, mediaPxTablet),
        FontSize(15, mediaPxTablet),
    ])}

    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
        Width(60, mbSize),
        Height(25, mbSize),
        LineH(25, mbSize),
        "font-weight: 500",
    ])}
`
const ReviewList = styled.div`
    ${Media(mediaPxMb,[
        MT(191, mbSize),
    ])}
`

 