import React from 'react';

// components
import {
    styled,
    Media,
    Padding,
    FontSize,
    Width,
    Height,
} from '../../components/common/mediaStlye';
    
// img
import ArrowIconImg from '../../img/common/arrowIron.png';
    
const mediaPxMb = 1024;
const mbSize = 428;

const List = ({setContentStatus}) => {

    const listData = [
        {title:"나의활동", status:1},
        {title:"쇼핑정보", status:2},
        {title:"혜택관리", status:3},
        {title:"고객센터", status:4},
        {title:"계정정보", status:5},
        {title:"로그아웃", status:6, isLogout:true},
    ];

    const onClickList = (index) => {
        if(index == 3 || index==4 || index == 6){return;}
        setContentStatus(index);
    }

    return(
        <ListWrap>
            {
                listData.map((item, index) => {
                    return(
                        <ListEl key={index}>
                            <ListText isLogout={item.isLogout?true:false} onClick={() => onClickList(index+1)}>{item.title}</ListText>
                            {
                                !item.isLogout&&
                                <ListArrow onClick={() => onClickList(index+1)} src={ArrowIconImg} alt="방향아이콘"/>
                            }
                        </ListEl>
                    )
                })
            }
        </ListWrap>
    )
};

export default List;

const ListWrap = styled.ul`
`
const ListEl = styled.li`
    box-sizing: border-box;
    padding: 43px 79px 38px 75px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #E2E2E2;
    ${Media(mediaPxMb,[
        Padding(38, 27, 38, 23, mbSize),
    ])}
`
const ListText = styled.div`
    font-weight: 300;
    font-size: 14px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize),
    ])}
    ${({isLogout}) => {
        return isLogout&&`color:#E83548;`
    }}
`
const ListArrow = styled.img`
    width: 9px;
    height: 13px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(9, mbSize),
        Height(13, mbSize),
    ])}
`