import React from 'react';

// style
import styled from "styled-components"
import CheckBox from '../common/checkbox';

import {
    Media,
    FontSize,
    MB,
} from '../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const PurchaseInputTitle = ({data, onClick}) => {
    return(
        <PersonalTitleWrap>
            <PersonalTitle>{data.title}</PersonalTitle>
            <CheckBoxWrap>
                <CheckBox onClick={onClick} id={data.id} red/>
                <CheckText>{data.text} 정보와 동일</CheckText>
            </CheckBoxWrap>
        </PersonalTitleWrap>
    )
};

export default PurchaseInputTitle;

const PersonalTitleWrap = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    ${Media(mediaPxMb,[
        MB(13, mbSize),
    ])}
`
const PersonalTitle = styled.div`
    font-size: 18px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(18, mbSize),
    ])}
`
const CheckBoxWrap = styled.div`
    display: flex;
    align-items: center;
`
const CheckText = styled.div`
    font-size: 15;
    font-weight: 400;
    font-family: "HelveticaNeue";
    margin-left: 13px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`