//react
import React ,{useState, useEffect, useRef} from 'react';

// style
import styled from "styled-components"

//img
import fillArrowImg from '../../../img/common/fillArrow.png';
import greyArrowImg from '../../../img/common/greyArrow.png';
import trashIconImg from '../../../img/common/trashIcon.png';


import {
    Media,
    Width,
    FontSize,
    Height,
    MR,
    ML,
    Padding
} from '../../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const SelectItem = ({item, selectData, setSelectData}) => {

    const onClickArrow = (type) => {
        let newCount = item.count;
        let newArr = selectData;
        if(type == "minus"){
            newCount--;
        }else{
            newCount++;
        }
        newArr.map(value => {
            if(value.index == item.index){
                value.count=newCount
            }
        })
        setSelectData([...newArr])
    }


    const onClickDelete = () => {
        let newArr = selectData;
        newArr = newArr.filter(e => e.index !== item.index);
        setSelectData([...newArr])
    }

    return(
        <SelectItemCon>
            <SelectTitleOption>
                <SelectTitle>Sol</SelectTitle>
                <TitleDelete>
                    <Selectoption>{item.title}</Selectoption>
                    <DeleteBtn onClick={() => onClickDelete()} src={trashIconImg} alt="삭제 아이콘"/>
                </TitleDelete>
            </SelectTitleOption>
            <CountWrap>
                <CountItem onClick={() => {if(item.count==1){return}onClickArrow("minus")}}>
                    <CountArrow src={item.count==1?greyArrowImg:fillArrowImg} alt="화살표 아이콘"/>
                </CountItem>
                <CountItem>
                    <CountText>{item.count}</CountText>
                </CountItem>
                <CountItem onClick={() => onClickArrow("plus")}>
                    <CountArrow style={{transform: "rotate(180deg)"}} src={fillArrowImg} alt="화살표 아이콘"/>
                </CountItem>
            </CountWrap>
        </SelectItemCon>
    )
};

export default SelectItem;

const SelectItemCon = styled.div`
    width: 100%;
    border-bottom:1px solid #CBCBCB;
    padding:14px 0 15px;
    ${Media(mediaPxMb,[
        Padding(14, 0, 15, 0, mbSize),
    ])}
    &:last-child{
        border-bottom: 0;
    }
`
const SelectTitleOption = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const TitleDelete = styled.div`
    display: flex;
    align-items: center;
`
const DeleteBtn = styled.img`
    width: 17px;
    height: 17px;
    margin-left:13px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(17, mbSize),
        Height(17, mbSize),
        ML(13, mbSize),
    ])}
`
const SelectTitle = styled.div`
    font-size: 15px;
    font-weight: 500;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const Selectoption = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const CountWrap = styled.div`
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-top: 11px;
`
const CountItem = styled.div`
    border:1px solid #000;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:nth-child(2){
        border-left: 0;
        border-right: 0;
    }
    &:last-child{
        margin-right: 30px;
        ${Media(mediaPxMb,[
            MR(30, mbSize),
        ])}
    }
    ${Media(mediaPxMb,[
        Width(32, mbSize),
        Height(32, mbSize),
    ])}
`
const CountArrow = styled.img`
    width: 9px;
    height: 7px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        Width(9, mbSize),
        Height(7, mbSize),
    ])}
`
const CountText = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`