//react
import React ,{useState, useEffect, useRef} from 'react';

// style
import styled from "styled-components"

// components
import DateText from '../../common/dateText';
import { FontSize, Media, ML, MR, MT } from '../../common/mediaStlye';

// img
import ReviewStarStatus from '../../common/reviewStarStatus';

const mediaPxMb = 1024;
const mbSize = 428;

const ProductReviewItem = ({listIndex, item, onClickReviewImg}) => {

    const [sliceContent, setSliceContent] = useState("")
    const [isSlice, setIsSlice] = useState(true);
    const [isMore, setIsMore] = useState(false);

    
    // 펼쳐보기, 문자열자르기 --> 분기처리
    useEffect(() => {
        if(item.content.length > 70){
            setIsMore(true);
            let newText = item.content;
            newText=newText.substring(0, 70);
            setSliceContent(newText + "...")
        }else{
            setIsSlice(false)
        }
    }, [])

    return(
        <Item>
            {
                item.img.length !== 0?
                <TopImg>
                    <TopTextWrap>
                        <Nickname>{item.nickname}</Nickname>
                        <Date>{DateText(item.date, ".")}</Date>
                    </TopTextWrap>
                    <TopImgWrap>
                        {
                            item.img.map((el, index) => {
                                return(
                                    <Img onClick={() => onClickReviewImg(index, listIndex)} src={el} key={index}/>
                                )
                            })
                        }
                    </TopImgWrap>
                </TopImg>
                :
                <Top>
                    <Nickname>{item.nickname}</Nickname>
                    <Date>{DateText(item.date, ".")}</Date>
                </Top>
            }

            <Bottom>
                <Star src={ReviewStarStatus(item.star)} alt="별점"/>
                <Content>
                    {isSlice?sliceContent:item.content}
                </Content>
            </Bottom>
            {
                isMore&&
                <More onClick={() => setIsSlice(!isSlice)}>{isSlice?"+ 펼쳐보기":"- 접기"}</More>
            }
        </Item>
    )
};

export default ProductReviewItem;

const Item = styled.div`
    border-bottom: 1px solid #000;
`
const Top = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
    ${Media(mediaPxMb,[
        MT(16, mbSize),
    ])}
`
const TopImg = styled.div`
    display: flex;
    margin-top: 16px;
    ${Media(mediaPxMb,[
        MT(16, mbSize),
    ])}
`
const TopTextWrap = styled.div`
`
const TopImgWrap = styled.div`
    margin-left: 15px;
    ${Media(mediaPxMb,[
        ML(15, mbSize),
    ])}
`
const Date = styled.div`
    font-size: 14px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize)
    ])}
`
const Nickname = styled.div`
    font-size: 14px;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize)
    ])}
`
const Img = styled.img`
    width: calc(100%/7);
    margin-right: 5px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        MR(4, mbSize)
    ])}
    &:last-child{
        margin-right: 0px;
    }
`
const Bottom = styled.div`
    margin: 14px 0 15px;
    display: flex;
`
const Star = styled.img`
    width: 40px;
    height: 67px;
    margin-right: 15px;
`
const Content = styled.div`
    font-size: 14px;
    line-height: 20px;
    font-weight: 300;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize)
    ])}
`	

const More = styled.div`
    font-size: 13px;
    font-weight: 500;
    text-align: right;
    margin-bottom: 15px;
    ${Media(mediaPxMb,[
        FontSize(14, mbSize)
    ])}
`