//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"
import styles from './exchangeModal.module.css'

// img 
import backIconImg from '../../../img/common/backIcon.png';
import fillArrowImg from '../../../img/common/fillArrow.png';

import {
    Media,
    Width,
    FontSize,
    Padding,
    MT,
    Height,
    PL,
    ML,
    Px,
    MB,
    MR,
    Margin,
    Padding2,
    LineH,
    Right,
} from '../../common/mediaStlye.js';
import ModalHead from './modalHead';
import PolicyAlert from './policyAlert';
import InsertComma from '../../common/insertComma';

const mediaPxMb = 1024;
const mbSize = 428;

const ExchangeModal = ({subtitle, title, onClickBack, onClickSubmit, clickData})=> {

    const [reasonIndex, setReasonIndex] = useState(0);
    const [currentSelect, setCurrentSelect] = useState(0);
    const [currentReason, setCurrentReason] = useState("교환 사유를 선택 해주세요.");
    const [isPolicy, setIsPolicy] = useState(false);
    const [isList, setIsList] = useState(false);
    const reasonData =[
        "이유1",
        "이유2",
        "옵션 선택을 잘못했습니다.",
        "기타 ( 직접입력 )",
    ]
    const convertDate = date => {
        var week = new Array('일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일');
        var today = date.getDay();
        var todayLabel = week[today];
        return (`${date.getMonth()+1}/${date.getDate()}(${todayLabel[0]})`)
    } 

    const onChangeCase = (index) => {
        setCurrentSelect(index);
        setReasonIndex(0)
        if(index==1){
            setCurrentReason("반품 사유를 선택 해주세요.");
        }else{
            setCurrentReason("교환 사유를 선택 해주세요.");
        }
    }
    
    const onClickListEl = (index, item) => {
        setReasonIndex(index);
        setCurrentReason(item);
        setIsList(false);
    }

    return(
        <Container>
            <ReviewModalWrap>
                {
                    isPolicy&&
                    <PolicyAlert onClickClose={() => setIsPolicy(false)}/>
                }
                <ModalHead subtitle={subtitle} title={title} onClickBack={onClickBack}/>
                
                <PrdList>
                    {
                        clickData.prd.map((item, index) => {
                            return(
                                <PrdListEl key={index}>
                                    <PrdImg src={item.prd_img}/>
                                    <PrdTextWrap>
                                        <PrdDate>{convertDate(clickData.completeDate)} 도착</PrdDate>
                                        <PrdNamePrice>
                                            <PrdName>{item.prd_title}</PrdName>
                                            <PrdPrice>{InsertComma(item.prd_price)} won</PrdPrice>
                                        </PrdNamePrice>
                                        <PrdCount>{item.count}개</PrdCount>
                                    </PrdTextWrap>
                                </PrdListEl>
                            )
                        })
                    }
                    
                </PrdList>

                <RadioWrap>
                    {
                        ["교환", "반품"].map((item, index) => {
                            return(
                                <InputWrap key={index}>
                                    <CaseInput className={styles.caseInput} name="case" id={`${index}radioCase`} onClick={() => onChangeCase(index)} type="radio" name="case" defaultChecked={index==0}/>
                                    <CaseLabel className={styles.caseLabel} name="pay" for={`${index}radioCase`}></CaseLabel>
                                    <CaseText>{item}</CaseText>
                                </InputWrap>
                            )
                        })
                    }
                </RadioWrap>

                <ExchangeTextWrap>
                    <ExchangeText onClick={ () => setIsPolicy(true)}>교환 / 환불 규정 보기</ExchangeText>
                </ExchangeTextWrap>
                
                <ReasonListWrap>
                    <ReasonHead style={{color:reasonIndex==0?"#CBCBCB":"#000"}} onClick={() => setIsList(!isList)}>{currentReason}<ReasonArrow active={isList} src={fillArrowImg} alt="화살표 아이콘"/></ReasonHead>  
                    {
                        isList&&
                        <ReasonList>
                            {
                                reasonData.map((item, index) => {
                                    return(
                                        <ReasonListEl
                                        onClick={() => onClickListEl(index+1, item)}
                                        key={index}>{item}</ReasonListEl>
                                    )
                                })
                            }
                        </ReasonList>
                    }

                    <ReasonTextArea active={reasonIndex==4} placeholder={`${currentSelect==0?"교환":"환불"} 사유를 입력해주세요.`}/>  
                </ReasonListWrap>


                <BtnWrap>
                    <SubmitBtn active={reasonIndex!==0} onClick={() => onClickSubmit()}>신청하기</SubmitBtn>
                </BtnWrap>

            </ReviewModalWrap>
        </Container>
    )   
};

export default ExchangeModal;

const Container = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.47);
    z-index: 99;
    top:0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
`
const ReviewModalWrap = styled.div`
    background: #fff;
    padding: 25px 35px 45px;
    width: 640px;
    box-sizing: border-box;
    position: relative;
    ${Media(mediaPxMb,[ 
        `max-height: calc(100vh - ${Px(100, mbSize)})`,
       "overflow: scroll",
       Width(406, mbSize),
        Padding(28, 8, 36, 8, mbSize)
    ])}
`
const BackBtn = styled.div`
    font-size: 18px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
    ])}
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
    ${Media(mediaPxMb,[
        Width(14, mbSize),
        Height(8, mbSize),
    ])}
`
const SubTitleBack = styled.div`
    display: flex;
    justify-content: space-between;
`
const SubTitle = styled.div`
    font-size: 12px;
    font-weight: 500;
    padding-left: 23px;
    ${Media(mediaPxMb,[
        FontSize(10, mbSize),
        PL(12, mbSize),
    ])}
`
const Title = styled.h1`
    font-size: 32px;
    font-weight: 500;
    font-family: "Big Caslon";
    padding: 10px 0 12px 23px;
    border-bottom: 1px solid #000;
    ${Media(mediaPxMb,[
        FontSize(23, mbSize),
        Padding(12, 0, 11, 12, mbSize)
    ])}
`


const PrdList = styled.div`
    background-color: #F8F7F7;
    padding: 12px 110px 14px;
    box-sizing: border-box;
    max-height: 250px;
    overflow: scroll;
    margin-top: 12px;
    ${Media(mediaPxMb,[
        Padding(12, 13, 14, 18, mbSize),
        `max-height: ${Px(150, mbSize)}`
    ])}
`
const PrdListEl = styled.div`
    display: flex;
    margin-bottom: 10px;
    ${Media(mediaPxMb,[
        MB(10, mbSize),
    ])}
    &:last-child{
        margin-bottom: 0;
    }
`
const PrdImg = styled.img`
    width: 77px;
    margin-right: 23px;
    ${Media(mediaPxMb,[
        Width(77, mbSize),
        MR(23, mbSize),
    ])}
`
const PrdTextWrap = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
`
const PrdDate = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const PrdNamePrice = styled.div`
    display: flex;
    width: 100%;
    justify-content: space-between;
`
const PrdName = styled.div`
    font-size: 15px;
    font-weight: 780;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const PrdPrice = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const PrdCount = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const RadioWrap = styled.div`
    display: flex;
    align-items: center;
    padding: 0 155px;
    justify-content: space-between;
    margin: 22px 0 56px;
    ${Media(mediaPxMb,[
        Margin(23, 0, 33, 0, mbSize),
        Padding2(0, 73,mbSize),
    ])}
`
const InputWrap = styled.div`
    display: flex;
    align-items: center;
`
const CaseInput = styled.input`
`
const CaseLabel = styled.label`
`
const CaseText = styled.div`
    font-size: 15px;
    font-weight: 400;
    margin-left: 20px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        ML(20, mbSize),
    ])}
`
const ExchangeTextWrap = styled.div`
    text-align: center;
    margin-bottom: 19px;
    ${Media(mediaPxMb,[
        MB(19, mbSize),
    ])}
`
const ExchangeText = styled.div`
    font-weight: 400;
    font-size: 15px;
    cursor: pointer ;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const ReasonListWrap = styled.div`
    padding: 0 26px;
    box-sizing: border-box;
    position: relative;
    ${Media(mediaPxMb,[
        "padding:0"
    ])}
`
const ReasonHead = styled.div`
    width: 100%;
    height: 50px;
    border:1px solid #000;
    position: relative;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    font-weight: 400;
    font-size: 15px;
    ${Media(mediaPxMb,[
        Height(50, mbSize),
        LineH(50, mbSize),
        FontSize(15, mbSize),
    ])}
`
const ReasonArrow = styled.img`
    position: absolute;
    top: 50%;
    right: 40px;
    width: 12px;
    transform: translateY(-50%);
    ${Media(mediaPxMb,[
        Width(12, mbSize),
        Right(40, mbSize),
    ])}
    ${({active}) => {
        return active&&`transform:rotate(180deg)`
    }}
`
const ReasonList = styled.ul`
    border:1px solid #000;
    width: calc(100% - 52px);
    position: absolute;
    z-index:5;
    border-top: 0;
    background-color: #fff;
    ${Media(mediaPxMb,[
        "width:100%;"
    ])}
`
const ReasonListEl = styled.li`
    width: 100%;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    font-weight: 400;
    font-size: 15px;
    ${Media(mediaPxMb,[
        Height(50, mbSize),
        LineH(50, mbSize),
        FontSize(15, mbSize),
    ])}
`
const ReasonTextArea = styled.textarea`
    width: 100%;
    height: 141px;
    border:1px solid #000;
    margin-top: 11px;
    resize: none;
    padding: 21px 38px 21px;
    box-sizing: border-box;
    font-weight: 300;
    font-size: 12px;
    opacity: 0;
    ${Media(mediaPxMb,[
        Height(141, mbSize),
        Padding(21, 38, 21, 38, mbSize),
        FontSize(12, mbSize),
    ])}
    ${({active}) => {
        return active&&`opacity:1`
    }}
`







const BtnWrap =styled.div`
    width: 100%;
    text-align: center;
    margin: 27px 0 0;
    ${Media(mediaPxMb,[
        MT(18, mbSize),
    ])}
`
const SubmitBtn = styled.button`
    border: 1px solid #000;
    width: 385px;
    height: 66px;
    background-color: #fff;
    font-size: 15px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        Width(385, mbSize),
        Height(66, mbSize),
    ])}
    transition: 300ms;
    ${({active}) => {
        return active&&`
        background-color:#211013;
        color:#fff
        `
    }}
`