//react
import React, { useEffect, useState } from 'react';

// style
import styled from "styled-components"

// img 
import backIconImg from '../../../img/common/backIcon.png';
import fillArrowImg from '../../../img/common/fillArrow.png';

import {
    Media,
    Width,
    FontSize,
    Padding,
    MT,
    Height,
    PL,
    ML,
    Px,
    MB,
    MR,
    Margin,
    Padding2,
    LineH,
    Right,
} from '../../common/mediaStlye.js';

const mediaPxMb = 1024;
const mbSize = 428;

const ModalHead = ({subtitle, title, onClickBack}) => {
    
    return(
        <>
            <SubTitleBack>
                <SubTitle>{subtitle}</SubTitle>
                <BackBtn onClick={() => onClickBack()}>
                    <BackBtnImg src={backIconImg} alt="뒤로가기 버튼" />
                    Back
                </BackBtn>
            </SubTitleBack>
            <Title>{title}</Title>
        </>
    )
};

export default ModalHead;

const BackBtn = styled.div`
    font-size: 18px;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(13, mbSize),
        MR(12, mbSize)
    ])}
`
const BackBtnImg = styled.img`
    width: 19px;
    height: 11px;
    margin-right: 22px;
    ${Media(mediaPxMb,[
        Width(14, mbSize),
        Height(8, mbSize),
    ])}
`
const SubTitleBack = styled.div`
    display: flex;
    justify-content: space-between;
`
const SubTitle = styled.div`
    font-size: 12px;
    font-weight: 500;
    padding-left: 23px;
    ${Media(mediaPxMb,[
        FontSize(10, mbSize),
        PL(12, mbSize),
    ])}
`
const Title = styled.h1`
    font-size: 32px;
    font-weight: 500;
    font-family: "Big Caslon";
    padding: 10px 0 12px 23px;
    border-bottom: 1px solid #000;
    ${Media(mediaPxMb,[
        FontSize(23, mbSize),
        Padding(12, 0, 11, 12, mbSize)
    ])}
`


