//react
import React ,{useState, useEffect, useRef} from 'react';

// style
import styled from "styled-components"
import BackBtn from '../../../components/common/backBtn';
import Header from '../../../components/header/header';

// img
import headDetialImg from '../../../img/collection/episode/headDetial.png'
import episodeImg_2 from '../../../img/collection/episode/episodeImg_2.png';
import episodeImg_7 from '../../../img/collection/episode/episodeImg_7.png';

import {
    FontSize,
    Height,
    LineH,
    Media,
    Mobile,
    MT,
    Padding,
    PC,
} from '../../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;


const EpisodeDetail = (props) => {

    return(
        <Container>
            <Header isMbBack/>
            <PC><BackBtn /></PC>
            <GridWrap>
                <Mobile>
                    <MbTitle>
                        Cotton collection
                    </MbTitle>
                </Mobile>
                <MainImg></MainImg>
                
                <ContentElsWrap>
                    <ContentWrap>
                        <TextSide>
                            He was simple sugar, <br />
                            spun on hot air,<br />
                            soaked in pink,<br />
                            a tasty treat.<br />
                            He was cotton candy.<br />
                            <br />
                            I would wrap him around my finger,<br />
                            like I could coax a ring out of sugar and thin air.<br />
                            To have felt him melt in my mouth,<br />
                            each time the tip of my tongue got a taste.<br />
                            He was cotton candy.<br />
                            <br />
                            He was a carnival with all the best attractions.<br />
                            but balloon darts pop when you pour enough money into the game.<br />
                            but a dunk tank is just a plunge into shallow depths, a break from the sun.<br />
                            but elephants should be free, not tamed by fire and humans’ greedy desire.<br />
                            but a clown without their makeup might as well be a less creepy comedian.<br />
                            but won over stuffed animals are just like cotton candy,<br />
                            a squishy substance when you need a stable solid.<br />
                            <br />
                            Step right up!<br />
                            Spotlight on the star of our circus show,<br />
                            see the cotton candy man.<br />
                            His heart made of sugar,<br />
                            a toxic substance.<br />
                            His breath’s brevity enough to set off cotton candy’s chemical reaction,<br />
                            scorching hot air against pink paint,<br />
                            there is nothing sweet about being spun.<br />
                            <br />
                            Dyed in bright colors to deliver a warped reality,<br />
                            he was seemingly a healthy vibrant,<br />
                            unlike the poison within.<br />
                        </TextSide>
                        <ImgSide src={episodeImg_2} />
                    </ContentWrap>

                    <ContentWrap>
                        <ImgSide src={episodeImg_7}/>
                        <TextSideSimple>
                            <TextTitle>Cotton collection</TextTitle>
                            <TextSubTitle>2020</TextSubTitle>
                        </TextSideSimple>
                    </ContentWrap>
                </ContentElsWrap>

            </GridWrap>
        </Container>
    )
};

export default EpisodeDetail;

const Container = styled.div`
`

const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
const MbTitle = styled.div`
    ${Padding(100, 0, 19, 19, mbSize)}
    ${FontSize(22, mbSize)}
    font-weight: 500;
    font-family: "Big Caslon";
`
const MainImg = styled.div`
    width: 100%;
    height: 1080px;
    background-image: url(${headDetialImg});
    ${Media(mediaPxTablet,[
        Height(1080, 1920),
    ])}
    ${Media(mediaPxMb,[
        Height(479, mbSize),
    ])}
`
const ContentElsWrap = styled.div`
    display: flex;
    flex-direction: column;
`
const ContentWrap = styled.div`
    width: 100%;
    display: flex;
    ${Media(mediaPxMb,[
        "flex-direction: column",
    ])}
`
const TextSide = styled.div`
    width: 50%;
    padding: 170px 0 174px 103px;
    box-sizing: border-box;
    color:#8E8485;
    font-size: 15px;
    line-height: 20px;
    ${Media(mediaPxTablet,[
        FontSize(15, mediaPxTablet),
        LineH(20, mediaPxTablet),
        Padding(170, 30, 174, 103, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100%",
        FontSize(15, mbSize),
        LineH(23, mbSize),
    ])}
`
const ImgSide = styled.img`
    width: 50%;
    ${Media(mediaPxMb,[
        "width:100%",
    ])}
`
const TextSideSimple = styled.div`
    display:flex;
    flex-direction: column;
    width: 50%;
    justify-content: center;
    align-items: center;
    background-color: #F8F7F7;
    &>*{
        color:#8E8485;
        font-weight: 300;
    }
    ${Media(mediaPxMb,[
        Height(502, mbSize),
        "width:100%"
    ])}
`
const TextTitle = styled.div`
    font-size: 36px;
    line-height: 59px;
    ${Media(mediaPxTablet,[
        FontSize(36, mediaPxTablet),
        LineH(59, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(16, mbSize),
    ])}
`
const TextSubTitle = styled.div`
    font-size: 21px;
    line-height: 34px;
    margin-top: 10px;
    ${Media(mediaPxTablet,[
        FontSize(21, mediaPxTablet),
        LineH(34, mediaPxTablet),
        MT(10, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(9, mbSize),
    ])}
`