import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    FontSize,
    MB,
    PT,
} from '../../../components/common/mediaStlye';

import DateText from '../../common/dateText';
import MyQnAListEl from './myQnAListEl';

const mediaPxMb = 1024;
const mbSize = 428;


const MyQnA = ({qaData, setHeaderIcon, headerIcon}) => {
    return(
        <Wrap>
            {
                qaData.length == 0?
                <NoData>작성하신 상품 문의가 없습니다.</NoData>
                :
                <>
                    {
                        qaData.map((item, index) => {
                            return(
                                <MyQnAListEl
                                headerIcon={headerIcon}
                                setHeaderIcon={setHeaderIcon}
                                item={item}
                                key={index}/>
                            )
                        })
                    }
                </>
            }
        </Wrap>
    )
};

export default MyQnA;

const Wrap = styled.div` 
`

const NoData = styled.div`
    color:#A0A0A0;
    font-size: 12px;
    font-weight: 300;
    text-align: center;
    padding-top: 90px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        PT(90, mbSize),
    ])}
`