import React from 'react';

// style
import styled from "styled-components"

// component
import InsertComma from '../common/insertComma';

import {
    Media,
    FontSize,
} from '../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const BillElDetail = ({title, price}) => {
    

    return(
        <BillDetailItem>
            <BillDetailTitle>{title}</BillDetailTitle>
            <BillDetailPrice>{InsertComma(price)} won</BillDetailPrice>
        </BillDetailItem>
    )
};

export default BillElDetail;

const BillDetailItem = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 11px;
    padding-right: 36px;
    &>*{
        color: #707070; 
        font-size: 13px;
        font-weight: 300;
        ${Media(mediaPxMb,[
            FontSize(13, mbSize),
        ])}
    }
`
const BillDetailTitle = styled.div`
`
const BillDetailPrice = styled.div`
`