//react
import React from 'react';
import {useHistory} from "react-router-dom";

// style
import styled from "styled-components"

import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    PT,
    PL,
    Right,
    MB
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const GalleryItemMSeul = ({img, title, historyLink}) => {
    
    const history = useHistory();

    return(
        <GalleryItemWrap >
            <GImg src={img} alt="아티스트 이미지" onClick={() => history.push(historyLink)}/>
            <GTitle>{title}</GTitle>
        </GalleryItemWrap>
    )
};

export default GalleryItemMSeul;

const GalleryItemWrap = styled.div`
    margin-bottom:41px;
    width:344px;
    ${Media(mediaPxTablet,[
        Width(344, mediaPxTablet),
        MB(41, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100%",
    ])}
`
const GImg = styled.img`
    width:100%;
    cursor: pointer;
`
const GTitle = styled.div`
    margin:13px 0 62px;
    font-weight:500;
    font-size:20px;
    cursor: pointer;
    ${Media(mediaPxTablet,[
        Margin(13, 0, 62, 0, mediaPxTablet),
        FontSize(20, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        FontSize(20, mbSize),
        Margin(17, 0, 61, 19, mbSize),
    ])}
`