//react
import React ,{useState, useEffect} from 'react';
import {Link} from "react-router-dom";

// style
import styled from "styled-components"

// components
import Header from '../../components/header/header';
import BackBtn from '../../components/common/backBtn';

// img
import DetailSecondImg_1 from '../../img/taste/detailSecond/detailSecond_1.png';
import DetailSecondImg_2 from '../../img/taste/detailSecond/detailSecond_2.png';
import DetailSecondImg_3 from '../../img/taste/detailSecond/detailSecond_3.png';
import DetailSecondImg_4 from '../../img/taste/detailSecond/detailSecond_4.png';

import {
    Media,
    Width,
    FontSize,
    Height,
    Padding,
    Margin,
    PB,
    MT,
    MB,
    LineH,
    PC,
    Mobile,
    PT
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1700;
const mediaPxMb = 1024;
const mbSize = 428;

const TasteDetailSecond = (props) => {

    const subData = [
        {img: DetailSecondImg_2, text:"Artist is a broad term that describes occupations in fine arts,  crafts, desktop publishing, website design, animation and video game production. …Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing "},
        {img: DetailSecondImg_3, text:"Artist is a broad term that describes occupations in fine arts,  crafts, desktop publishing, website design, animation and video game production. …Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing  firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, "},
        {img: DetailSecondImg_4, text:"Artist is a broad term that describes occupations in fine arts,  crafts, desktop publishing, website design, animation and video game production. …Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing  firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, "},
    ]

    return(
        <Container>
            <Header isMbBack/>
            <PC>
                <BackBtn />
            </PC>
            <GridWrap>
                <SmallTitleWrap>
                    <SmallTitle>Vogue x seulmit collection vol.1</SmallTitle>
                    <Date>2020.01.01</Date>
                </SmallTitleWrap>
                <MaibnImg bgImg={DetailSecondImg_1}></MaibnImg>

                <MainText>
                    Artist is a broad term that describes occupations in fine arts,  crafts, desktop publishing, website design, animation and video game production. …Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing 
                    firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, animation and video game production. … Artists can work as freelancers or for studios, advertising agencies, 
                    media design companies, marketing firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, animation and video game production. … Artists can work as freelancers or 
                    for studios, advertising agencies, media design  companies, marketing Artist is a broad term that describes occupations in fine arts,  crafts, desktop publishing, website design, animation and video game production. …Artists can work as freelancers or for 
                    studios, advertising agencies, media design companies, marketing  firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, animation and video game production. … 
                    Artists can work as freelancers or for studios, advertising agencies, media design companies, marketing firms or in graphic arts departments. Artist is a broad term that describes occupations in fine arts, crafts, desktop publishing, website design, 
                    animation and video game production. … Artists can work as freelancers or for studios, advertising agencies, media design  companies, marketing 
                </MainText>
                {
                    subData.map((item, index) => {
                        return(
                            <SubWrap key={index}>
                                <SubImg bgImg={item.img}></SubImg>
                                <SubText>{item.text}</SubText>
                            </SubWrap>
                        )
                    })
                }


            </GridWrap>
        </Container>
    )
};

export default TasteDetailSecond;

const Container = styled.div`
`

const GridWrap = styled.div`
    width:calc(100% - 76px);
    margin: 0 0 0 76px;
    padding-bottom:285px;
    ${Media(mediaPxTablet,[
        PB(285, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
// 상단 작은 제목
const SmallTitleWrap = styled.div`
    width:calc(100% - 84px);
    border-bottom:1px solid #1f1113;
    padding: 148px 17px 15px;
    margin: 0 auto 15px;
    display:flex;
    justify-content:space-between;
    ${Media(mediaPxTablet,[
        Padding(148, 17, 15, 17, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        `margin: 0 ${10, mbSize}, ${19, mbSize}`,
        "flex-direction: column-reverse",
    ])}
`
const SmallTitle = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
         FontSize(20, mbSize)
    ])}
`
const Date = styled.span`
    font-size:20px;
    font-weight:500;
    ${Media(mediaPxTablet,[
        FontSize(20, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(13, mbSize)
    ])}
`

const MaibnImg = styled.div`
    width:100%;
    height: 823px;
    ${Media(mediaPxMb,[
        Height(694, mbSize),
    ])}
    ${({bgImg}) => {
        return`
            background-image: url(${bgImg});
        `
    }}
`
const MainText = styled.p`
    width:1671px;
    margin: 50px auto;
    color:#707070;
    font-size:15px;
    line-height:30px;
    ${Media(mediaPxTablet,[
        Width(1494, mediaPxTablet),
        LineH(30, mediaPxTablet),
        FontSize(15, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        `font-family: "Big Caslon"`,
        "font-weight: 500",
        LineH(30, mbSize),
        FontSize(15, mbSize),
    ])}
`

const SubWrap = styled.div`
`
const SubImg = styled.div`
    height:349px;
    ${Media(mediaPxTablet,[
        Height(394, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        Height(349, mbSize),
    ])}
    ${({bgImg}) => {
        return`
            background-image: url(${bgImg});
        `
    }}
`
const SubText = styled.p`
    width:1494px;
    margin: 67px auto;
    color:#707070;
    font-size:15px;
    line-height:30px;
    ${Media(mediaPxTablet,[
        Width(1494, mediaPxTablet),
        FontSize(15, mediaPxTablet),
        LineH(30, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        `font-family: "Big Caslon"`,
        "font-weight: 500",
        LineH(30, mbSize),
        FontSize(15, mbSize),
    ])}
`