import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    Padding,
    FontSize,
    MT,
    Width,
    MR,
    MB,
    LineH,
    Margin,
    Height,
    Margin2,
    Px,
    Top,
} from '../../../components/common/mediaStlye';

import DateText from '../../common/dateText';
import InsertComma from '../../common/insertComma';
const mediaPxMb = 1024;
const mbSize = 428;

const MyQnADetail = ({detailData, setIsDetail}) => {


    // 서버에서 넘어온 타입 한글로 변환
    const returnType = (int) => {
        if(int == 0){
            return("답변 대기중")
        }else{
            return("답변완료")
        }
    }

    // 문의 삭제 클릭
    const onClickDelete = () => {
        setIsDetail(false) 
    }

    return(
        <Container>
            <Grid>
                <TopArea>
                    <DateType>
                        <Date>{DateText(detailData.qustionData, ".")}</Date>
                        <Type active={detailData.type==1}>{returnType(detailData.type)}</Type>
                    </DateType>
                    <Title>{detailData.title}</Title>
                </TopArea>
                <PrdWrap>
                    <PrdImg src={detailData.prdImg}/>
                    <PrdTextWrap>
                        <PrdType>{detailData.prdType}</PrdType>
                        <PrdNamePrice>
                            <PrdName>{detailData.prdName}</PrdName>
                            <PrdPrice>{InsertComma(detailData.prdPrice)} won</PrdPrice>
                        </PrdNamePrice>
                    </PrdTextWrap>
                </PrdWrap>
                <Content>
                    <Question>{detailData.question}</Question>
                    <AnswerWrap style={{opacity:detailData.type==1?1:0}}>
                        <AnswerDate>{DateText(detailData.answerDate, ".")}</AnswerDate>
                        <Answer>{detailData.answerContent}{detailData.answerContent}{detailData.answerContent}{detailData.answerContent}</Answer>
                    </AnswerWrap>
                </Content>
                <BtnWrap>
                    <DeleteBtn onClick={() => onClickDelete()}>문의 삭제</DeleteBtn>
                    <BackBtn onClick={() => setIsDetail(false)}>돌아가기</BackBtn>
                </BtnWrap>
            </Grid>
        </Container>
    )
};

export default MyQnADetail;

const Container = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    z-index: 80;
    margin: 0 0 0 76px;
    display: flex;
    align-items: center;
    justify-content: center;
    ${Media(mediaPxMb,[
        "margin:0;",
        "align-items: unset",
        "justify-content: unset",
        Top(74, mbSize),
    ])}

`
const Grid = styled.div`
    width: 570px;
    position: relative;
    ${Media(mediaPxMb,[
        "width:100%",
        "height:100%",
        `max-height: calc(100vh - ${Px(100, mbSize)})`,
        "overflow: scroll",
    ])}
`
const TopArea = styled.div`
    padding: 0 109px 0 111px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        Padding(0, 37, 0, 37, mbSize),
    ])}
`
const DateType = styled.div`
    display: flex;
    justify-content: space-between;
    ${Media(mediaPxMb,[
        Padding(37, 0, 0, 0, mbSize),
    ])}
`
const Date = styled.div`
    color:#A0A0A0;
    font-size: 12px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
`
const Type = styled.div`
    color:#A0A0A0;
    font-size: 12px;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
    ])}
    ${({active}) => {
        return active&&`color:#E83548;`
    }}
`
const Title = styled.div`
    margin-top: 12px;
    font-size: 14px;
    font-weight: 400;
    ${Media(mediaPxMb,[
        MT(12, mbSize),
        FontSize(14, mbSize),
    ])}
`
const PrdWrap = styled.div`
    border-top: 1px solid #000;
    background-color: #F8F7F7;
    margin-top: 13px;
    display: flex;
    padding: 12px 109px 14px 111px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        MT(13, mbSize),
        Padding(12, 37, 14, 37, mbSize),
    ])}
`
const PrdImg = styled.img`
    width: 77px;
    margin-right: 23px;
    ${Media(mediaPxMb,[
        Width(77, mbSize),
        MR(27, mbSize),
    ])}
`
const PrdTextWrap = styled.div`
    width: 100%;
`
const PrdType = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    margin-bottom: 5px;
    margin-top: 8px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
        MB(5, mbSize),
        MT(8, mbSize),
    ])}
`
const PrdNamePrice = styled.div`
    display: flex;
    justify-content: space-between;
`
const PrdName = styled.div`
    font-size: 15px;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const PrdPrice = styled.div`
    font-size: 15px;
    color:#8B8B8B;
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`
const Content = styled.div`
    font-size: 12px;
    line-height: 20px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        LineH(20, mbSize),
    ])}
`
const Question = styled.div`
    width: 399px;
    margin: 36px 0 54px 111px;
    font-weight: 400;
    max-height: 80px;
    overflow: scroll;
    ${Media(mediaPxMb,[
        "width:100%",
        "box-sizing: border-box",
        `max-height: ${Px(80, mbSize)}`,
        Padding(0, 47, 0, 47, mbSize),
        "margin:0;",
        MT(16, mbSize),
        MB(43, mbSize),
    ])}
`
const AnswerWrap = styled.div`
    border:1px solid #000;
    padding: 39px 98px 54px 111px;
    box-sizing: border-box;
    ${Media(mediaPxMb,[
        Padding(19, 23, 36, 23, mbSize),
        "margin: 0 auto;",
        `width:calc(100% - ${Px(24, mbSize)})`
    ])}
`
const AnswerDate = styled.div`
    font-size: 12px;
    color:#A0A0A0;
    margin-bottom: 19px;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        MB(19, mbSize),
    ])}
`
const Answer = styled.div`
    font-size: 12px;
    line-height: 20px;
    height: 119px;
    overflow-y: scroll;
    font-weight: 400;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        LineH(20, mbSize),
        Height(119, mbSize),
    ])}
`
const BtnWrap = styled.div`
    margin-top: 26px;
    text-align: center;
    margin-bottom: 30px;
    ${Media(mediaPxMb,[
        MT(22, mbSize),
        MB(30, mbSize),
    ])}
    &>button{
        width: 226px;
        height: 55px;
        border:1px solid #000;
        outline: none;
        font-size: 15px;
        ${Media(mediaPxMb,[
            Width(185, mbSize),
            Height(45, mbSize),
            FontSize(14, mbSize),
        ])}
    }
`
const DeleteBtn = styled.button`
    background-color: #fff;
    margin-right: 12px;
    ${Media(mediaPxMb,[
        MR(10, mbSize)
    ])}
`
const BackBtn = styled.button`
    background-color: #000;
    color:#fff;
`