//react
import React ,{useState} from 'react';

// style
import styled from "styled-components"
import ProductReviewItem from './productReviewItem';

import {
    Media,
    Width,
    FontSize,
    MB,
    MT,
    Height,
    MR
} from '../../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;


const ProductReview = ({reviewData, onClickReviewImg, setIsReviewModal}) => {
    const [activePage, setActivePage] = useState(1);

    return(
        <Wrap>
            <List>
                {
                    reviewData.map((item, index) => {
                        return(
                            <ProductReviewItem onClickReviewImg={onClickReviewImg} item={item} listIndex={index} key={index}/>
                        )
                    })
                }
            </List>

            <IndexWrite>
                <IndexWrap>
                    {
                        [1, 2, 3, 4, 5].map((item, index) => {
                            return(
                                <Index onClick={() => setActivePage(index+1)} active={activePage==index+1?true:false} key={index}>{item}</Index>
                            )
                        })
                    }
                </IndexWrap>

                <Write onClick={() => setIsReviewModal(true) }>Write Review</Write>
            </IndexWrite>
        </Wrap>
    );

};

export default ProductReview;

const Wrap = styled.div`
    margin-bottom: 64px;
    ${Media(mediaPxMb,[
        MB(64, mbSize),
    ])}
`
const List = styled.div`
`
const IndexWrite = styled.div`
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    ${Media(mediaPxMb,[
        MT(10, mbSize),
    ])}
`
const IndexWrap = styled.div`
    display: flex;
`
const Index = styled.div`
    font-size: 14px;
    margin-right: 20px;
    cursor: pointer;
    ${Media(mediaPxMb,[ 
        FontSize(14, mbSize),
        MR(20, mbSize),
    ])}
    ${({active}) => {
        return active?
        `color: #000;`
        :
        `color: #8B8B8B;`
    }}
`
const Write = styled.button`
    font-size: 15px;
    background-color: #fff;
    border:1px solid #000;
    outline: none;
    width: 201px;
    height: 44px;
    ${Media(mediaPxMb,[ 
        Width(201, mbSize),
        Height(44, mbSize),
        FontSize(15, mbSize),
    ])}
`