//react
import React ,{useState, useEffect} from 'react';
import {Link} from "react-router-dom";

// style
import styled from "styled-components"
import Header from '../../components/header/header';
import InfoList from '../../components/info/infoList';

import {
    Mobile,PC,
    Media,
    Width,
    FontSize,
    PB,
    Padding,
    MB,
    MT,
    PT,
} from '../../components/common/mediaStlye';

const mediaPxTablet = 1300;
const mediaPxMb = 1024;
const mbSize = 428;

const Info = (props) => {

    return(
        <Container>
            <Header />
            <GridWrap>
                <Grid>
                    <TitleWrap>
                        <Title>Notice</Title>
                        <Desc>Receive notification from us</Desc>
                    </TitleWrap>
                
                    <InfoList link={"NoticeDetail"}/>
                    <InfoList isEnd/>

                </Grid>
            </GridWrap>
        </Container>
    )
};

export default Info;
 
const Container = styled.div`
`
const GridWrap = styled.div`
    width:calc(100vw - 76px);
    margin: 0 0 0 76px;
    ${Media(mediaPxMb,[
        "width:100vw",
        "margin:0",
    ])}
`
const Grid = styled.div`
    width:1140px;
    margin:0 auto;
    padding-bottom: 305px;
    ${Media(mediaPxTablet,[
        Width(1140, mediaPxTablet),
        PB(305, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        PT(74, mbSize),
        "width:100%",
        "margin:0",
    ])}
`

const TitleWrap = styled.div`
    padding:84px 0 72px;
    & > p {
        font-weight:500;
        font-family: "Big Caslon";
    }
    ${Media(mediaPxTablet,[
        Padding(84, 0, 72, 0, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        Padding(26, 0, 23, 19, mbSize),
    ])}
`

const Title = styled.p`
    font-size:35px;
    margin-bottom:13px;
    color:#0F1720;
    ${Media(mediaPxTablet,[
        FontSize(35, mediaPxTablet),
        MB(13, mediaPxTablet),
    ])}
    ${Media(mediaPxMb,[
        MB(10, mbSize),
        FontSize(22, mbSize),
    ])}
`
const Desc = styled.p`
    font-size:25px;
    color:#0F1720;
    ${Media(mediaPxTablet,[
        FontSize(25, mediaPxTablet)
    ])}
    ${Media(mediaPxMb,[
        FontSize(15, mbSize),
    ])}
`