//react
import React ,{useState, useEffect} from 'react';

// style
import styled from "styled-components"

const Checkbox = ({text, margin, checked, onClick, isContent}) => {

    console.log(isContent);
    return(
        <Label margin={margin&&margin}>
            <InputCkBox type="checkbox" checked={checked} onChange={onClick}/>
            {text}
            {
            isContent
            &&
            <Content>
                "전체내용 보기 (필수)"
            </Content>
            }
        </Label>
    )
};

export default Checkbox;

const Label = styled.label`
    font-size: 15px;
    font-family: "HelveticaNeue";
    font-weight: 400;
    ${({margin})=>{
        return (`margin:${margin};`)
    }}
`
const InputCkBox = styled.input`
    margin-right: 13px;
`
const Content = styled.span`
    font-size: 13px;
    font-family: "HelveticaNeue";
    font-weight: 400;
`