//react
import React from 'react';

// style
import styled from "styled-components";

import {
    FontSize,
    Height,
    MB,
    Media,
    Width,

} from '../../common/mediaStlye';

const mediaPxMb = 1024;
const mbSize = 428;

const ProductDetailBox = ({text, onClick}) => {
    
    return(
        <Box onClick={onClick && onClick}>
            {text}
        </Box>
    )
};

export default ProductDetailBox;

const  Box = styled.button`
    width: 100%;
    height: 66px;
    margin-bottom: 5px;
    background-color: #fff;
    border:1px solid #000;
    font-size: 15px;
    cursor: pointer;
    font-weight: 300;
    ${Media(mediaPxMb,[
        Width(385, mbSize),
        Height(66, mbSize),
        FontSize(15, mbSize),
        MB(5, mbSize),
    ])}
`