import React, { useEffect, useState } from 'react';

// components
import {
    styled,
    Media,
    PT,
    FontSize,
    Width,
    Height,
    Margin,
    Padding,
    Padding2,
    ML,
} from '../../common/mediaStlye';

// img
import FilterArrowImg from '../../../img/common/fillArrow.png';
import MyReviewListEl from './myReviewListEl';

const mediaPxMb = 1024;
const mbSize = 428;



const MyReview = ({reviewData, setReviewData}) => {

    const [isFilter, setIsFilter] = useState(false);
    const [currentFilter, setCurrentFilter] = useState("ALL");
    const filterData = ["ALL", "Filter_1", "Filter_2", "Filter_3"];

    const onClickFilterEl = (index) => {
        setIsFilter(false);
        setCurrentFilter(filterData[index]);
    }

    return(
        <Wrap>
            <FilterWrap>
                <FilterHead onClick={() => setIsFilter(!isFilter)}>{currentFilter}<FilterArrow rotate={isFilter} src={FilterArrowImg} alt="방향 아이콘"/></FilterHead>
                {
                    isFilter&&
                    <FilterList>
                        {
                            filterData.map((item, index) => {
                                return(
                                    <FilterEl onClick={() => onClickFilterEl(index)} key={index}>{item}</FilterEl>
                                )
                            })
                        }
                    </FilterList>
                }
            </FilterWrap>
            {
                reviewData.length == 0?
                <NoData>작성하신 상품 사용기가 없습니다.</NoData>
                :
                <>
                    {
                        reviewData.map((item, index) => {
                            return(
                                <MyReviewListEl
                                reviewData={reviewData}
                                setReviewData={setReviewData}
                                item={item}
                                index={index}
                                key={index}/>
                            )
                        })
                    }
                    <DeleteBtnWrap>
                        <DeleteBtn>삭제</DeleteBtn>
                    </DeleteBtnWrap>
                </>
            }

        </Wrap>
    )
};

export default MyReview;

const Wrap = styled.div` 
`

const NoData = styled.div`
    color:#A0A0A0;
    font-size: 12px;
    font-weight: 300;
    text-align: center;
    padding-top: 90px;
    ${Media(mediaPxMb,[
        PT(90, mbSize),
        FontSize(12, mbSize),
    ])}
`

const FilterWrap = styled.div`
    text-align: right;
    position: relative;
`
const FilterHead = styled.div` 
    font-size: 12px;
    margin: 15px 29px 10px 0;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        Margin(12, 21, 18, 0, mbSize)
    ])}
`
const FilterArrow = styled.img` 
    width: 7px;
    margin-left: 16px;
    ${Media(mediaPxMb,[
        Width(7, mbSize),
        ML(16, mbSize)
    ])}
    ${({rotate}) => {
        return rotate&&`transform: rotate(180deg);`
    }}
`
const FilterList = styled.ul` 
    position: absolute;
    right: 29px;
    border:1px solid #000;
    background-color: #fff;
    z-index:2;
`
const FilterEl = styled.li` 
    font-size: 12px;
    padding: 10px 12px;
    text-align: center;
    cursor: pointer;
    ${Media(mediaPxMb,[
        FontSize(12, mbSize),
        Padding2(10, 12, mbSize)
    ])}
`
const DeleteBtnWrap = styled.div` 
    text-align: center;
`
const DeleteBtn = styled.button` 
    width: 385px;
    height: 66px;
    font-size: 15px;
    margin: 48px 0 337px;
    border:1px solid #000;
    ${Media(mediaPxMb,[
        Width(385, mbSize),
        Height(66, mbSize),
        Margin(58, 0, 152, 0, mbSize),
    ])} 
`
