//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link, useHistory} from "react-router-dom";

// style
import styled from "styled-components"

import {
    Media,
    Width,
    Height,
    FontSize,
    ML,
} from '../../components/common/mediaStlye';

const mediaPx = 500;

const CommonSocialBtn = ({item, backText, onClick}) => {

    const [textWidth, setTextWidth] = useState(false);

    // useEffect(() => {
    //     if(backText == "Login"){
    //         setTextWidth(true);
    //     }else{
    //         setTextWidth(false);
    //     }
    // }, [])

    return(
        <SocialLogin onClick={onClick && onClick}>
            <SocialIcon src={item.img} alt="소셜 로그인 아이콘"/>
            <SocialText textWidth={textWidth}>{item.title} {backText}</SocialText>
        </SocialLogin>
    )
};

export default CommonSocialBtn;


const SocialLogin = styled.div`
    height:50px;
    border:1px solid #000;
    margin-bottom:4px;
    display:flex;
    align-items:center;
    justify-content:center;
    cursor: pointer;
    ${Media(mediaPx,[
        Height(50, 428)
    ])}
`

const SocialIcon = styled.img`
    margin-right:10px;
`
const SocialText = styled.span`
    font-size: 11px;
    text-align:center;
    ${({textWidth})=>{
    return textWidth?
    `width:84px`
    :
    `width:130px;`
    }}

    @media (max-width: ${mediaPx}px) {
        ${({textWidth})=>{
        return textWidth?
        `
            ${FontSize(11, 428)}
            ${Width(84, 428)}
        `
        :
        `
            ${Width(130, 428)}
            ${FontSize(13, 428)}
        `
        }}
    }

`